// Coming Soon Overlay Styles
@import '../../styles/themes.scss';

.coming-soon-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(74, 71, 71, 0.7);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  z-index: 10;
  pointer-events: all; // Block interactions
  
  .coming-soon-content {
    text-align: center;
    color: var(--theme-text-primary);
    
    .coming-soon-label {
      font-size: 1.5rem;
      font-weight: bold;
      text-transform: uppercase;
      letter-spacing: 2px;
      color: #ffffff;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
      margin-bottom: 8px;
    }
    
    .coming-soon-subtitle {
      font-size: 0.9rem;
      color: var(--theme-text-secondary);
      font-style: italic;
      opacity: 0.9;
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
    }
  }
  
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.95;
  }
}

// Responsive design
@media (max-width: 768px) {
  .coming-soon-overlay {
    .coming-soon-content {
      .coming-soon-label {
        font-size: 1.2rem;
        letter-spacing: 1px;
      }
      
      .coming-soon-subtitle {
        font-size: 0.8rem;
      }
    }
  }
}