// Encounter Tag Modal Styles - Side-by-Side Layout
@import '../../styles/themes.scss';

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  backdrop-filter: blur(4px);
}

.modal-container.encountertag {
  width: 90%;
  max-width: 1400px;
  height: 90vh;
  max-height: 900px;
  display: flex;
  flex-direction: column;
  background: var(--theme-background);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 20px 40px var(--theme-shadow);

  // Header Strip
  .encountertag-header {
    position: relative;
    height: 150px;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
    
    .header-background {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      
      .cimage {
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 0.7;
      }
    }
    
    .header-overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5));
      z-index: 2;
    }
    
    .header-content {
      position: relative;
      z-index: 3;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;
      padding: 20px 30px;
      
      .header-left {
        .encounter-info {
          .encounter-title {
            color: var(--theme-text-primary);
            font-size: 28px;
            font-weight: bold;
            margin: 0 0 8px 0;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          .tag-discovery {
            display: flex;
            align-items: center;
            gap: 12px;
            
            .tag-name {
              color: var(--theme-text-accent);
              font-size: 18px;
              font-weight: 600;
              text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
            }
            
            .tag-type {
              background: var(--theme-background-light);
              color: var(--theme-text-accent);
              padding: 4px 12px;
              border-radius: 16px;
              font-size: 12px;
              font-weight: 500;
              text-transform: uppercase;
              border: 1px solid var(--theme-border);
            }
          }
        }
      }
      
      .header-right {
        display: flex;
        align-items: center;
        gap: 20px;
        
        .encounter-tags {
          display: flex;
          gap: 8px;
          
          .encounter-tag-pill {
            background: var(--theme-background-light);
            color: var(--theme-text-primary);
            padding: 6px 12px;
            border-radius: 20px;
            font-size: 12px;
            font-weight: 500;
            border: 1px solid var(--theme-border);
            backdrop-filter: blur(4px);

          }
          .encounter-medallion-outer {
             .tag-pill {
                position: absolute;
              }
          }
          
        }
        
        .close-button {
          background: rgba(255, 255, 255, 0.1);
          color: #ffffff;
          border: none;
          width: 36px;
          height: 36px;
          border-radius: 50%;
          font-size: 20px;
          cursor: pointer;
          transition: all 0.2s ease;
          backdrop-filter: blur(4px);
          
          &:hover {
            background: rgba(255, 255, 255, 0.2);
            transform: scale(1.1);
          }
        }
      }
    }
  }

  // Side-by-Side Body
  .encountertag-body {
    flex: 1;
    display: flex;
    gap: 20px;
    padding: 20px;
    min-height: 0;
    
   
    
    // Right Side - Scoring
    .scoring-section {
      flex: 1;
      display: flex;
      flex-direction: column;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 8px;
      border: 6px solid rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(4px);
      .campaign-score-reward-container{
          width: 90%;
          align-self: center;;
        }
     
      
      .scoring-content {
        flex: 1;
        padding: 2px;
        
        .scoring-ledger {
          background-color: #00000044;      
          margin: 10px;
          padding: 20px;
          border-radius: 14px;
          .score-entries {
        
            .score-entry {
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding: 12px 16px;
              margin-bottom: 8px;
              background: rgba(255, 255, 255, 0.05);
              border-radius: 8px;
              border: 1px solid rgba(139, 131, 131, 0.79);
              opacity: 0;
              transform: translateX(20px);
              
              &.animate-in {
                animation: scoreSlideIn 0.4s ease forwards;
              }

              &.multiplier-low {
              
                border-color: rgba(208, 28, 28, 0.891);
              }

              &.multiplier-high {
             
                border-color: rgba(26, 255, 6, 0.3);
              }

              // Allow inline styles to override multiplier backgrounds
              &[style*="background-color"] {
                border-color: rgba(255, 255, 255, 0.2) !important;
              }
              
              .score-description {
                color: #ffffff;
                font-weight: 500;
              }
              
              .score-points {
                font-weight: 600;
                font-size: 16px;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                

                &.multiplier-low{
                  color: #ab2929;
                  font-size: 18px;
                  font-weight: bold;
                  background: rgba(95, 15, 13, 0.1);
                  border-color: rgba(167, 152, 152, 0.843);
                }

                &.multiplier-high{
                  color: #09c909;
                  font-size: 18px;
                  font-weight: bold;
                }



                .score-dice {
                  &.negative .dice-placeholder {
                    background: linear-gradient(145deg, #ffebee, #ef5350);
                    color: #c62828;
                    border-color: #d32f2f;
                  }
                  
                  &.positive .dice-placeholder {
                    background: linear-gradient(145deg, #e8f5e8, #66bb6a);
                    color: #2e7d32;
                    border-color: #388e3c;
                  }
                }
              }
              
            
              
            }
          }
          
          .score-total {
            margin-top: 20px;
            opacity: 0;
            transform: scale(0.9);
            
            &.animate-in {
              animation: totalSlideIn 0.5s ease forwards;
            }
            
            &.glow {
              animation: totalSlideIn 0.5s ease forwards, glowPulse 2s ease-in-out infinite;
            }
            
            .total-separator {
              height: 2px;
              background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.5), transparent);
              margin-bottom: 16px;
            }
            
            .total-entry {
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding: 16px 20px;
              background: rgba(255, 215, 0, 0.1);
              border-radius: 12px;
              border: 2px solid rgba(255, 215, 0, 0.3);
              .dice-icon.dice-d20 .d20-container .cimage {
                width: 32px;
                height: 32px;
              }
              .total-label {
                color: #ffd700;
                font-weight: 600;
                font-size: 18px;
                text-transform: uppercase;
              }
                .total-points {
                font-weight: bold;
                font-size: 24px;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                
                .total-dice {
                  &.negative .dice-placeholder {
                    background: linear-gradient(145deg, #ffebee, #ef5350);
                    color: #c62828;
                    border-color: #d32f2f;
                    box-shadow: 0 0 10px rgba(244, 67, 54, 0.4);
                  }
                  
                  &.positive .dice-placeholder {
                    background: linear-gradient(145deg, #fff8e1, #ffd700);
                    color: #f57f17;
                    border-color: #ffc107;
                    box-shadow: 0 0 15px rgba(255, 215, 0, 0.6);
                  }
                }
                
                &.positive {
                  color: #4ecdc4;
                }
                
                &.negative {
                  color: #ff6b6b;
                }
              }
            }
            
            // Campaign Score Display
            .campaign-score-entry {
              background: rgba(138, 43, 226, 0.1);
              border: 2px solid rgba(138, 43, 226, 0.3);
              margin-top: 12px;
              
              .total-label {
                color: #da70d6;
              }
              
              .campaign-score-breakdown {
                display: flex;
                align-items: center;
                gap: 12px;
                font-weight: bold;
                font-size: 16px;
                
                .current-score {
                  padding: 6px 10px;
                  background: rgba(255, 255, 255, 0.1);
                  border-radius: 8px;
                  display: flex;
                  align-items: center;
                  
                  .dice-score {
                    .dice-placeholder {
                      background: linear-gradient(145deg, #f5f5f5, #e0e0e0);
                      color: #424242;
                      border-color: #666666;
                    }
                  }
                }
                
                .score-separator {
                  color: #da70d6;
                  font-weight: bold;
                  font-size: 18px;
                }
                
                .new-score {
                  padding: 6px 10px;
                  background: rgba(78, 205, 196, 0.1);
                  border-radius: 8px;
                  display: flex;
                  align-items: center;
                  
                  .dice-score {
                    .dice-placeholder {
                      background: linear-gradient(145deg, #e0f7fa, #4ecdc4);
                      color: #00695c;
                      border-color: #4ecdc4;
                    }
                  }
                }
                
                .final-score {
                  padding: 6px 10px;
                  background: rgba(255, 215, 0, 0.1);
                  border-radius: 8px;
                  display: flex;
                  align-items: center;
                  
                  .dice-score {
                    .dice-placeholder {
                      background: linear-gradient(145deg, #fff8e1, #ffd700);
                      color: #f57f17;
                      border-color: #ffc107;
                      box-shadow: 0 0 10px rgba(255, 215, 0, 0.4);
                    }
                  }
                }
              }
            }
          }
        }


       
      }
    }
  }

   .section-header {
        padding: 16px 20px;
      //  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        
        h3 {
          color: #ffffff;
          margin: 0;
          font-size: 30px;
          font-weight: 600;
        }
      }

  // Footer
  .encountertag-footer {
    padding: 20px 30px;
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
    
    .campaign-reward-section {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    
    .encountertag-continue-button {
      background: var(--ui-color-primary, #ff6b6b);
      border: 4px solid white;
      margin-top: 24px;
      border-radius: 18px;;
      color: #ffffff;
      align-self: center;
      
      padding: 12px 40px;
    
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
      text-transform: uppercase;

     
      &:hover:not(.disabled) {
        background: var(--ui-color-primary-hover, #ff4c4c);
        transform: scale(1.05);
      
      }
      
      &.disabled {
        background: rgba(255, 255, 255, 0.1);
        color: rgba(255, 255, 255, 0.5);
        cursor: not-allowed;
       
      }
    }
  }
}

// Animations
@keyframes messageSlideIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes wordFadeIn {
  to {
    opacity: 1;
  }
}

@keyframes wordHighlight {
  0%, 100% {
    background: rgba(255, 215, 0, 0.2);
  }
  50% {
    background: rgba(255, 215, 0, 0.4);
  }
}

@keyframes scoreSlideIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes totalSlideIn {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes glowPulse {
  0%, 100% {
    box-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
  }
  50% {
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.6);
  }
}
