.level-up-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2px 5px;
  background-color: #627492;

  border: 4px solid #b3b1b1;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  //min-width: 120px;
  height: fill-available;
  margin-left: 10px;
  margin-top: 2px;
  margin-bottom: 2px;
  &:hover {
    background-color: #262e8d;
    transform: scale(1.02);
  }

  .inner-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    //height: 100%;
    justify-content: center;
    background-color: #92a4c2;
    border-radius: 12px;
    padding: 5px;
  }

  .level-up-label {
    font-size: 1.1em;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }

  .character-readiness-row {
    display: flex;
    flex-direction: row;
    gap: 4px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .readiness-symbol {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: bold;
    border: 1px solid #333;

    &.unready2, &.unready1 {
      background-color: #ffffff;
      color: #d32f2f;
    }

    &.ready {
      background-color: #c8e6c9;
      color: #388e3c;
    }

    &.perfect {
      background-color: #4caf50;
      color: white;
    }

    &.overready {
      background-color: #000000;
      color: #fb0202;
    }
  }
}
