.quest-terminal-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;


.quest-terminal-modal {
  background: var(--background-color, #2a2a2a);
  border: 2px solid var(--border-color, #666);
  border-radius: 8px;
  padding: 20px;
  max-width: 1200px;
  width: 95%;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid var(--border-color, #666);
  padding-bottom: 10px;

  h2 {
    color: var(--text-color, #fff);
    margin: 0;
  }

  .close-button {
    background: none;
    border: none;
    font-size: 24px;
    color: var(--text-color, #fff);
    cursor: pointer;
    
    &:hover {
      color: #ff6b6b;
    }
  }
}

.modal-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  min-height: 400px;
}

.conversation-panel {
  h3 {
    color: var(--text-color, #fff);
    margin: 0 0 15px 0;
    border-bottom: 1px solid var(--border-color, #666);
    padding-bottom: 5px;
  }
}

.conversation-content {
  background: var(--secondary-background, #333);
  border: 1px solid var(--border-color, #666);
  border-radius: 4px;
  padding: 15px;
  max-height: 400px;
  overflow-y: auto;
}

.conversation-message {
  margin-bottom: 15px;
  
  &:last-child {
    margin-bottom: 0;
  }

  .speaker {
    font-weight: bold;
    color: var(--accent-color, #4a9);
    font-size: 12px;
    margin-bottom: 5px;
    text-transform: uppercase;
  }

  .message {
    color: var(--text-color, #fff);
    line-height: 1.4;
    background: var(--background-color, #222);
    padding: 8px 12px;
    border-radius: 4px;
    border-left: 3px solid var(--accent-color, #4a9);
  }
}

.options-panel {
  h3 {
    color: var(--text-color, #fff);
    margin: 0 0 15px 0;
    border-bottom: 1px solid var(--border-color, #666);
    padding-bottom: 5px;
  }
}

.player-options {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.option-tile {
  border: 2px solid var(--border-color, #666);
  border-radius: 8px;
  padding: 15px;
  background: var(--secondary-background, #333);
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    border-color: var(--accent-color, #4a9);
    transform: translateY(-1px);
  }

  &.selected {
    border-color: #4a9;
    background: rgba(68, 170, 153, 0.1);
    box-shadow: 0 0 10px rgba(68, 170, 153, 0.3);
  }

  .option-content {
    margin-bottom: 10px;

    h4 {
      color: var(--text-color, #fff);
      margin: 0 0 8px 0;
      font-size: 16px;
    }

    p {
      color: var(--text-muted, #ccc);
      margin: 0;
      font-size: 14px;
      line-height: 1.4;
    }
  }

  .player-preferences {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
  }

  .player-preference {
    width: 30px;
    height: 30px;
    border: 2px solid;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    background: var(--background-color, #222);
    transition: transform 0.2s ease;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.accept-rewards {
  background: var(--background-color, #222);
  border: 1px solid var(--border-color, #666);
  border-radius: 8px;
  padding: 15px;
  margin-top: 10px;

  h4 {
    color: var(--text-color, #fff);
    margin: 0 0 10px 0;
  }

  ul {
    color: var(--text-color, #fff);
    margin: 0 0 15px 0;
    padding-left: 20px;

    li {
      margin-bottom: 5px;
      
      span {
        color: var(--text-muted, #ccc);
        font-style: italic;
      }
    }
  }

  .accept-button {
    background: #4a9;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: background 0.2s ease;

    &:hover {
      background: #3a8;
    }
  }
}

.no-choices {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  text-align: center;

  p {
    color: var(--text-muted, #ccc);
    margin-bottom: 20px;
  }

  .accept-button {
    background: #4a9;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: background 0.2s ease;

    &:hover {
      background: #3a8;
    }
  }
}
}
