.dm-screen-container {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #e5ded595;
    border-radius: 10px;
    margin: 6px;
    border: 4px solid rgb(87, 77, 44);
    box-sizing: border-box;
    margin-bottom: 10px;
    z-index: 200;
    &.hidden {
        display: none;
    }
    .dm-screen-branches-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 10px;  
        width: 90%; 
        .dm-screen-branch {
            flex: 1;
        }            
    }
    .dm-screen-rumors {
       bottom: 100px;
       position: absolute;
    }
    .dm-screen-dm-level {
        font-size: 1.3em;
        margin-right: 14px;
        .fill-bar {
            font-size: .65em;
        }
    }

   
   
    .table-encounter-container {
        max-width: 99%;
        box-sizing: border-box;
        top: 0px;
        border-width: 8px;
        padding: 10px 10px;
        height: 200px;
        font-size: 1.2em;
        margin: 2px;
       
        .event-column {
            gap: 1px;
            font-size: .8em;
            padding: 0;
            .encounter-event {
                border-width: 1px;
                padding: 4px;
                text-align: center;
                justify-content: center;

                &.seen {
                    background: #ffffff33;
                }
                &.current {
                    background: white;
                    color: black;
                }

                .event-controls {
                    display: none;
                }
            }
        }
    }
  .dm-screen-collected-tags-header {
            font-size: 1.4em;
        }
    .dm-screen-collected-tags-container {
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
        margin-bottom: 15px;
        justify-content: center;
       
        .encounter-flavor-tag {
            padding: 2px 4px;
            font-size: .75em;
            position: relative;
       
        }
    }
    .dm-screen-collected-themes{
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
        padding: 10px;
    }
    .dm-screen-collected-tags-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    


    // Set the Scene
    .dm-screen-set-scene{
        display: flex;
        width: 90%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 10px;
        width: 100%;
        font-size: 20px;
    
        .fill-bar {
            width: 90%;
        }

        .dm-screen-encounter-tags-row{
            display: flex;
            flex-direction: row;
            gap: 8px;
            .encounter-medallion-outer {
                
                .text-pill{
                    top: -2px;
                }
            }
        }
    }

    .dm-screen-set-scene-tags, .dm-screen-branch-tags {
        display: flex;
        flex-direction: row;
       // gap: 10px;
        flex-wrap: wrap;
        justify-content: center;
        padding: 0px;
        margin-bottom: 4px;
        .encounter-flavor-tag {
            padding: 0px 10px;
            font-size: .75em;
            margin:0px;
            position: relative;
            &.large-text{
                font-size: .9em;
                font-weight:600;
            }
        }
    }
    .fill-bar {
        margin-bottom: 4px;
        .fill-bar-container {
            margin-bottom: 4px;
            margin-top: 4px;
            
        }
    }
     .dm-screen-branches-header {
            font-size: 1.2em;
            font-weight: bold;
        }

    .dm-screen-branches-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
        width: 100%;
        gap: 0px;
       
        .dm-screen-upcoming-branches {
            display: flex;
            flex-direction: row;
            gap: 0px;
            justify-content: center;
            width: 100%;
           
            .dm-screen-branch {
                flex: 1;
                margin-left: 10px;
                margin-right: 10px;
                .fill-bar {
                    .fill-bar-label {
                        font-size: 1em;
                    }
                    .fill-bar-container {
                        margin-bottom: 4px;
                        margin-top: 4px;
                    }
                }
            }
        }
    }    
    .dm-screen-tag-collections {
       
        bottom: 100px;
        left: 20px;
        display: flex;
        flex-direction: column-reverse;

        gap: 10px;
        margin-top: auto;
        width: 100%;
        height: 100px;


        .collection-buttons-container {
            display: flex;
            flex-direction: row;
            gap: 10px;
            margin-top: auto;
            width: 100%;
            height: 100px;
        }
           
        .collections-button {
            flex: 1;
            min-height: 100px;
            border: none;
            background: #1a2f1a;
         
            color: #fff;
            cursor: pointer;
            transition: all 0.2s ease;
            display: flex;
            align-items: center;
            justify-content: center;
            border-top: 4px solid #7a9665;
            box-sizing: border-box;

            &:hover {
                background: #567d46;
            }
            
            &.dm-button {
                background: #2d1b69;
                border-top-color: #5a4fcf;
                
                &:hover {
                    background: #3d2b79;
                }
            }

            &.llm-button {
                background: #1a4d4d;
                border-top-color: #4CAF50;
                
                &:hover {
                    background: #2a5d5d;
                }
            }

            .button-content {
                display: flex;
                align-items: center;
                gap: 10px;
                font-size: 24px;
                font-weight: bold;

                .collected-count {
                    background: #cddc39;
                    color: #000;
                    padding: 2px 8px;
                    border-radius: 12px;
                    font-weight: bold;
                }
                
                .reward-count {
                    background: linear-gradient(135deg, #f39c12, #e67e22);
                    color: white;
                    padding: 4px 10px;
                    border-radius: 12px;
                    font-weight: bold;
                    font-size: 18px;
                    box-shadow: 0 2px 4px rgba(243, 156, 18, 0.3);
                    animation: pulse 2s infinite;
                }

                .status-indicator {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: #f44336;
                    
                    &.enabled {
                        background: #4CAF50;
                        box-shadow: 0 0 6px rgba(76, 175, 80, 0.6);
                    }
                }
                
                @keyframes pulse {
                    0% { transform: scale(1); }
                    50% { transform: scale(1.05); }
                    100% { transform: scale(1); }
                }
            }
        }
    }
}

.game-toasts-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 9999;
  pointer-events: none;

  > div {
    position: relative;
    margin-bottom: 10px;
    pointer-events: all;
  }
}


.campaign-score-info {
    margin: 4px;
    margin-left: 0px;
    margin-bottom: 18px;
    padding: 10px;
    border-radius: 5px;
    background-color: #aab6b6c1;
    color: rgb(84, 73, 73);
    text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
    border-radius: 100%;
   
    margin-top:18px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 55px;
    
    height: 55px;
    border: 4px solid #72716e;
    
    .score-value {
      position: relative;;
      font-size: 1.5em;
      top: 2px;
      font-weight: bold;
    }
    .score-label {
        position: relative;
        top: 9px;
        
        font-size: .7em;
        font-weight: bold;
      }
    &.overview {
       width: 96px;
       max-width: 96px;
       height: 76px;
       .score-value {
            font-size: 2em;
            top: 1px;
       }
         .score-label {
              font-size: .8em;
              top: 10px;
         }
    }
   
}