.right-sidebar {
  width: 450px;
  
  display: flex;
  flex-direction: column;
  background: var(--chat-background-color, #809aa8);
  border-left: 4px solid var(--chat-button-color, #6b7e9f);
  overflow: hidden;
  
  .right-sidebar-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 10px;
    gap: 6px;
    overflow: hidden;
    
    // DM Control Panel (horizontal layout for space efficiency)
    .dm-control-panel {
      display: flex;
      flex-direction: row;
      gap: 8px;
      flex-shrink: 0;

      .dm-control-button {
        position: relative;
        background: var(--gameroom-bottom-bar-background);
        border: 4px solid var(--gameroom-bottom-bar-border);
        border-radius: 12px;
        color: var(--gameroom-bottom-bar-text);
        padding: 12px 16px;
        font-size: 22px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 80px;
        flex: 1;
        
       color: rgb(224, 224, 224);
        
        .reward-count {
          position: absolute;
          top: -6px;
          right: -6px;
          background: rgba(255, 0, 0, 0.9);
          color: white;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 11px;
          font-weight: bold;
          border: 2px solid white;
        }
        
        &.collections-button {
          background: rgba(26, 156, 55, 0.2);
          border-color: rgba(70, 212, 94, 0.5);
        }
        
        &.skill-button {
          background: rgba(94, 85, 228, 0.411);
          border-color: rgba(86, 60, 201, 0.74);

        } &:hover {
          transform: scale(1.05);
          border-color: rgba(255, 255, 255, 0.5);
        }
      }
    }
    
    // Settings Panel (horizontal layout for space efficiency)
    .settings-panel {
      display: flex;
      flex-direction: row;
      gap: 6px;
      flex-shrink: 0;

      .settings-button {
        position: relative;
        background: var(--gameroom-bottom-bar-background);
        border: 2px solid var(--gameroom-bottom-bar-border);
        border-radius: 6px;
        color: var(--gameroom-bottom-bar-text);
        padding: 8px 12px;
        font-size: 12px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        
        &:hover {
          transform: scale(1.03);
          border-color: rgba(255, 255, 255, 0.5);
        }
        
        .status-indicator {
          position: absolute;
          top: -3px;
          right: -3px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          border: 2px solid white;
          
          &.enabled {
            background: rgba(0, 255, 0, 0.8);
          }
        }
      }
    }
    
    // Tone Tracker - compact version
    .tone-tracker {
      flex-shrink: 0;
      max-height: 120px;
      overflow-y: auto;
    

    }
    
    // Chat Container - takes remaining space
    .chat-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      min-height: 0; // Allows flex child to shrink
      overflow: hidden;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 8px;
      border: 6px solid rgba(174, 243, 255, 0.808);
      
      // Ensure chat fills available space
      .chat-exterior {
        flex: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
        
        .chat-container {
          flex: 1;
          height: 100%;
          display: flex;
          flex-direction: column;
        }
      }
    }
  }
}