/* Import the themes.css file */
@import url('../styles/themes.css');

.encounter-display {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 10px;
    border: 6px solid #000000;
    margin: 5px;
    font-size: large;
    color:#bebebe;
    font-weight: bold;
    width: -webkit-fill-available;
    max-width: 50%;
    height: 60px;
    cursor: pointer;
    transition: 1s;
    position:relative;
   
    .encounter-name {
        font-size: 1.3em;
        font-weight: bold;
        position: relative;
    }
    .encounter-event-icons {
        position: absolute;
        bottom:4px; ;
        left: 4px;
        display: flex;
        flex-direction: row;
        gap: 5px;
        .encounter-event-icon {
            background-color: white;
            border-radius: 100%;
            height: 40px;
            width: 40px;
            border: 4px solid #00000044;
            .cimage {
                margin-top: 5px;
                height: 30px;
                width: 30x;
            }
        }
    }
}

.encounter-display.forest {
    color: var(--forest-display-pen);
    border-color: var(--forest-display-pencil);
    &:hover {
        border-color: var(--forest-display-pen);
        background-color: var(--forest-display-pen-semi);
    }
}

