.party-level-up-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.party-level-up-modal-container {
  background-color: #8B7355;
  border: 3px solid #5D4E37;
  border-radius: 15px;
  width: 90%;
  max-width: 800px;
  max-height: 80vh;
  overflow-y: auto;
  color: #2F2F2F;
}

.party-level-up-modal-header {
  background-color: #6B5B47;
  padding: 15px 20px;
  border-radius: 12px 12px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #5D4E37;

  h2 {
    margin: 0;
    color: #F5F5DC;
    font-size: 1.5em;
  }

  .close-button {
    background: none;
    border: none;
    color: #F5F5DC;
    font-size: 2em;
    cursor: pointer;
    line-height: 1;
    
    &:hover {
      color: #FFD700;
    }
  }
}

.party-level-up-modal-content {
  padding: 20px;
  
  .selection-prompt {
    margin-bottom: 20px;
    font-size: 1.1em;
    color: #2F2F2F;
    text-align: center;
  }

  .players-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}

.party-level-up-modal-footer {
  background-color: #6B5B47;
  padding: 15px 20px;
  border-radius: 0 0 12px 12px;
  border-top: 2px solid #5D4E37;
  display: flex;
  justify-content: center;

  .confirm-button {
    background-color: #228B22;
    color: white;
    border: 2px solid #006400;
    border-radius: 8px;
    padding: 12px 24px;
    font-size: 1.1em;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      background-color: #32CD32;
      transform: scale(1.05);
    }

    &.exit {
      background-color: #CD853F;
      border-color: #8B7355;

      &:hover {
        background-color: #DEB887;
      }
    }
  }
}

// Styles to make the ResultsDisplay more compact for level-up modal
.level-up-results-wrapper {
  .results-panel {
    .results-panel-container {
      padding: 15px;

      .results-panel-header {
        margin-bottom: 15px;
        font-size: 1.2em;
        
      }

      .travel-results {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        gap: 10px;
        
        .effect-display {
          padding: 8px 12px;
          margin: 0;
          
          .effect-target {
            font-size: 0.9em;
            margin-bottom: 6px;
            
            .character-icon,
            .player-icon {
              width: 24px !important;
              height: 24px !important;
              min-width: 24px !important;
              min-height: 24px !important;
            }
            
            .character-name,
            .player-name {
              font-size: 0.85em;
            }
          }

          .effect-list {
            .effect-item {
              font-size: 0.8em;
              padding: 2px 6px;
              margin: 2px 0;

              &.character-level {
                background-color: #e8f5e8;
                color: #2e7d32;
                border-left: 3px solid #4caf50;
                font-weight: bold;
              }

              &.character-attributes {
                background-color: #fff3e0;
                color: #f57c00;
                border-left: 3px solid #ff9800;
              }

              &.attribute-gains {
                background-color: #f3e5f5;
                color: #7b1fa2;
                border-left: 3px solid #9c27b0;
                padding: 6px 8px;

                .attribute-gains-container {
                  display: flex;
                  gap: 8px;
                  align-items: center;

                  .attribute-gain-item {
                    display: flex;
                    align-items: center;
                    gap: 3px;
                    
                    .attribute-icon {
                      font-size: 14px;
                      
                      &.combat {
                        color: #d32f2f;
                      }
                      
                      &.magic {
                        color: #7b1fa2;
                      }
                      
                      &.tools {
                        color: #f57c00;
                      }
                    }
                    
                    .attribute-value {
                      font-weight: bold;
                      font-size: 0.85em;
                    }
                  }
                }
              }
            }
          }
        }
      }

      .bottom-container {
        margin-top: 15px;
        
        .return-button {
          padding: 8px 16px;
          font-size: 0.9em;
        }
      }
    }
  }
}
