@keyframes tooltip-fade-in {
  from {
    opacity: 0;
   // transform: scale(0.95);
  }
  to {
    opacity: 1;
   // transform: scale(1);
  }
}

.tooltip-wrapper-container {
  position: relative;
  display: inline-block;
  /* Make sure the base container has a high z-index */
  z-index: 10;
  
  /* Add support for absolute positioned children */
  &.preserve-positioning {
    position: static;
    /* Use display contents to avoid affecting the layout of absolutely positioned children */
    display: contents;
    
    /* Ensure that event handlers still work correctly with display: contents */
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 100; /* Increased z-index value */
      /* Make sure the pseudo element doesn't actually block interactions */
      pointer-events: none;
    }
  }
  
  /* Handle container within elements that have overflow: hidden */
  .unlocked & {
    overflow: visible;
    z-index: 500; /* Much higher z-index to ensure visibility */
  }
}
.tooltip-help-icon {
    position: absolute;
    top: 4px;
    left: 4px;
    width: 20px;
    height: 20px;
    background: #2196f3;
    color: #fff;
    border-radius: 50%;
    font-size: 1rem;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 3px rgba(33, 150, 243, 0.2);
    pointer-events: auto;
    z-index: 999;
    user-select: none;
    cursor: pointer;
}
.tooltip-wrapper {
  position: fixed;
  z-index: 9999; /* Extremely high z-index to ensure it's on top of everything */
  pointer-events: none;
  
  animation: tooltip-fade-in 0.2s ease-out forwards;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.25));
  
  /* Ensure tooltip renders above other elements */
  &[style] {
    z-index: 9999; 
  }
    .tooltip-content {
    background-color: rgba(0, 0, 0, 0.856);
    color: white;
    padding: 16px;
    border-radius: 4px;
    font-size: 1.5rem;
    line-height: 1.4;
    text-align: center;
    white-space: normal;
    word-wrap: break-word;
    width: 400px;
    /* Ensure tooltip content has proper z-index */
    position: relative;
    z-index: 9;
  }

  // Arrow styling
  &::after {
    content: "";
    position: absolute;
    border-width: 6px;
    border-style: solid;
    border-color: transparent;
  }

  // Arrow positions
  &.tooltip-top::after {
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
    border-top-color: rgba(0, 0, 0, 0.8);
    border-bottom: none;
  }

  &.tooltip-bottom::after {
    top: -12px;
    left: 50%;
    transform: translateX(-50%);
    border-bottom-color: rgba(0, 0, 0, 0.8);
    border-top: none;
  }

  &.tooltip-left::after {
    right: -12px;
    top: 50%;
    transform: translateY(-50%);
    border-left-color: rgba(0, 0, 0, 0.8);
    border-right: none;
  }

  &.tooltip-right::after {
    left: -12px;
    top: 50%;
    transform: translateY(-50%);
    border-right-color: rgba(0, 0, 0, 0.8);
    border-left: none;
  }
}

// Themes and custom classes
.tooltip-wrapper.tooltip-light {
  .tooltip-content {
    background-color: rgba(255, 255, 255, 0.95);
    color: #222;
    border: 1px solid #ddd;
  }

  &.tooltip-top::after {
    border-top-color: rgba(255, 255, 255, 0.95);
  }

  &.tooltip-bottom::after {
    border-bottom-color: rgba(255, 255, 255, 0.95);
  }

  &.tooltip-left::after {
    border-left-color: rgba(255, 255, 255, 0.95);
  }

  &.tooltip-right::after {
    border-right-color: rgba(255, 255, 255, 0.95);
  }
}

.tooltip-wrapper.tooltip-game {
  .tooltip-content {
    background-color: rgba(47, 41, 24, 0.95);
    color: #d6c8a2;
    border: 2px solid #6a5c3d;
    font-family: 'Fantasy', serif;
  }

  &.tooltip-top::after {
    border-top-color: rgba(47, 41, 24, 0.95);
  }

  &.tooltip-bottom::after {
    border-bottom-color: rgba(47, 41, 24, 0.95);
  }

  &.tooltip-left::after {
    border-left-color: rgba(47, 41, 24, 0.95);
  }

  &.tooltip-right::after {
    border-right-color: rgba(47, 41, 24, 0.95);
  }
}
