.game-encounter-dungeon-crawl {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 16px;


.dungeon-content {
  display: flex;
  flex: 1;
  gap: 16px;
}

.dungeon-main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.dungeon-rooms {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.dungeon-room {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 16px;
  border: 2px solid #4a4a5a;
  transition: all 0.3s ease;
}

.dungeon-room.active {
  border-color: #4a90e2;
  box-shadow: 0 0 12px rgba(74, 144, 226, 0.3);
}

.dungeon-room.starting-room {
  text-align: center;
}

.dungeon-room.completed {
  padding: 8px 16px;
  background: rgba(100, 100, 100, 0.2);
  border-color: #666;
}

.completed-room-summary {
  display: flex;
  align-items: center;
  gap: 16px;
}

.room-name {
  font-weight: bold;
  color: white;
  flex: 1;
}

.room-tag {
  background: rgba(74, 144, 226, 0.3);
  color: #4a90e2;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
}

.room-attributes {
  display: flex;
  gap: 8px;
}

.room-attributes span {
  background: rgba(255, 255, 255, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 12px;
  color: white;
}

.room-header {
  margin-bottom: 16px;
}

.room-header h3 {
  color: white;
  margin-bottom: 8px;
  font-size: 20px;
}

.room-header p {
  color: #ccc;
  line-height: 1.4;
  font-style: italic;
}

.start-dungeon-button {
  padding: 12px 24px;
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
  margin-top: 16px;
}

.start-dungeon-button:hover {
  background: #357abd;
}

.room-main {
  display: flex;
  gap: 24px;
  align-items: center;
}

.room-requirements {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: rgba(255, 255, 255, 0.05);
  padding: 16px;
  border-radius: 8px;
  min-width: 80px;
}

.requirement-item {
  display: flex;
  align-items: center;
  gap: 8px;
  color: white;
}

.requirement-item .icon {
  font-size: 16px;
}

.requirement-item .value {
  font-weight: bold;
  font-size: 18px;
}

.room-tag-area {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.room-tag {
  background: rgba(74, 144, 226, 0.2);
  color: #4a90e2;
  padding: 16px 24px;
  border-radius: 12px;
  font-size: 18px;
  font-weight: bold;
  border: 2px solid #4a90e2;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;
  min-width: 150px;
}

.room-tag.highlighted {
  background: rgba(74, 144, 226, 0.4);
  border-color: #357abd;
  box-shadow: 0 0 16px rgba(74, 144, 226, 0.5);
  transform: scale(1.05);
}

.room-tag.activated {
  background: rgba(39, 174, 96, 0.3);
  border-color: #27ae60;
  color: #27ae60;
}

.room-tag:hover {
  transform: scale(1.02);
  box-shadow: 0 0 8px rgba(74, 144, 226, 0.3);
}

.room-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 200px;
}

.room-option {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid #666;
  border-radius: 8px;
  padding: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
}

.room-option:hover {
  border-color: #4a90e2;
  background: rgba(74, 144, 226, 0.1);
}

.room-option.selected {
  border-color: #4a90e2;
  background: rgba(74, 144, 226, 0.2);
  box-shadow: 0 0 8px rgba(74, 144, 226, 0.3);
}

.option-attributes {
  display: flex;
  gap: 8px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: bold;
}

.option-attributes span {
  background: rgba(255, 255, 255, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  color: white;
}

.option-characters {
  margin-bottom: 8px;
}

.character-icons {
  display: flex;
  gap: -8px; /* Negative gap for overlap */
}

.character-icon {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid #4a4a5a;
  overflow: hidden;
  margin-left: -8px;
}

.character-icon:first-child {
  margin-left: 0;
}

.character-icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.dm-reward-indicator {
  position: absolute;
  top: 4px;
  right: 4px;
  background: #f39c12;
  color: white;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 4px;
}

.dungeon-sidebar {
  width: 200px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.dungeon-totals-ui {
  display: flex;
  gap: 16px;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  padding: 16px;
  border-radius: 8px;
}

.totals-bar {
  position: relative;
  width: 40px;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  border: 2px solid #666;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
}

.totals-bar.combat {
  border-color: #e74c3c;
}

.totals-bar.magic {
  border-color: #3498db;
}

.totals-bar.tools {
  border-color: #8b4513;
}

.bar-fill {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transition: height 0.3s ease;
  border-radius: 0 0 6px 6px;
}

.totals-bar.combat .bar-fill {
  background: linear-gradient(to top, rgba(231, 76, 60, 0.8), rgba(231, 76, 60, 0.4));
}

.totals-bar.magic .bar-fill {
  background: linear-gradient(to top, rgba(52, 152, 219, 0.8), rgba(52, 152, 219, 0.4));
}

.totals-bar.tools .bar-fill {
  background: linear-gradient(to top, rgba(139, 69, 19, 0.8), rgba(139, 69, 19, 0.4));
}

.bar-preview {
  position: absolute;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.3);
  transition: all 0.2s ease;
  border-radius: 0 0 6px 6px;
}

.dm-reward {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background: #f39c12;
  color: white;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 4px;
  border-radius: 4px;
  z-index: 10;
}

.bar-icon {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 18px;
}

.dungeon-progress {
  background: rgba(255, 255, 255, 0.1);
  padding: 16px;
  border-radius: 8px;
  text-align: center;
}

.dungeon-progress h4 {
  color: white;
  margin-bottom: 8px;
}

.progress-indicator {
  color: #4a90e2;
  font-weight: bold;
  font-size: 16px;
}

.next-room-options {
  margin-top: 16px;
  background: rgba(255, 255, 255, 0.05);
  padding: 16px;
  border-radius: 8px;
}

.next-room-options h4 {
  color: white;
  margin-bottom: 16px;
  text-align: center;
}

.room-options-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.next-room-option {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid #666;
  border-radius: 8px;
  padding: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.next-room-option:hover {
  border-color: #4a90e2;
  background: rgba(74, 144, 226, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.next-room-option h5 {
  color: white;
  margin-bottom: 8px;
  font-size: 14px;
}

.next-room-option p {
  color: #ccc;
  font-size: 12px;
  line-height: 1.3;
  margin-bottom: 8px;
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-container {
  background: #2a2a3a;
  border-radius: 12px;
  padding: 24px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.close-button {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.modal-description {
  color: #ccc;
  margin-bottom: 16px;
  line-height: 1.4;
}

.dungeon-results {
  margin-bottom: 16px;
}

.effect-display {
  margin-bottom: 12px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 6px;
}

.effect-target {
  font-weight: bold;
  color: #4a90e2;
  margin-bottom: 4px;
}

.effect-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.effect {
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 14px;
}

.effect.party-exhaustion {
  background: rgba(230, 126, 34, 0.2);
  color: #e67e22;
}

.effect.player-exp {
  background: rgba(52, 152, 219, 0.2);
  color: #3498db;
}

.effect.player-attention {
  background: rgba(255, 0, 0, 0.2);
  color: #ff6b6b;
}

.effect.dm-points {
  background: rgba(74, 144, 226, 0.2);
  color: #4a90e2;
}

.effect.dungeon-complete {
  background: rgba(39, 174, 96, 0.2);
  color: #27ae60;
}

.modal-actions {
  display: flex;
  justify-content: center;
}

.modal-actions button {
  padding: 10px 24px;
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.modal-actions button:hover {
  background: #357abd;
}
}