.modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.7);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	backdrop-filter: blur(4px);
}

.modal-container {
	background: var(--ui-color-secondary);
	border-radius: 24px;
	padding: 24px;

	// width: 90%;
	border: 6px solid var(--ui-color-secondary-border);
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
	animation: modalAppear 0.3s ease-out;
	min-width: 800px;
	&.travel {
		width: 50%;
		max-width: 800px;
	}
	&.puzzle {
		max-width: unset;
		width: auto;
		.puzzle-results {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: center;
			gap: 20px;
			.effect-display{
				max-width: 120px;
				max-height: 180px;
				padding-bottom: 4px;
				justify-content: flex-start;
			}
		}
	}
}

.modal-header {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 24px;
	padding-bottom: 16px;
	border: none;
	padding-top: 0px;
	margin-top: 0px;
	margin-bottom: 22px;
	background: #00000000;

	h2 {
		color: #ffffff;
		font-size: 34px;
		margin: 0;
		margin-bottom: 12px;
	}

	.modal-description {
		color: #cccccc;
		font-size: 24px;
		margin-top: 8px;
		line-height: 1.4;
	}

	.close-button {
		background: none;
		border: none;
		color: #ffffff;
		font-size: 24px;
		cursor: pointer;
		padding: 8px;
		border-radius: 50%;
		transition: background-color 0.2s;

		&:hover {
			background-color: #ffffff20;
		}
	}
}

.modal-options {
	display: flex;
	flex-direction: row;
	gap: 20px;
	justify-content: center;
	max-height: 70vh;
	overflow-y: auto;
	padding: 8px;

	.modal-option {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 24px 16px;
		background: #3a3a4a;
		border: 6px solid #5a5a6a;
		border-radius: 16px;
		cursor: pointer;
		transition: all 0.2s ease;
		overflow: hidden;
		max-width: 350px;
		width: 350px;
		&:hover {
			transform: translateY(-4px);
			border-color: #7a7a8a;
			background: #4a4a5a;

			.option-icon {
				transform: scale(1.1);
			}
		}

		&:active {
			transform: translateY(0);
		}

		&.level-up-option-character {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #8a8a9a;
			.cimage {
				position: relative;
				width: 80px;
				height: 80px;
				border-radius: 100%;
				margin-bottom: 10px;
			}
		}
		&.rare {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #6d77ff;
		}
		&.legend {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #ffb732;
		}

		.option-icon {
			font-size: 48px;
			margin: 16px 0;
			transition: transform 0.2s ease;
		}

		.option-content {
			text-align: center;
			width: 100%;

			h3 {
				color: #ffffff;
				margin: 0 0 8px 0;
				font-size: 18px;
			}

			p {
				color: #cccccc;
				margin: 0;
				font-size: 14px;
				line-height: 1.4;
			}
		}
	}
}

.modal-actions {
	display: flex;
	justify-content: center;
	margin-top: 24px;
	padding-top: 16px;
	border-top: 2px solid #4a4a5a;
}

.modal-action-button {
	background: #4a90e2;
	color: white;
	border: none;
	padding: 12px 24px;
	border-radius: 8px;
	font-size: 16px;
	font-weight: bold;
	cursor: pointer;
	transition: background-color 0.2s ease;

	&:hover:not(:disabled) {
		background: #357abd;
	}

	&:active:not(:disabled) {
		background: #2968a3;
	}

	&.disabled,
	&:disabled {
		background: #666;
		cursor: not-allowed;
		opacity: 0.6;
	}
}

// TagModal specific styles
.modal-effects {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	gap: 20px;
	margin-bottom: 20px;
	flex-wrap: wrap;
}

.modal-tag-container {
	margin-top: 20px;
	background: #00000055;
	padding: 10px;
	border-radius: 18px;
	min-height: 42px;
	.clicks-remaining {
		text-align: center;
		margin-bottom: 12px;
		font-size: 18px;
		font-weight: bold;
		color: #ffffff;
		padding: 8px 16px;
		background: rgba(74, 144, 226, 0.2);
		border-radius: 8px;
		display: none;
	}

	.tag-instructions {
		text-align: center;
		margin-bottom: 16px;
		color: #cccccc;
		font-size: 14px;
		line-height: 1.4;
	}

	.modal-tags-container {
		display: flex;
		justify-content: center;
		
		.modal-tags {
			display: flex;
			flex-wrap: wrap;
			gap: 12px;
			justify-content: center;
			max-width: 600px;
		}
		    .encounter-medallion-outer {
             .tag-pill {
				top: 0px;
			 }
			}
	}
}

.remaining-clicks-warning {
	text-align: center;
	color: #ffaa44;
	font-style: italic;
	padding: 20px;
	background: rgba(255, 170, 68, 0.1);
	border-radius: 8px;
	margin: 20px 0;
}

// Left Side - Conversation
.modal-container {
	.conversation-section {
		flex: 1.2;
		display: flex;
		flex-direction: column;
		background: rgba(255, 255, 255, 0.05);
		border-radius: 8px;
		border: 6px solid rgba(255, 255, 255, 0.1);
		backdrop-filter: blur(4px);

		.conversation-content {
			flex: 1;
			padding: 0;
			overflow-y: auto;

			.conversation-display {
				background-color: #00000044;
				margin: 10px;
				padding-top: 20px;
				border-radius: 14px;
				&.chat-expanded-messages {
					display: flex;
					flex-direction: column;
					margin-top: 10px;
					justify-content: center;
					padding: 20px;

					.chat-message-container {
						margin-bottom: 10px;
						flex-direction: row;
						font-size: 1.5em;
						opacity: 0;
						transform: translateY(10px);
						animation: messageSlideIn 0.5s ease forwards;

						.chat-message-display {
							display: flex;
							flex-direction: row;
							align-items: center;

							.chat-message-speaker-container {
								display: flex;
								flex-direction: column;
								align-items: flex-end;

								.chat-message-speaker {
									font-weight: bold;
									font-size: 0.8em;
									margin-bottom: 0px;
									color: #ffffff;
									line-height: 1em;
									white-space: nowrap;
								}

								.chat-message-icon {
									width: 50px;
									height: 50px;
									border-radius: 50%;
									margin: 10px;

									.cimage {
										border-radius: 5px;
									}
								}
							}

							.chat-messager-content {
								display: flex;
								flex-direction: column;
								align-items: center;
								width: 100%;

								.chat-message-speaker {
									display: none;
								}
							}

							.chat-message-text {
								border-radius: 5px;
								background: rgba(255, 255, 255, 0.95);
								padding: 10px;
								min-height: 50px;
								width: calc(100% - 50px);
								color: var(--notebook-text-color, #716f6d);

								.chat-message-words {
									.chat-word {
										&.latest {
											background: rgba(255, 215, 0, 0.3);
											border-radius: 2px;
											padding: 1px 2px;
										}
									}
								}
							}
						}

						// Alternating layout like expanded chat
						&:nth-child(even) {
							.chat-message-display {
								flex-direction: row-reverse;

								margin-left: 20px;
								margin-right: 0px;

								.chat-message-icon {
									margin-left: -5px;
									margin-right: 10px;
								}

								.chat-message-speaker {
									margin-left: -5px;
								}

								.chat-message-speaker-container {
									align-items: flex-start;
								}
							}
						}

						&:nth-child(odd) {
							.chat-message-display {
								margin-right: 30px;

								.chat-message-icon {
									margin-left: 10px;
									margin-right: -5px;
								}

								.chat-message-speaker {
									margin-left: 10px;
								}
							}
						}
					}
				}
			}

			.no-conversation {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				color: #ccc;
				font-style: italic;
			}
		}
	}
}

@keyframes modalAppear {
	from {
		opacity: 0;
		transform: translateY(-20px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}

// Custom scrollbar for modal options
.options {
	&::-webkit-scrollbar {
		width: 8px;
	}

	&::-webkit-scrollbar-track {
		background: #2a2a3a;
		border-radius: 4px;
	}

	&::-webkit-scrollbar-thumb {
		background: #5a5a6a;
		border-radius: 4px;

		&:hover {
			background: #6a6a7a;
		}
	}
}
