/* Import the themes.css file */
@import url("../../styles/themes.css");

@keyframes shimmer {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.gameroom-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;

  // New 2-Panel Layout (Main + Right Sidebar)
  .gameroom-layout {
    display: flex;
    flex-direction: row;
    flex: 1;
    height: calc(100vh - 140px); // Leave space for bottom bar
    
    .gameroom-main-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      
      .gameroom-top-container {
        display: flex;
        flex-direction: row;
        justify-content: center;
        height: 120px;
        flex-shrink: 0;

    .gameroom-setup {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      color: white;
      .gameroom-setup-title {
        font-size: 3em;
        display: block;
        flex: 1;
      }
      .gameroom-setup-time-remaining {
        font-size: 2em;
        display: block;
        flex: 1;
      }
    }
    .player-display-container {
      // display:flex;
      // flex-direction: column;
      // justify-content: center;
      // align-items: center;
      // width: 100%;

      // color: rgb(154, 154, 84);
     
      .score-info {
        margin: 10px;

        margin-left: 0px;
        margin-right: 0px;

        padding: 10px;
        border-radius: 15px;
        background-color: #bfcadcc1;
        color: rgb(84, 73, 73);
        //  text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
        background-color: rgb(119, 119, 119);

        margin-top: 10px;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 180px;

        height: 96px;
        position: relative;
        left: -58px;
        z-index: 0;
        .score-value {
          font-size: .5em;
          font-weight: bold;
        }
        .score-label {
          font-size: 1.2em;
          left: 28px;
          top: -12px;
          color: white;
          position: relative;
          font-weight: bold;
        }
        .score-value {
          font-size: 1em;
          left: 24px;
          top: -4px;
          color: white;
          position: relative;
          font-weight: bold;
        }
      }

    }
  }
      .gameroom-table-container {
        flex: 1;
        display: flex;
        width: 100%;
        
        .game-table-slab {
          width: 100%;
          margin: 5px;
          display: flex;
          margin-top: 0px;
          margin-bottom: 0px;
        }
      }
    }
  }
  
}
.campaign-display {
    &.overview {
      height: 100px;
    }
  }
.encounter-icon-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0; // Put behind other content

  .cimage {
    width: 100%;
    height: 100%;
    opacity: 0.17;
    object-fit: contain; // Maintain aspect ratio
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
  }
}

.bottom-bar-controls {
  display: flex;

  margin-right: 0px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.gameroom-nav-button-container {
  height: 80px;
  margin: 4px;
  margin-left: 4px;
  font-size: 0.8em;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  .gameroom-nav-button {
    width: 80px;
    height: 80px;
    box-sizing: border-box;
    background-color: rgb(54, 66, 100);
    border-color: rgb(37, 37, 60);
    margin-top: 0;
    cursor: pointer;
    transition: 0.5s;
    border-width: 4px;
    font-size: 1em;
  }
}

.table-encounter-container {
  display: flex;
  flex-direction: column;

  border-radius: 12px;
  border: 5px solid #000000;

  font-size: 3em;
  font-weight: bold;
  width: 100%;

  position: relative;

  transition: 1s;
  position: relative;
  overflow: hidden;
  .table-encounter-scene {
    height: 100%;
    .table-encounter-title {
      display: none;
    }
  }
  .encounter-name {
    font-size: 1.3em;
    font-weight: bold;
    position: relative;
  }

  &.forest {
    color: var(--forest-display-pen);
    border-color: var(--forest-display-pen);
  }

  &.town {
    color: var(--town-display-pen);
    border-color: var(--town-display-pen);
  }

  &.building {
    color: var(--town-display-pen);
    border-color: var(--town-display-pen);
  }

  &.mountain {
    color: var(--mountain-display-pen);
    border-color: var(--mountain-display-pen);
  }

  &.dungeon {
    color: var(--dungeon-display-pen);
    border-color: var(--dungeon-display-pen);
  }

  &.water {
    color: var(--water-display-pen);
    border-color: var(--water-display-pen);
  }

  .table-encounter-divisions {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 2px solid green;
    height: 90%;
    width: 100%;
    .event-column {
      border: 2px solid blue;
      display: flex;
      flex-direction: column;
      flex: 1 1 0;
      justify-content: center;
      gap: 40px;
      padding: 10px;
      .event-column-label {
        margin-top: auto;
      }
      .encounter-event {
        position: relative;
        margin: 8px;
        padding: 12px;
        margin-top: auto;
        border: 4px solid #ccc;
        border-radius: 24px;
        overflow: hidden;

        .event-controls {
          width: 100%;
          align-content: center;
          justify-content: center;
          display: flex;
          .move-button {
            width: 75%;
            position: absolute;
            height: 25%;
            padding: 0;
            transition: 0.5s;

            background: #ffffff33;
            cursor: pointer;

            &.up {
              top: 0;
            }

            &.down {
              bottom: 0;
            }

            &:hover {
              background: #ffffff55;
            }

            &.left {
              left: 0;
            }
            &.right {
              right: 0;
            }
          }

          .swap-mid-button {
            width: 25%;
            height: 100%;
            padding: 0;
            transition: 0.5s;
            background: #ffffff33;
            top: 0;
            cursor: pointer;
            position: absolute;

            &:hover {
              background: #ffffff55;
            }

            &.left {
              right: 0;
            }
            &.right {
              left: 0;
            }
          }
        }
      }
    }
  }
  .table-encounter-session {
    position: relative;
    width: 100%;
    height: 100%;
    // top: -100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .encounter-intro-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      position: relative;

      .encounter-intro-title {
        display: flex;
        font-size: 1.6em;

        margin-top: 0px;
        margin-bottom: 30px;
      }
      .encounter-intro-location {
        display: flex;
        position: relative;
        font-size: 1.2em;
      }
      .encounter-intro-instructions {
        display: flex;
        position: relative;
        font-size: 0.6em;
        line-height: 1em;
        &.expect {
          margin-bottom: 10px;
        }
        &.plot {
          margin-top: 10px;
          margin-bottom: 10px;
          line-height: normal;
        }
        &.dm-tricks {
          margin-top: 10px;
          margin-bottom: 10px;
        }
      }
      .encounter-flavor-tag {
        position: relative;
      }
      .encounter-intro-tags {
        display: flex;
        flex-direction: row;
        margin-bottom: 20px;

        &.plot {
          margin-top: 10px;
          line-height: normal;
        }
      }
    }
  }

  .encounter-roleplay-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
    .encounter-header {
      display: flex;
      flex-direction: row;
      // width: 100%;
      justify-content: space-around;
      align-items: center;

      .encounter-instructions {
        display: flex;
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 0.75em;
        color: var(--ui-color-primary-text, #efefef);
        padding: 4px;
        width: 100%;
      }

      .encounter-header-right {
        min-width: 0px;
      }

      .encounter-roleplay-tutorial {
        background-color: #00000066;
        font-size: 0.4em;
        color: white;
        padding: 0px;
        margin-top: 8px;
        margin-bottom: 4px;
        border-radius: 10px;
        border: 6px solid #00000036;
        width: 100%;

        max-height: 200px;
        overflow: hidden;
        .sample-tag {
          height: 15px;
          margin-left: 4px;
          top: 0px;
        }
      }
      .encounter-roleplay-text {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #ffffff54;
        border-radius: 10px;
        padding: 12px;
        border: 6px solid #00000077;
      }
      .encounter-roleplay-skill-container {
        // padding: 10px;
        font-size: 0.51em;
        min-width: 250px;
        // background-color: #ffffff54;
        //  border-radius: 10px;
        //  padding: 12px;
        //  border: 6px solid #00000077;

        .fill-bar-label {
          margin-bottom: 2px;
        }
        .fill-bar-container {
          height: 20px;
          border: 3px solid #00000044;
        }
        .encounter-roleplay-hurry-button {
          background-color: var(--ui-color-primary, #65b6b2);
          border-radius: 10px;
          border: 4px solid var(--ui-color-primary-border, #b3c4c3);
          margin-top: 10px;
          font-size: 1.7em;
          color: var(--ui-color-primary-text, #efefef);
          padding-top: 2px;
          height: 40px;
          width: 100%;
          box-sizing: border-box;
          transition: 0.3s all;
          cursor: pointer;
          &:hover {
            background-color: var(--ui-color-primary-hover, #4a9b9a);
            color: rgb(255, 255, 255);
            transform: scale(1.05);
          }
          &.disabled {
            opacity: 0.5;
            cursor: default;
            pointer-events: none;
          }
        }
        .encounter-roleplay-hurry-text {
          font-size: 0.5em;

          margin-bottom: 10px;
          color: white;
          display: none;
        }
      }
    }
    .encounter-roleplay-token-display {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 0.5em;
      margin: 20px;
    }
    .encounter-roleplay-title {
      display: flex;
      position: relative;
      font-size: 0.8em;
    }

    .encounter-roleplay-flavor-tags-container {
      position: relative;
      height: 100%;
      width: 100%;
      font-size: 0.5em;
      .tag-pill {
        position: absolute;
      }
      .encounter-medallion-outer {
        .tag-pill {
          position: inherit;
        }
      }

      &.disabled {
        &::before,
        &::after {
          opacity: 0;
        }
      }
      // Add horizontal bands
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(
          to bottom,
          transparent 0%,
          rgba(2, 218, 128, 0.05) 0%,
          rgba(2, 218, 128, 0.3) 35%,

          transparent 35%,
          rgba(255, 255, 255, 0.05) 36%,
          rgba(255, 255, 255, 0.2) 70%,
          transparent 70%,
          rgba(118, 0, 0, 0.051) 71%,
          rgba(118, 0, 0, 0.3) 100%,
          transparent 90%
        );
        pointer-events: none;
        transition: opacity 0.3s ease;
      }

      // Add horizontal lines
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(
          to bottom,
          rgba(255, 255, 255, 0.2) 0%,
          transparent 2%,
          transparent 35%,
          rgba(255, 255, 255, 0.2) 35%,
          transparent 37%,
          transparent 70%,
          rgba(255, 255, 255, 0.3) 70%,
          transparent 72%
        );
        pointer-events: none;
        transition: opacity 0.3s ease;
      }
      .band-label {
        position: absolute;
        left: 10px;

        transform: translateY(-50%);
        color: rgba(255, 255, 255, 0.9);
        font-size: 1em;

        pointer-events: none;
        // text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
        z-index: 0;
        &.disabled {
          opacity: 0;
        }
      }
    }
  }
}

.encounter-flavor-tag {
  position: absolute;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: 0.2s;
  color: white;
  font-size: 0.75em;

  &:hover .tooltip-text {
    opacity: 1; // Show the tooltip on hover
  }

  &.text-pill {
    padding: 10px;
    padding-top: 5px;
    border-style: solid;
  }
}
.tooltip-text {
  position: absolute;
  bottom: 100%; // Position the tooltip above the element
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(48, 48, 48, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
  z-index: 10;
  font-size: 0.7em;
  text-overflow: wrap;
  width: 100%;
  white-space: normal;
  width: 300px;
}

.encounter-rp-icon {
  position: absolute;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  transition: 0.5s;
  font-size: 0.75em;

  &:hover {
    transform: scale(1.05);

    .tooltip-text {
      opacity: 1; // Show the tooltip on hover
    }
  }

  .cimage {
    width: 60px;
    height: 60px;
    border-radius: 10px;
    display: flex;
  }
  .encounter-rp-character-name {
    font-size: 0.75em;
    font-weight: bold;
    color: rgb(177, 200, 223);
  }

  .encounter-rp-icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    .encounter-rp-icon-row {
      display: flex;
      flex-direction: row;
      gap: 10px;
      margin-top: 0px;
      line-height: 0.9em;
      align-items: center;
      justify-content: center;
    }
  }
}

@keyframes glowTag {
  0% {
    box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color),
      0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
  }
  50% {
    box-shadow: 0 0 10px var(--glow-color), 0 0 20px var(--glow-color),
      0 0 30px var(--glow-color), 0 0 40px var(--glow-color);
  }
  100% {
    box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color),
      0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
  }
}

@keyframes glowless {
  0% {
    box-shadow: 0 0 2px var(--glow-color), 0 0 5px var(--glow-color),
      0 0 8px var(--glow-color), 0 0 10px var(--glow-color);
  }
  50% {
    box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color),
      0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
  }
  100% {
    box-shadow: 0 0 2px var(--glow-color), 0 0 5px var(--glow-color),
      0 0 8px var(--glow-color), 0 0 10px var(--glow-color);
  }
}

