          .perception-display {
            display: flex;
            flex: 1 1 auto;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            .text-pill {
              padding-top: 0px;
              height: 32px;
            }
            .perception-tags {
              margin-top: 0px;
              display: flex;
              flex-direction: row;
              margin-bottom: 10px;
              margin-top: 4px;
            }
          }
        }

        .character-perception-options-container {
          display: flex;
          flex-direction: row;
        }

        .perception-display-campaign-container {
          display: flex;
          flex-direction: column;
          gap: 0px;
          width: 100%;

          .perception-display {
            flex-direction: row;
          }
          .perception-description {
            background-color: #00000044;
            border-radius: 10px;
            margin-bottom: 4px;
            color: white;
          }
          .campaign-icon {
            margin-right: 0px;
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 100px;

            .cimage {
              width: 80px;
              height: 80px;
              border-radius: 10px;
            }
          }
          .campaign-plot-hook {
            font-size: large;
            margin-right: 20px;
            margin-left: -10px;
          }
        }
      }
    }
  }
}

.bottom-bar-container {

  background: rgba(0, 0, 0, 0.9);
  border-top: 2px solid #444;
  backdrop-filter: blur(5px);
  z-index: 100;
  height: 140px;

  &.hidden {
    display: none;
  }

  .bottom-bar {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0 10px;
    box-sizing: border-box;
    .tone-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: 100%;
      gap: 20px;
      .tone-label {
        width: 100px;
        font-size: 1.5em;
        line-height: 1em;
        font-weight: bold;
        color: var(--gameroom-bottom-bar-text);
      }
      .tone-list {
        .dm-screen-collected-themes {
          display: flex;
          flex-direction: row;
          gap: 10px;

          .fill-bar {
            min-width: 100px;
          }
        }
      }
    }

    // DM Control Panel (left side)
    .dm-control-panel {
      display: flex;
      flex-direction: row;
      gap: 8px;
      align-items: center;
      flex-shrink: 0;

      .dm-control-button {
        position: relative;
        background: var(--gameroom-bottom-bar-background);
        border: 6px solid var(--gameroom-bottom-bar-border);
        border-radius: 16px;
        color: var(--gameroom-bottom-bar-text);
        padding: 8px 16px;
        font-size: 24px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.2s ease;
        min-height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 6px;
        &:hover {
          transform: scale(1.1);
        }

        .reward-count {
          position: absolute;
          top: -8px;
          right: -8px;
          background: #ff6b6b;
          color: white;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          font-size: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: bold;
        }

        &.quests-button {
          background: rgba(255, 107, 107, 0.2);
          border-color: rgba(255, 107, 107, 0.5);
        }

        &.topics-button {
          background: rgba(107, 255, 107, 0.2);
          border-color: rgba(107, 255, 107, 0.5);
        }

        &.skill-button {
          background: rgba(107, 107, 255, 0.2);
          border-color: rgba(107, 107, 255, 0.5);
        }
      }
    }

    // Settings Panel (right side)
    .settings-panel {
      display: flex;
      flex-direction: row;
      gap: 8px;
      align-items: center;
      flex-shrink: 0;

      .settings-button {
        position: relative;
        background: var(--gameroom-bottom-bar-background);
        border: 2px solid var(--gameroom-bottom-bar-border);
        border-radius: 8px;
        color: var(--gameroom-bottom-bar-text);
        padding: 8px 12px;
        font-size: 12px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.2s ease;
        min-height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
          background: rgba(255, 255, 255, 0.1);
          border-color: rgba(255, 255, 255, 0.3);
        }

        .status-indicator {
          position: absolute;
          top: 2px;
          right: 2px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          
          &.enabled {
            background: #4caf50;
            box-shadow: 0 0 4px rgba(76, 175, 80, 0.6);
          }
        }

        &.llm-button {
          background: rgba(255, 152, 0, 0.2);
          border-color: rgba(255, 152, 0, 0.5);
        }

        &.chat-rules-button {
          background: rgba(156, 39, 176, 0.2);
          border-color: rgba(156, 39, 176, 0.5);
        }
      }
    }
  }

  // New DM Screen content styles
  .bottom-bar-content {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 20px;
    
    height: 100%;
    width: 100%;
    overflow-x: auto;
    
    .bottom-bar-campaign-header {
      flex-shrink: 0;
    }
    
    .bottom-bar-encounter-section,
    .bottom-bar-location-section,
    .bottom-bar-branches-section {
      display: flex;
      flex-direction: column;
      gap: 5px;
      min-width: 200px;
      
      .section-header {
        font-size: 12px;
        font-weight: bold;
        text-transform: uppercase;
        margin-bottom: 5px;
        color: white;
      }
      
      .encounter-tags-row {
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
      }
      
      .tag-group {
        display: flex;
        gap: 5px;
        flex-wrap: wrap;
      }
    }
    
    .bottom-bar-branches-section {
      .branches-container {
        display: flex;
        gap: 15px;
        overflow-x: auto;
        
        .branch-item {
          display: flex;
          flex-direction: column;
          gap: 5px;
          min-width: 180px;
          flex-shrink: 0;
          
          .branch-tags {
            display: flex;
            gap: 3px;
            flex-wrap: wrap;
          }
        }
      }
    }
    
    .bottom-bar-controls {
      margin-left: auto;
      flex-shrink: 0;
    }

    // Compact tag pills for horizontal layout
    .tag-pill {
      font-size: 10px;
      padding: 2px 6px;
      margin: 1px;
    }
    
    .fill-bar {
      height: 16px;
      font-size: 10px;
    }

    // Scrollbar styling
    scrollbar-width: thin;
    scrollbar-color: #666 #333;
    
    &::-webkit-scrollbar {
      height: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: #333;
    }
    
    &::-webkit-scrollbar-thumb {
      background: #666;
      border-radius: 3px;
    }
    
    &::-webkit-scrollbar-thumb:hover {
      background: #888;
    }

    // New Bottom Bar Components
    .bottom-bar-encounter {
      display: flex;
      flex-direction: column;
      gap: 8px;
      min-width: 250px;
      padding: 10px;
      background: rgba(0, 100, 150, 0.2);
      border: 6px solid rgba(0, 150, 255, 0.4);
      border-radius: 8px;
      color: white;
      cursor: pointer;
      height: 100%;
      min-height: 100px;
      transition: all 0.3s ease;

      &:hover {
        background: rgba(0, 100, 150, 0.3);
        transform: scale(1.02);
      }

      .encounter-header-row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        .encounter-name {
          font-size: 24px;
          font-weight: bold;
          color: #87CEEB;
        }
        
        .encounter-percent {
          font-size: 19px;
          color: #B0E0E6;
          font-weight: 600;
        
        }
      }

      .encounter-dots-row {
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .encounter-dots-section,
        .location-dots-section {
          display: flex;
          align-items: center;
          gap: 5px;

          .dots-label {
            font-size: 18px;
            color: #B0E0E6;
            min-width: 50px;
          }

          .dots-container {
            display: flex;
            gap: 3px;
          }
        }
      }
    }

    .bottom-bar-next-encounters {
      display: flex;
      flex-direction: column;
      gap: 8px;
      min-width: 300px;
      padding: 10px;
      background: rgba(150, 100, 0, 0.2);
      border: 6px solid rgba(255, 150, 0, 0.4);
      border-radius: 8px;
      color: white;
      cursor: pointer;
      transition: all 0.3s ease;
      min-height: 100px;

      &:hover {
        background: rgba(150, 100, 0, 0.3);
        transform: scale(1.02);
      }

      .next-encounters-header {
        .next-encounters-title {
          font-size: 18px;
          font-weight: bold;
          color: #FFD700;
          text-transform: uppercase;
        }
      }

      .next-encounters-row {
        display: flex;
        gap: 10px;
        overflow-x: auto;
        justify-content: center;
        .next-encounter-item {
          display: flex;
          flex-direction: column;
          gap: 4px;
          min-width: 80px;
          flex-shrink: 0;

          .next-encounter-name {
            font-size: 18px;
            color: #FFE4B5;
            text-align: center;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          .dots-container {
            display: flex;
            gap: 2px;
            justify-content: center;
          }
        }
      }
    }

    .bottom-bar-scoring {
      display: flex;
      min-width: 600px;
      background: rgba(120, 0, 120, 0.15);
      border: 2px solid rgba(150, 0, 255, 0.3);
      border-radius: 8px;
      overflow: hidden;
      color: white;
      height: 120px;
      .scoring-event-side {
        display: flex;
        flex-direction: column;
        gap: 5px;
        flex: 1;
        padding: 10px;
        background: rgba(0, 150, 100, 0.2);
        border-right: 1px solid rgba(150, 0, 255, 0.3);
        min-width: 240px; 
        align-items: center;
        .event-header-row {
          .event-name {
            font-size: 24px;
            font-weight: bold;
            color: #90EE90;
          }
        }

        .event-score-row {
          display: flex;
          margin-top: 10px;
          gap: 10px;
          align-items: center;

          .event-score-label {
            font-size: 12px;
            color: #98FB98;
          }
        }
      }

      .scoring-tension-side {
        display: flex;
        flex-direction: column;
        gap: 5px;
        flex: 1;
        padding: 10px;
        background: rgba(150, 75, 0, 0.2);
        border-right: 1px solid rgba(150, 0, 255, 0.3);
         min-width: 200px; 
        .tension-header-row {
          .tension-name {
            font-size: 24px;
            top: -10px;
            position: relative;
            font-weight: bold;
            color: #dddddd;
            text-align: center;
          }
        }

        .tension-gauge-container {
          display: flex;
          justify-content: center;
          align-items: center;
          flex: 1;
          min-height: 80px;
          .stress-info {
            top: 14px;
            left: -80px;
          }
          // Create a stress-needle-like wrapper for the bottom bar
          .bottom-bar-stress-needle {
            height: 80px;
            width: 140px;
            position: relative;
            background-color: transparent;
            
            .needle-gauge {
              position: absolute;
              height: auto;
              left: 0px;
              top: 28px;
              width: auto;
              
              svg {
                width: 100%;
                height: 140px;
                overflow: visible;
                position: static;
                left: auto;
                top: auto;
              }
            }
          }
        }
      }

      .scoring-campaign-side {
        display: flex;
        flex-direction: column;
        gap: 5px;
        flex: 1;
        padding: 10px;
        background: rgba(100, 0, 150, 0.2);
 min-width: 220px; 
        .campaign-header-row {
          .campaign-name {
            font-size: 24px;
            font-weight: bold;
            color: #DDA0DD;
          }
        }

        .campaign-score-row {
          display: flex;
          margin-top: 10px;
          gap: 10px;
          align-items: center;
          justify-content: center;
          flex-direction: row;
          
          .campaign-score-label {
            font-size: 12px;
            color: #D8BFD8;
          }
        }
      }
    }

    // Tag dots styling
    .tag-dot {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      border: 1px solid rgba(255, 255, 255, 0.6);
      transition: all 0.2s ease;

      &.filled {
        background: currentColor;
        box-shadow: 0 0 4px rgba(255, 255, 255, 0.8);
      }

      &.hollow {
        background: transparent;
      }

      &.encounter-dot.filled {
        background: #87CEEB;
      }

      &.location-dot.filled {
        background: #98FB98;
      }

      &.next-encounter-dot.filled {
        background: #FFD700;
      }
    }

    // Tooltip content styling
    .encounter-tooltip-content,
    .next-encounters-tooltip-content {
      min-width: 300px;
      max-width: 500px;
      color: white;
      
      h4, h5 {
        margin: 0 0 8px 0;
        color: #87CEEB;
        font-size: 14px;
      }

      .tooltip-section,
      .tooltip-branch {
        margin-bottom: 12px;
        
        &:last-child {
          margin-bottom: 0;
        }
      }

      .tooltip-tags {
        display: flex;
        flex-wrap: wrap;
        gap: 4px;
        
        .tag-pill {
          font-size: 10px;
          padding: 2px 6px;
        }
      }
    }
  }
}
