        position: relative;

        .tone-progress-bar {
          height: 100%;
          border-radius: 2px;
          transition: width 0.3s ease;

          &.theme-dramatic {
            background-color: var(--fill-color-theme-dramatic);
          }
          &.theme-heroic {
            background-color: var(--fill-color-theme-heroic);
          }
          &.theme-mysterious {
            background-color: var(--fill-color-theme-mysterious);
          }
          &.theme-comedic {
            background-color: var(--fill-color-theme-comedic);
          }
          &.theme-serious {
            background-color: var(--fill-color-theme-serious);
          }
          &.theme-fantasy {
            background-color: var(--fill-color-theme-fantasy);
          }
          &.theme-horror {
            background-color: var(--fill-color-theme-horror);
          }
          &.theme-grim {
            background-color: var(--fill-color-theme-grim);
          }
        }

        .tone-redeem-button {
          position: absolute;
          right: 0;
          top: -30px;
          background: rgba(255, 255, 255, 0.1);
          border: 1px solid rgba(255, 255, 255, 0.2);
          border-radius: 4px;
          padding: 4px 8px;
          font-size: 12px;
          color: #fff;
          cursor: pointer;
          transition: all 0.2s ease;

          &:hover {
            background: rgba(255, 255, 255, 0.2);
            border-color: rgba(255, 255, 255, 0.3);
          }

          .redeem-icon {
            margin-right: 4px;
            font-size: 14px;
          }
        }
      }
    }
  }
}

.player-display-container {
  display: flex;
  align-items: center;
  gap: 0px;
  padding: 20px;
  margin-left: 40px;
  margin-top: 8px;
  .player-row {
    display: flex;

    align-items: center;

    .gameroom-player-row {
      display: flex;
      flex-direction: row;
      gap: 50px;

      align-items: center;
      justify-content: center;

      .player-display {
        position: relative;
        width: 100%;
        //height: 100px;
        display: flex;
        max-height: 110px;

        // &::before {
        //   content: '';
        //   position: absolute;
        //   top: 0;
        //   left: 0;
        //   right: 0;
        //   bottom: 0;
        border: 3px solid rgba(255, 255, 255, 0.3);
        border-radius: 15px;
        background-color: #ffffff20;
        // }

        .character-level-icon-column {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          margin-right: 1px;
          .character-level {
            font-size: 1em;
            font-weight: bold;
            color: rgba(255, 255, 255, 0.8);
            margin-bottom: 4px;
            .fill-bar {
              width: 90px;
              margin-left: 4px;
              margin-right: 4px;
              .fill-bar-container {
                height: 10px;
              }
            }
          }
        }

        .player-diamond-holder {
          left: -10px;
          position: relative;
          .player-diamond {
            position: relative;
            z-index: 3;
            width: 56px;
            height: 56px;

            background-color: rgba(255, 255, 255, 0.1);
            transform: rotate(45deg);
            border-radius: 10px;
            border: 4px solid rgba(255, 0, 0, 0.98);
          }
        }

        .player-icon-container {
          position: absolute;
          width: 56px;
          height: 56px;
          top: 3px;
          left: 10px;
          //  transform: rotate(-45deg);
          overflow: hidden;
          border-radius: 50%;
          z-index: 9;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        .character-icons {
          position: relative;
          left: -58px;
          top: 10%;
          // transform: rotate(-45deg) translateY(-50%);
          display: flex;
          flex-direction: column;
          gap: 0px;
          z-index: 9;
          overflow: visible;
          width: 0px;
          .character-icon {
            width: 38px;
            height: 38px;
            border-radius: 50%;
            border: 2px solid rgba(255, 255, 255, 0.5);
            overflow: hidden;
            cursor: pointer;
            transition: transform 0.2s;
            position: relative;
            z-index: 2;
            left: -70px;
            top: 5px;
            &:hover {
              .character-tooltip {
                opacity: 1;
              }

              img {
                // Move transform to the image instead
                transform: scale(1.1);
                transition: transform 0.2s;
              }
            }
          }
        }

        .player-stats {
          //right: -100px;
          //top: 50%;
          //  transform: rotate(-45deg) translateY(-50%);
          display: flex;
          flex-direction: column;
          gap: 2px;
          margin-bottom: 2px;
          padding-bottom: 2px;
          margin-top: 0px;
          line-height: 1em;

          // border: 3px solid blue;
          .player-stat-label {
            font-size: 0.8em;
            &.name {
              margin-top: 4px;
              font-weight: bold;
              font-size: 1.2em;
              color: rgba(255, 255, 255, 0.8);
            }
          }
          .stat-bar {
            height: 8px;
            border-radius: 4px;
            overflow: hidden;
            background: rgba(0, 0, 0, 0.402);
            margin: 0px 0;
            width: 90px;

            .fill {
              height: 100%;
              transition: width 0.3s ease;
              box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
            }
          }
        }
      }
    }
  }
}

.player-tooltip {
  position: absolute;

  background-color: rgba(77, 107, 134, 0.929);
  border: 4px solid rgba(255, 255, 255, 0.58);
  border-radius: 8px;
  padding: 12px;
  width: 240px;
  z-index: 200;
  pointer-events: none;
  transition: opacity 0.2s;
  left: 30px;
  top: 30px;

  .tooltip-header {
    font-size: 1.8em;
    line-height: 0.75em;
    font-weight: bold;
    margin-bottom: 8px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.4);
  }

  .tooltip-content {
    font-size: 1.2em;

    .preference-list {
      margin-top: 1px;
      margin-bottom: 12px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 6px;
      .text-pill {
        margin: 0px;
      }

      .preference-item {
        display: flex;
        align-items: center;
        gap: 6px;

        &.like {
          color: #fbfffb;
        }
        &.hate {
          color: #ffffff;
        }
      }
    }
  }
}

.character-tooltip {
  position: fixed;
  transform: translateX(-25%) translateX(-20px); // Move left by 100% of its width plus 20px gap
  z-index: 1; // Lower z-index to appear below icon
  background-color: rgba(77, 125, 134, 0.9);
  border: 4px solid rgba(255, 255, 255, 0.58);
  padding: 12px;
  border-radius: 8px;
  pointer-events: none;
  width: 200px;
  .tooltip-header {
    font-size: 1.8em;
    line-height: 0.75em;
    font-weight: bold;
    margin-bottom: 8px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.4);
  }

  .tooltip-content {
    font-size: 0.9em;

    .preference-list {
      display: flex;
      flex-direction: column;
      gap: 4px;

      .preference-item {
        display: flex;
        align-items: center;
        gap: 6px;

        &.like {
          color: #4caf50;
        }
        &.hate {
          color: #ff4d4d;
        }
      }
    }
  }
}

