// DM Collections Modal Styles
@import '../../styles/themes.scss';

.dm-collections-modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.8);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 2000;
}

.dm-collections-modal-container {
	background: var(--theme-background);
	color: var(--theme-text-secondary);
	border-radius: 10px;
	border: 6px solid var(--theme-border);
	width: 90%;
	max-width: 1200px;
	max-height: 90vh;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.dm-collections-modal-header {
	background: var(--theme-primary-light);
	padding: 20px 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 2px solid var(--theme-border);

	h2 {
		margin: 0;
		font-size: 1.8rem;
		color: var(--theme-text-primary);
		text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
	}

	.close-button {
		background: none;
		border: none;
		font-size: 3em;
		margin-right: -30px;;
		margin-top: -30px;
		color: var(--theme-text-primary);
		cursor: pointer;
		opacity: 0.7;
		transition: opacity 0.2s ease;
		//right: 10px;
		position: relative;
		&:hover {
			opacity: 1;
		}
	}
}

.dm-collections-modal-content {
	flex: 1;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	.collections-content {
		flex: 1;
		overflow-y: scroll;

		// Make the embedded TagCollectionsScreen fill the space
		.tag-collections-overlay {
			position: relative;
			width: 100%;
			height: 100%;
			background: transparent;
			display: flex;
			align-items: stretch;
			justify-content: center;
			z-index: auto;
		}

		.tag-collections-container {
			background: transparent;
			border: none;
			width: 100%;
			max-width: none;
			height: 100%;
			margin: 0;
			border-radius: 0;
			&.narrow {
				// width: auto;
				max-width: 400px;
			}
		}
	}

	.abilities-content {
		flex: 1;
		display: flex;
		flex-direction: column;
		overflow: hidden;
	}
}

.dm-collections-modal-footer {
	background: var(--theme-primary-light);
	border-top: 2px solid var(--theme-border);
	display: flex;

	.footer-tab {
		flex: 1;
		background: var(--theme-background);
		border: none;
		padding: 15px 20px;
		color: var(--theme-text-primary);
		font-size: 1.1rem;
		font-weight: bold;
		cursor: pointer;
		transition: all 0.3s ease;
		text-transform: uppercase;
		letter-spacing: 0.5px;

		&:hover {
		background: var(--theme-background-light);
		}

		&.active {
			background: var(--theme-background);
			color: #ffffff50;
			box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
		}

		&:not(:last-child) {
			border-right: 1px solid var(--theme-border);
		}
	}
}

// Responsive design
@media (max-width: 768px) {
	.dm-collections-modal-container {
		width: 95%;
		height: 95vh;
	}

	.dm-collections-modal-header {
		padding: 15px 20px;

		h2 {
			font-size: 1.5rem;
		}
	}

	.dm-collections-modal-footer {
		.footer-tab {
			padding: 12px 15px;
			font-size: 1rem;
		}
	}
}
