body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

span {
  cursor: default;
}

/* Global Scrollbar Styling */
/* WebKit browsers (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(42, 42, 58, 0.3);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #5a5a6a, #4a4a5a);
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: background 0.2s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #6a6a7a, #5a5a6a);
}

::-webkit-scrollbar-thumb:active {
  background: linear-gradient(135deg, #7a7a8a, #6a6a7a);
}

::-webkit-scrollbar-corner {
  background: rgba(42, 42, 58, 0.3);
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #5a5a6a rgba(42, 42, 58, 0.3);
}

/* For specific themed scrollbars */
.scrollbar-theme-blue {
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, #3498db, #2980b9);
    
    &:hover {
      background: linear-gradient(135deg, #5dade2, #3498db);
    }
  }
  
  scrollbar-color: #3498db rgba(42, 42, 58, 0.3);
}

.scrollbar-theme-red {
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, #e74c3c, #c0392b);
    
    &:hover {
      background: linear-gradient(135deg, #ec7063, #e74c3c);
    }
  }
  
  scrollbar-color: #e74c3c rgba(42, 42, 58, 0.3);
}

.scrollbar-theme-green {
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, #27ae60, #229954);
    
    &:hover {
      background: linear-gradient(135deg, #58d68d, #27ae60);
    }
  }
  
  scrollbar-color: #27ae60 rgba(42, 42, 58, 0.3);
}

/* Conversation/Modal specific scrollbar styling */
.scrollbar-conversation {
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
    border-radius: 3px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.2s ease;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.2));
    transform: scaleX(1.2);
  }

  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.2) rgba(255, 255, 255, 0.05);
}

/* Invisible scrollbar for clean interfaces */
.scrollbar-hidden {
  scrollbar-width: none; /* Firefox */
  &::-webkit-scrollbar {
    display: none; /* WebKit browsers */
  }
}