// RewardFillBar Styles
@import '../../styles/themes.scss';

.reward-fill-bar {
  position: relative;
  width: 100%;
  
  .reward-fill-container {
    position: relative;
    width: 100%;
    
    // Custom progress bar
    .reward-progress-bar {
      position: relative;
      width: 100%;
      height: 24px;
      
      .progress-track {
        width: 100%;
        height: 12px;
        background: rgba(0, 0, 0, 0.3);
        border: 2px solid rgba(255, 255, 255, 0.2);
        border-radius: 8px;
        position: relative;
        overflow: hidden;
        
        .progress-fill {
          height: 100%;
          background: var(--ui-color-primary);
        
          border-radius: 6px;
          transition: width 0.3s ease-out;
          position: relative;
            border-top-right-radius: 0px;
          border-bottom-right-radius: 0px;
        
        }
      }
    }
    // Milestone markers (dice icons)
    .milestone-markers {
      position: absolute;
      top: -25px;
      left: 0;
      right: 0;
      height: 20px;
      pointer-events: none;
      
      .milestone-marker {
        position: absolute;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        
        &.d6 {
          .cimage {
            width: 14px;
            height: 14px;
            filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
          }
        }
        
        &.dmpoint-dice {
          border-radius: 50%;
          border: 2px solid rgba(255, 255, 255, 0.3);
          background: rgba(0, 0, 0, 0.4);
          width: 20px;
          height: 20px;
          
          &.available-dmpoint {
            border-color: var(--ui-color-secondary, #ffd700);
            background: rgba(255, 215, 0, 0.2);
            animation: dmPointPulse 2s ease-in-out infinite;
            cursor: pointer;
            pointer-events: auto;
            
            &:hover {
              transform: translateX(-50%) scale(1.2);
              border-color: #ffffff;
              box-shadow: 0 0 10px rgba(255, 215, 0, 0.6);
            }
          }
        }
        
        &.d20 {
            
            border-radius: 100%;
            width: 24px;
            height: 24px;
            bottom: -2px;
            border: 4px solid var(--ui-color-primary-hover);
          .cimage {
            width: 18px;
            height: 18px;
            filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.4));
          }
        }
        
        // Flashing animation for crossed thresholds
        &.flashing {
          animation: thresholdFlash 1.5s ease-in-out;
          
          &.d20 {
            border-color: #ffff00 !important;
            box-shadow: 0 0 20px rgba(255, 255, 0, 0.8);
          }
        }
      }
    }
    
    // Score bubble
    .score-bubble {
      position: absolute;
      bottom: -44px;
      transform: translateX(-50%);
      pointer-events: none;
      z-index: 10;
      
      .bubble-arrow {
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-bottom: 8px solid rgba(0, 0, 0, 0.8);
        margin: 0 auto;
        margin-bottom: -1px;
      }
      
      .bubble-content {
        background: rgba(0, 0, 0, 0.8);
        color: white;
        padding: 6px 12px;
        border-radius: 12px;
        font-size: 12px;
        font-weight: bold;
        white-space: nowrap;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 40px;
        border: 2px solid rgba(255, 255, 255, 0.2);
        backdrop-filter: blur(4px);
        
        .score-text {
          .dice-icon {
            width: 12px;
            //min-width: 20px;
            height: 12px;
            margin-right: 0px;
          }
          color: #ffffff;
        }
        
        .reward-claim-button {
          background: var(--ui-color-primary, #ff6b6b);
          color: white;
          border: none;
          padding: 4px 8px;
          border-radius: 6px;
          font-size: 11px;
          font-weight: bold;
          cursor: pointer;
          text-transform: uppercase;
          pointer-events: all;
          transition: all 0.2s ease;
          
          &:hover {
            background: var(--ui-color-primary-hover, #ff4c4c);
            transform: scale(1.1);
            box-shadow: 0 2px 8px rgba(255, 107, 107, 0.4);
          }
          
          &:active {
            transform: scale(0.95);
          }
        }
        
        .dmpoint-claim-button {
          background: var(--ui-color-secondary, #ffd700);
          color: #000000;
          border: none;
          padding: 4px 8px;
          border-radius: 6px;
          font-size: 10px;
          font-weight: bold;
          cursor: pointer;
          text-transform: uppercase;
          pointer-events: all;
          transition: all 0.2s ease;
          
          &:hover {
            background: #ffed4e;
            transform: scale(1.1);
            box-shadow: 0 2px 8px rgba(255, 215, 0, 0.4);
          }
          
          &:active {
            transform: scale(0.95);
          }
        }
      }
      
      // Animation for bubble appearance
      animation: bubbleFloat 0.3s ease-out;
    }
  }
}

// Animations
@keyframes bubbleFloat {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(10px) scale(0.8);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0) scale(1);
  }
}

// Reward claim animation
.reward-fill-bar .score-bubble .bubble-content .reward-claim-button {
  animation: rewardPulse 2s ease-in-out infinite;
}

@keyframes rewardPulse {
  0%, 100% {
    box-shadow: 0 2px 8px rgba(255, 107, 107, 0.4);
  }
  50% {
    box-shadow: 0 2px 16px rgba(255, 107, 107, 0.8);
  }
}

@keyframes dmPointPulse {
  0%, 100% {
    box-shadow: 0 0 5px rgba(255, 215, 0, 0.4);
  }
  50% {
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.8);
  }
}

// DM point claim button animation
.reward-fill-bar .score-bubble .bubble-content .dmpoint-claim-button {
  animation: dmPointButtonPulse 2s ease-in-out infinite;
}

@keyframes dmPointButtonPulse {
  0%, 100% {
    box-shadow: 0 2px 8px rgba(255, 215, 0, 0.4);
  }
  50% {
    box-shadow: 0 2px 16px rgba(255, 215, 0, 0.8);
  }
}

// Progress bar shine animation
@keyframes shine {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

// Threshold crossing flash animation
@keyframes thresholdFlash {
  0%, 100% {
    transform: translateX(-50%) scale(1);
    filter: brightness(1);
  }
  25% {
    transform: translateX(-50%) scale(1.3);
    filter: brightness(2) drop-shadow(0 0 10px #ffff00);
  }
  50% {
    transform: translateX(-50%) scale(1.2);
    filter: brightness(1.8) drop-shadow(0 0 15px #ffff00);
  }
  75% {
    transform: translateX(-50%) scale(1.1);
    filter: brightness(1.4) drop-shadow(0 0 8px #ffff00);
  }
}
