:root {
    --chat-display-toggle-height: 50px;
    --chat-background-color: #728691;
    --chat-background-color-inner: #3c525ee9;
    --chat-background-color-trans: #9aa9b1;
    --chat-expanded-background-color: #9aa9b1e9;
    --chat-color-alt: #81aee3;
    --chat-button-color: #6b7e9f;
    --chat-button-color-alt: #566a71;
}




.chat-exterior {
     position: relative;
     height: 100%;
     flex: 1;
     display: flex;
     flex-direction: column;
     box-sizing: border-box;
     min-height: 0; // Allows flex shrinking
    
    .chat-messages-container {
        display: flex; 
        flex: 1;
        flex-direction: column;
        height: 100%;
        width: 100%;
        position: relative;
        box-sizing: border-box;
        background-color: var(--chat-background-color);
        border-radius: 10px;
        min-height: 0;
        overflow-y: auto;
        overflow-x: hidden;
        scroll-behavior: smooth; // Smooth scrolling
        gap: 5px;
        padding: 5px 5px 50px 5px; // Added extra bottom padding for buffer
        scrollbar-width: thin;
        scrollbar-color: var(--chat-button-color) transparent;
        background-color: var(--chat-background-color-inner);
        &::-webkit-scrollbar {
            width: 8px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: var(--chat-button-color);
            border-radius: 10px;
        }
        &::-webkit-scrollbar-track {
            background: transparent;
        }
        &::-webkit-scrollbar-thumb:hover {
            background: var(--chat-button-color-alt);
        }
            
        .chat-new-message {
            width: 100%;
            background-color: var(--chat-background-color-trans);
            color: white;
           
            position: relative;
            border-radius: 10px;
            transition: background-color 0.3s ease, box-shadow 0.3s ease;
            flex-shrink: 0;
            
            // Highlighted message (part of conversation)
            &.highlighted {
                background-color: #bad7ffe1;
                box-shadow: 0 0 5px rgba(129, 174, 227, 0.3);
                animation: fadeInHighlight 0.5s ease-out;
            }
            
            // Pulsing effect for the last message in conversation
            &.pulsing {
                animation: pulseGlow 2s ease-in-out infinite;
            }
            
            // New message highlight that fades after 3 seconds
            &.new-message {
                background-color: rgba(129, 174, 227, 0.2);
                box-shadow: 0 0 10px rgba(129, 174, 227, 0.5);
                
                &.fade-out {
                    transition: background-color 1s ease-out, box-shadow 1s ease-out;
                    background-color: var(--chat-background-color-trans);
                    box-shadow: none;
                }
            }

            &.animating {
                .chat-message-speaker-container {
                    animation: highlight 1s ease infinite;
                }
            }

            .chat-queue-indicator {
                position: absolute;
                bottom: 16px;
                right: 28px;
                color: var(--chat-color-alt);
                font-size: 36px;
                font-weight: bold;
                visibility: hidden;
                transition: opacity 0.3s ease;
                animation: blink 1.5s ease infinite;
                
                &.visible {
                    visibility: visible;
                    opacity: 1;
                }
            }
            
            .chat-queue-indicator-right {
                position: absolute;
                bottom: 16px;
                left: 28px; // Position on left side for player/character messages
                color: var(--chat-color-alt);
                font-size: 36px;
                font-weight: bold;
                visibility: hidden;
                transition: opacity 0.3s ease;
                animation: blink 1.5s ease infinite;
                
                &.visible {
                    visibility: visible;
                    opacity: 1;
                }
            }

            .chat-message-display {
                display: flex;
                flex-direction: row;
                gap: 10px;
                align-items: center;
                justify-content: space-between;
                box-sizing: border-box;
                border-radius: 10px;
                min-height: 120px;
                padding: 10px;
                
                // Default layout for DM messages (speaker on left)
                &.dm {
                    flex-direction: row;
                    
                    .chat-message-speaker-container {
                        order: 1;
                    }
                    
                    .chat-messager-content {
                        order: 2;
                    }
                }
                
                // Reversed layout for player and character messages (speaker on right)
                &.player, &.character {
                   // flex-direction: row-reverse;
                    
                    .chat-message-speaker-container {
                        order: 2;
                    }
                    
                    .chat-messager-content {
                        order: 1;
                        margin-right: 0;
                        margin-left: 10px;
                    }
                }
                
                .chat-message-speaker-container {
                    flex-direction: column;
                    display: flex;
                    justify-content: flex-start;
                    width: 84px;
                    align-items: center;
                    background-color: var(--chat-color-alt);
                    border: 4px solid var(--chat-button-color);
                    min-height: 84px;
                    box-sizing: border-box;
                    border-radius: 10px;
                    flex-shrink: 0;
                    
                    .chat-message-speaker {
                        color: white;
                        display: flex;
                        font-size: large;
                    }
                    
                    .chat-message-icon {
                        width: 50px;
                        height: 50px;
                        display: flex;
                        margin: 0;
                        background-color: var(--chat-button-color);
                        overflow: hidden;
                        box-sizing: border-box;
                        
                        .cimage {
                            position: relative;
                            display: flex;
                            border-radius: 5px;
                            width: 100%;
                        }
                    }
                }
                
                .chat-messager-content {
                    margin-right: 10px;
                    width: calc(100% - 100px);
                    display: flex;
                    flex-direction: column;
                    min-height: 100px;
                    
                    .chat-message-speaker {
                        display: none;
                    }
                }
                
                .chat-message-text {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    flex: 1;
                    border-radius: 15px;
                    border: 3px solid #00000055;
                    background: white;
                    width: 100%;
                    position: relative;
                    color: var(--notebook-text-color);
                    box-sizing: border-box;
                    min-height: 80px;
                    padding: 15px;
                    
                    .chat-message-words {
                        font-size: 1.2em;
                        line-height: 1.25em;
                        font-weight: 700;
                        pointer-events: none;
                        word-wrap: break-word;
                        width: 100%;
                        text-align: left;
                    }
                    
                    // Different styling for different message types
                    &.dm {
                        background: #f0f8ff; // Light blue for DM messages
                        border-color: var(--chat-color-alt);
                    }
                    
                    &.player {
                        background: #f0fff0; // Light green for player messages
                        border-color: #4caf50;
                        
                        .chat-message-words {
                            text-align: right;
                        }
                    }
                    
                    &.character {
                        background: #fff8f0; // Light orange for character messages
                        border-color: #ff9800;
                        
                        .chat-message-words {
                            text-align: right;
                        }
                    }
                }
            }
        }
    }
}


.chat-message-display {
    display: flex;
    flex-direction: row;
    max-height: 100%;
    .chat-message-speaker-container {
        .chat-message-speaker {
            &.under-icon {
              
                color: black;
               
            }
        }
    }
    .chat-message-icon{
        width: 150px;
        height: 150px;
        border-radius: 50%;
   
        margin: 10px;
        .chat-message-speaker {
            display: none;
        }
    }
    .chat-messager-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        .chat-message-speaker {
            font-weight: bold;
            font-size: 1.5em;
            margin-bottom: 4px;
            display: static;
        
        }
        .chat-message-text{
            border-radius: 5px;
            background: white;
            padding: 2px;
            min-height: 100px;
            width: 100%;
            max-height: 100%;
            color: var(--notebook-text-color);
            font-size: 1.1em;
            font-weight: bold;
            box-sizing: border-box;

        }
    }
}



@keyframes highlight {
    0% {
        border-color: var(--chat-button-color);
    }
    50% {
        border-color: var(--chat-color-alt);
    }
    100% {
        border-color: var(--chat-button-color);
    }
}

@keyframes blink {
    0%, 100% {
        opacity: 0.3;
    }
    50% {
        opacity: 1;
    }
}

@keyframes pulseGlow {
    0% {
        box-shadow: 0 0 5px rgba(129, 174, 227, 0.3);
    }
    50% {
        box-shadow: 0 0 20px rgba(129, 174, 227, 0.8);
    }
    100% {
        box-shadow: 0 0 5px rgba(129, 174, 227, 0.3);
    }
}

@keyframes fadeInHighlight {
    0% {
        background-color: var(--chat-background-color-trans);
        box-shadow: none;
    }
    20% {
        background-color: rgba(129, 174, 227, 0.2);
        box-shadow: 0 0 10px rgba(129, 174, 227, 0.5);
    }
    100% {
        background-color: rgba(129, 174, 227, 0.1);
        box-shadow: 0 0 5px rgba(129, 174, 227, 0.3);
    }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(2px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-word {
  display: inline-block;
  opacity: 0;
  transition: opacity 0.2s ease-out;
  
  &.visible {
    opacity: 1;
    transform: translateY(0);
  }
  
  &:not(.visible) {
    opacity: 0;
    transform: translateY(2px);
  }
}

