		.travel-sections {
			flex-direction: column-reverse;
			.travel-section {
				width: 100%;
				min-height: 50px;
			}
		}

		.travel-thermometer {
			position: relative;
			width: 30px;
			//height: 100%;
			margin-top: 0;
			font-size: 0.7em;
			display: flex;
			align-items: center;
			cursor: default;
			min-height: 300px;
			pointer-events: none;
			//border: 2px solid white;
			&.journey {
				height:auto;
			}
			.meter-markers {
				flex-direction: column-reverse;
				top: -6px;
				left: -1px;
				height: 104%;
			}

			.meter-icon {
				font-size: 0.8em;
