	.accept-journey-button {
		background: #4a4a4a;
		color: white;
		border: 6px solid #f0f0f0;
		padding: 12px;
		border-radius: 20px;
		cursor: pointer;
		transition: all 0.2s ease;
		display: flex;
		flex-direction: column;
		align-items: center;
		font-size: 0.5em;
		margin-top: 20px;

		&.disabled {
			opacity: .5;
			pointer-events: none;
			cursor: normal;
		}

		&:hover {
			background: #5a5a5a;
			transform: scale(1.05);
		}

		.journey-score {
			font-size: 0.8em;
			color: #b9b9b9;
			margin-top: 8px;
		}
	}
}

