.encounter-header {
  // width: 100%;
  padding: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  box-sizing: border-box;
  font-size: 0.5em;
  .encounter-header-content {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    // width: 100%;
    gap: 20px;
    //  border: 2px solid red;

    .encounter-header-left,
    .encounter-header-right {
      max-width: 33%;
      min-width: 33%;

      display: flex;
      align-items: center;
      justify-content: center;
      border: 6px solid var(--header-border-color, #acacac);
      border-radius: 15px;
      padding: 10px;
      background-color: var(--header-background-color, #8f9f94a1);
      //  min-width: 200px;

      .character-holder {
        width: 100%;
        
        .characters-title {
          font-size: 1.2em;
          margin-bottom: 10px;
          color: white;
        }

        .characters-display {
          .exhaustion-title {
            font-size: 0.9em;
            color: #ff6b6b;
            margin-bottom: 8px;
          }

          
        }
      }

    

      &.with-instructions {
        background-color: var(--ui-color-info-background, #2c2c2c);
        color: white;
        position: relative;
        width: 100%;
        font-size: 28px;
        .tooltip-help-icon {
          left: 10px;
          top: 10px;
        }
      }
      
      &.small-font {
        font-size: .9em;
      }
    }

    .encounter-header-center {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      max-width: 33%;
      border: 6px solid var(--header-border-color, #acacac);
      border-radius: 15px;
      padding: 12px;
      background-color: var(--header-background-color, #8f9f94a1);

      .encounter-header-title {
        font-size: 1.25em;
        font-weight: bold;
        margin-bottom: 8px;
      }

      .encounter-header-description {
        font-size: 0.85em;
        line-height: 1;
      }
    }
  }

}
