@keyframes border-conflicted {
	0% {
		border-color: rgb(39, 205, 24);
	}
	50% {
		border-color: rgb(255, 0, 0);
	}
	100% {
		border-color: rgb(55, 255, 0);
	}
}

.tag-pill {
	border-radius: 15px;
	height: fit-content;
	width: fit-content;
	position: relative;
	min-height: 24px;
	white-space: nowrap;
	padding: 0px 8px;
	margin: 2px 6px;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.2s ease;
	cursor: pointer;
	font-weight: bold;
	font-size: 1em;
	color: rgb(241, 241, 241);
	// Existing styles from text-pill
	&.hate {
		// background-color: #ff6e6e67;
		// border: 2px solid #ff000067;
		color: #c74f4f;
	}

	&.like {
		// background-color: #86d3e867;
		// border: 2px solid #557f9267;
		color: #6acf8d;
	}
	&.conflicted {
		// background-color: #86d3e867;
		// border: 2px solid #557f9267;
		color: #ff00ae;
	}
	&.trait {
		background-color: #d6ae8667;
		border: 2px solid #a3824f67;
	}

	&.branch {
		background-color: #c5d6ae67;
		border: 2px solid #889c6667;
	}

	&.expected {
		background-color: #aed6be67;
		border: 2px solid #82b39267;
	}

	&.established {
		background-color: #d6aec567;
		border: 2px solid #b39c8267;
	}

	&.combatant {
		background-color: #aebdd667;
		border: 2px solid #8a9cb367;
	}

	// Tag type styles
	&.type-location,
	&.location-tag {
		background-color: var(--tag-color-location);
		border: 2px solid #ffffff;
		color: #ffffff;
	}

	&.type-location-expected,
	&.type-foreshadowing-expected{

		background-color: #9696966e;
		border: 2px solid #747474;
		//opacity:
	
	}
  
  &.type-encounter-expected {
    	color: #3f3f3f;
  }

	&.type-encounter {
		background-color: #00000000; //var(--tag-color-encounter);
		border: none;
		color: #fafde2;
	}

	&.type-puzzle {
		background-color: #5f7d9ebe; //var(--tag-color-encounter);
		border-style: normal;
		border: 2px solid #ffffff;
		color: #ffffff;
		text-shadow: #000000;
	}

	&.type-puzzle-answer{
		background-color: #f2ff05; //var(--tag-color-encounter);
		//border-style: normal;
		border: 2px solid #ffffff;
		color: #000000;
	}

	&.type-all {
		background-color: var(--tag-color-all);
		border: 2px solid #ffffff;
		color: #ffffff;
	}

	&.player-conflicted {
		//background: linear-gradient(135deg, #00fc5867, #ff6e6e67);

		animation: border-conflicted 2s infinite;
	}
	&.type-plot {
		background-color: #000000;
		border: 2px solid #ffffff;
		color: #ffffff;
	}

	&.type-backstory {
		background-color: var(--tag-color-backstory);
		border: 2px solid #ffffff;
		color: #ffffff;
	}

	&.type-foreshadowing {
		background-color: var(--tag-color-upcoming);
		border: 2px solid #ffffff;
		color: white;
	}

	&.type-foreshadowing-random {
		background-color: var(--tag-color-upcoming);
		border: 2px solid #ffffff;
		color: white;
		position: relative;

		// Pulsing border
		animation: outline-pulse 2s infinite;
		outline: 4px solid rgba(255, 255, 255, 0.5);
	}

	@keyframes shimmer {
		0% {
			transform: translateX(-100%) rotate(30deg);
		}
		100% {
			transform: translateX(100%) rotate(30deg);
		}
	}

	@keyframes outline-pulse {
		0% {
			outline-color: rgb(0, 0, 0);
		}
		50% {
			outline-color: rgb(12, 245, 129);
		}
		100% {
			outline-color: rgb(0, 0, 0);
		}
	}

	&.type-magic-item {
		background-color: var(--tag-color-magic);
		border: 2px solid #ffffff;
	}

	&.type-magic-item-expected {
		background-color: var(--tag-color-magic-expected);
		border: 2px solid #ffffff;
	}

	&.type-treasure {
		background-color: var(--tag-color-treasure);
		border: 2px solid #ffffff;
		color: #2c2c09;
		padding-left: 20px;
		padding-right: 20px;
	}

	&.type-dmpoint {
		background-color: #000000;
		border: 2px solid #ffffff;
	}
	&.type-none {
		background-color: #aaaaaa;
		border: 2px solid #ffffff;
	}
	&.type-enemy {
		background-color: var(--tag-color-danger);
		color: black;
		&.player-hate {
			color: #ff2600;
		}
	}

	&.player-like {
		// color: #66c085;
		border-color: #66c085;
		border-width: 4px;
	}

	&.player-hate {
		//color: #7b3131;
		border-color: #7b3131;
		border-width: 4px;
	}

	// DEFINE STYLES AFTER HERE IF YOU WANT TO IGNORE PLAYER TASTE
	&.type-collection {
		background-color: #51754100;
		border: 2px solid #4e4e4e;
		color: #3e3e3e;
		padding-left: 10px;
		padding-right: 10px;
	}

	&.type-collection-collected {
		background-color: #e2e2e2;
		border: 2px solid #9e9e9e;
		color: #1d1d1d;
	}

	&.type-quest {
		background-color: #ff1493; // Bright pink
		border: 2px solid #ff69b4;
		color: #fff;
		font-weight: bold;
		box-shadow: 0 0 10px rgba(255, 20, 147, 0.5);
		
		&:hover {
			background-color: #ff69b4;
			box-shadow: 0 0 15px rgba(255, 20, 147, 0.7);
		}
	}

	&.type-quest-terminal {
		background-color: #ff1493; // Bright pink
		border: 2px solid #ffff00; // Yellow border for terminal
		color: #fff;
		font-weight: bold;
		box-shadow: 0 0 10px rgba(255, 20, 147, 0.5), 0 0 5px rgba(255, 255, 0, 0.5);
		
		&:hover {
			background-color: #ff69b4;
			box-shadow: 0 0 15px rgba(255, 20, 147, 0.7), 0 0 10px rgba(255, 255, 0, 0.7);
		}
	}

	// States
	&.disabled,
	&.used {
		opacity: 0.5;

		filter: grayscale(50%);
		cursor: default;
	}

	&.off {
		cursor: pointer;
		pointer-events: none;
	}

	&.highlighted {
		transform: scale(1.05);
		box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
		filter: brightness(1.2);
	}

	&:hover:not(.disabled):not(.used) {
		transform: translateY(-2px);
		filter: brightness(1.15);

		.tooltip {
			opacity: 1;
			transform: translateX(-50%) translateY(-8px);
		}
	}

	// Tooltip
  .tooltip {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%) translateY(200%);
    padding: 8px;
    background: rgba(211, 211, 211, 0.8);
    border-radius: 4px;
    font-size: 0.9em;
    opacity: 0;
    transition: all 0.3s ease;
    pointer-events: none;
    max-width: 300px;
    width: max-content;
    white-space: normal;
    word-wrap: break-word;

    z-index: 1000;
    .tooltip-content {
      color: #363636;
      text-align: center;
    }
		// Pulse animation
		// &.pulse {
		animation: tooltip-pulse 1.2s infinite;
		// }

		@keyframes tooltip-pulse {
			0% {
				box-shadow: 0 0 0 0 rgba(211, 211, 211, 0.7);
			}
			70% {
				box-shadow: 0 0 12px 8px rgba(211, 211, 211, 0.3);
			}
			100% {
				box-shadow: 0 0 0 0 rgba(211, 211, 211, 0);
			}
		}

		// This is styled to look like the info tooltips :P
		&.info-tooltip {
			background-color: rgba(0, 0, 0, 0.8);
			color: white;
			padding: 16px;
			border-radius: 4px;
			font-size: 1.5rem;
			line-height: 1.4;
			text-align: center;
			white-space: normal;
			word-wrap: break-word;
			width: 400px;
			opacity: 1;
			box-shadow: none;
			animation: none;
			.tooltip-content {
				color: white;
				font-weight: normal;
			}
		}
	}

	// Treasure icon container
	.treasure-icon-container {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 24px;
		height: 24px;

		img {
			width: 40px;
			height: 40px;
			// object-fit: contain;
		}
	}

	// Treasure icon container
	.dmpoint-icon-container {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 24px;

		img {
			width: 40px;
			height: 35px;
			// object-fit: contain;
		}
	}
}

.encounter-medallion-outer {
	background: radial-gradient(circle at center, #928a73, #2c2c2c);
	border: 2px solid rgba(183, 176, 99, 0.8);
	box-shadow: 0 0 10px rgba(255, 255, 255, 0.5),
		0 0 20px rgba(255, 255, 255, 0.3), inset 0 0 8px rgba(255, 255, 255, 0.4);
	border-radius: 50%;
	width: 90px;
	height: 90px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s ease;
  box-sizing: border-box;
	
  &.expected {
   background: radial-gradient(circle at center, #94918a, #ffffff52);
   box-shadow: none;
  }
  
  &:hover {
		box-shadow: 0 0 20px rgba(255, 255, 255, 0.3),
			inset 0 0 25px rgba(255, 255, 255, 0.2);
		transform: scale(1.02);
	}
	.tag-pill {
		top: 26px;
    font-size: 20px;
		position: inherit;
	}
  .tooltip{
   position: absolute;
   bottom: 82px;
   left: 50%;
  }

  &.off {
	cursor: pointer;
		pointer-events: none;
  }

  }
