.player-character-level-row {
  background-color: #A0826D;
  border: 2px solid #8B7355;
  border-radius: 8px;
  padding: 15px;
  transition: all 0.2s ease;

  &.highlighted {
    background-color: #B8956A;
    border-color: #D2B48C;
    box-shadow: 0 0 10px rgba(210, 180, 140, 0.5);
  }

  .player-info {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    gap: 12px;

    .player-icon {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      border: 2px solid #5D4E37;
    }

    .player-name {
      font-size: 1.2em;
      font-weight: bold;
      color: #2F2F2F;
    }
  }

  .characters-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-left: 52px; // Align with player name
  }
}
