// Import base SelectQuestModal styles since we want similar appearance
@import './SelectQuestModal.scss';

.active-quests-modal-overlay {
	@extend .select-quest-modal-overlay;
}

// Specific styles for active quests modal
.active-quests-modal-overlay {
	.quest-tile-content.active-quest {
		// Make active quest tiles slightly different
		border: 2px solid rgba(68, 170, 153, 0.6);
		
		.quest-progress-bar {
			background: rgba(0, 0, 0, 0.5);
			border-radius: 6px;
			padding: 8px;
			margin-bottom: 12px;
			
			.progress-label {
				color: #fff;
				font-size: 14px;
				font-weight: bold;
				margin-bottom: 4px;
				text-align: center;
			}
			
			.progress-track {
				background: rgba(255, 255, 255, 0.2);
				border-radius: 4px;
				height: 8px;
				overflow: hidden;
				margin-bottom: 4px;
				
				.progress-fill {
					background: linear-gradient(90deg, #44aa99, #66cc99);
					height: 100%;
					border-radius: 4px;
					transition: width 0.3s ease;
				}
			}
			
			.progress-details {
				color: #ccc;
				font-size: 12px;
				text-align: center;
			}
		}
		
		.active-indicator {
			&:contains("READY TO COMPLETE") {
				background: rgba(76, 175, 80, 0.9);
				animation: pulse 2s infinite;
			}
		}
	}
	
	.no-active-quests {
		text-align: center;
		padding: 40px 20px;
		color: #ccc;
		
		p {
			margin: 0 0 16px 0;
			font-size: 16px;
			
			&:first-child {
				font-size: 18px;
				font-weight: bold;
				color: #fff;
			}
		}
	}
}

@keyframes pulse {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0.7;
	}
	100% {
		opacity: 1;
	}
}