.encounter-ability-button {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	background: #ffffff00;
padding: 0px;
	width: 160px;
	height: 160px;
	//background-color: var(--event-tile);
	border-radius:15px;
	border: 4px solid var(--event-tile-border);
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
	pointer-events: all;
	transition: all 0.2s ease;
	z-index: 2;
	&.snack {
		left: 20px;
	}
	&.break {
		right: 20px;
	}
	&.quest-debug {
		top: 200px;
		left: 20px;
		border-color: #ff69b4;
		.ability-container {
			background-color: rgba(255, 105, 180, 0.1);
		}
		&:hover {
			background-color: rgba(255, 105, 180, 0.2);
		}
		z-index: 99;
	}
	&.hidden{
		display: none;
	}

	&.disabled {
		opacity: 0.5;
		cursor: normal;
				pointer-events: none;
	}
	&:hover{
		transform: scale(1.05);
		background-color: var(--event-tile-hover);
	}
	.ability-label{
		font-size: 1.5em;
		font-weight: bold;
		//background-color: var(--event-tile);
	}

	.ability-container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 80px;
		position: relative;
		background-color: var(--event-tile);
	}

	.lock-icon-container {
		width: 100%;
		right: 10px;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: rgba(188, 188, 188, 0.548);		cursor: pointer;
		pointer-events: all;
		z-index: 3;
		.lock-icon {
			font-size: 50px;
		}
	}

	.ability-info {
		font-size: .9em;
		font-weight: normal;
		width: 80%;
	}
	.ability-amount-label-container {
		position: absolute;
		bottom: 10px;
		left: 10px;
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 10px;
		padding: 5px 10px;
		font-size: 1.2em;
		color: white;
		.ability-amount-label{
			font-weight: bold;
			color: white;
			font-size: 1.2em;
		}
	}

	.quest-icon-container {
		
		display: flex;
		align-items: center;
		justify-content: center;
	
			.quest-icon {
			position: absolute;
			top:  0px;
			left: 0px;
			width: 100%;
			height: 100%;
			font-size: 50px;
		}
	}
}



