	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 160px;
	height: 160px;
	background-color: var(--event-tile);
	border-radius: 100%;
	border: 4px solid var(--event-tile-border);
	position: absolute;
	top: 20px;
	left: 20px;
	cursor: pointer;
	pointer-events: all;
	transition: all 0.2s ease;
	z-index: 2;
	&.hidden{
		display: none;
	}
	&:hover{
		transform: scale(1.05);
		background-color: var(--event-tile-hover);
	}
	.encounter-overview-exposition-label{
		font-size: 1.5em;
		font-weight: bold;
	}

	.encounter-overview-exposition-effect {
		font-size: .9em;
		font-weight: normal;
		width: 80%;
	}
}