.character-holder {
  
  padding: 10px;
  
  .characters-title {
    font-size: 14px;
    margin-bottom: 10px;
  }

  .characters-display {
    .exhaustion-title {
      font-size: 12px;
      color: #ff4d4d;
      margin-bottom: 5px;
    }

    .characters-row {
      display: flex;
      gap: 10px;
      justify-content: space-around;
      align-items: center;
      .character-selector-line {
        display: flex;
        flex-direction: row;
        height: 80px;
        gap: 10px;
      }

      .selector-button {
        cursor: pointer;
        width: 110px;
        height: 60px;
        border-radius: 12px;
        border: 6px solid #ffffff88;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--ui-color-primary);
        transition: background-color 0.2s;
        font-size: 20px;
        color: var(--ui-color-primary-text);

        &:hover {
          background-color: #3b3b3b;
        }

        &.random-button {
          margin-right: 10px;
        }

        &.end-button {
          margin-left: 10px;
        }
      }
    }
  }
}
