
.modal-container {
  &.setscene {
  width: 80%;
  max-width: 1100px;
  padding: 0;
  border-width: 8px;;
  .modal-header {
    border-bottom: none;
    background-color: #4a4a5a;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    h2 {
      margin-left: 20px;
      margin-top: 14px;
      margin-bottom: 2px;
    }
  }
  .setscene-content {
    display: flex;
    gap: 30px;
    margin-bottom: 30px;
    
    .setscene-image {
      flex: 0 0 300px;
      height: 300px;
      border-radius: 12px;
      margin: 24px;
      overflow: hidden;
      border: 3px solid #4a4a5a;
      
      .cimage {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    
    .setscene-info {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 20px;
      
      .location-name {
        font-size: 2.2em;
        color: #ffffff;
        margin: 0;
        font-weight: bold;
      }
      
      .location-description {
        font-size: 1.2em;
        color: #cccccc;
        line-height: 1.5;
        margin: 0;
      }
      
      .encounter-info {
        background: rgba(255, 255, 255, 0.1);
        padding: 15px;
        border-radius: 8px;
        margin-right: 40px;
       // border-left: 4px solid #4CAF50;
        
        h4 {
          margin: 0 0 10px 0;
          color: white;
          font-size: 1.1em;
        }
        
        p {
          margin: 5px 0;
          color: #dddddd;
        }
      }
      
      .location-tags {
        h4 {
          margin: 0 0 10px 0;
          color: #ffffff;
          font-size: 1.1em;
        }
        
        .tags-grid {
          display: flex;
          flex-wrap: wrap;
          gap: 8px;
          
          .location-tag {
            background: linear-gradient(135deg, #3a3a4a, #5a5a6a);
            color: #ffffff;
            padding: 6px 12px;
            border-radius: 16px;
            font-size: 0.9em;
            border: 1px solid #6a6a7a;
          }
        }
      }
    }
  }
  
  .setscene-actions {
    display: flex;
    justify-content: center;
   // padding-top: 20px;
   // border-top: 2px solid #4a4a5a;
    
    .setscene-continue-button {
      background: var(--ui-color-primary);
      color: white;
      border: 6px solid white;
      margin-bottom: 8px;
      padding: 14px 30px;
      font-size: 1.3em;
      font-weight: bold;
      border-radius: 15px;
      cursor: pointer;
      transition: all 0.3s ease;
      
      &:hover {
        transform: translateY(-2px);
       background-color: var(--ui-color-primary-hover);
      }
      
      &:active {
        transform: translateY(0);
      }
    }
  }
}
}
