@keyframes pulse {
  0% {
      transform: scale(1);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
  }
  70% {
      transform: scale(1.05);
      box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
  }
  100% {
      transform: scale(1);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
  }
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 0.8; }
}


.town-encounter {
    position: relative;
    width: 100%;
    height: 100%;

    .results-panel {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      padding: 10px;
      border-top: 2px solid rgba(255, 255, 255, 0.3);
      color: white;
      font-size: 0.8em;
      z-index: 10;
      .results-panel-container {
        width: 50%;
      }
    }
    
    .buildings-container {
      position: relative;
      height: calc(75%);
      width: 95%;
      top: -12px;
      //overflow: hidden;
      .text-pill{
        font-size: 0.8em;
        padding-bottom: 2px;
      }
    }
  
    .building-card {
      position: absolute;
      background: rgba(255, 255, 255, 0.1);
      border: 8px solid rgba(123, 72, 31, 0.85);
      border-radius: 8px;
      padding: 12px;
      transition: transform 0.2s;
      font-size: .5em;
      color: white;
      top: 0px;
      left: 0px;
      display: flex;
      flex-direction: column;
      max-width: 240px;
      pointer-events: none;
      cursor: default;
      //height: 100%;  
      .score{
        font-size: .8em;
        font-weight: bold;
        color: white;
      }
      .interaction-hint {
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 0.8em;
        color: rgba(255, 255, 255, 0.8);
        animation: fadeInOut 2s infinite;
    }
      &.highlight {
        box-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
        border-color: rgba(255, 255, 255, 0.8);
        transform: scale(1.02);
        transition: all 0.3s ease;

        .building-tags .tag {
            &.clickable:not(.resolved) {
                animation: pulse 2s infinite;
            }
        }
      }
      &.selectable {
        border-color: rgba(255, 255, 255, 0.8);
        cursor: pointer;
        
        &:hover {
          transform: scale(1.02);
        }
      }
      .building-header {
        color: rgb(191, 191, 201);
        .building-name{
          font-size: 1.2em;
          font-weight: bold;
          line-height: .9em;
          margin-bottom: 4px;
          background-color: #00000044;
          border-radius: 14px;
          padding: 4px;
        }
        .effect-row{
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          font-size: 0.8em;
          margin-top: 2px;
          align-items: center;
          justify-content: center;
          gap: 10px;
          img {
            max-width: 30px;
            max-height: 30px;
          }
        }
       
      }
       .building-effect {
            font-size: 0.75em;
            color: white;
            font-style: italic;
            &.hidden {
              color: #00000000;
            }
        }

      .visitor-slots{
       
        display: flex;
        flex-direction: row;
       
        align-items: center;
        justify-content: center;
        gap: 10px;
        .slot {
          border: 6px solid #ffffff44;
           background-color: #00000044;
           border-radius: 15px;;
          height: 80px;
          width: 90px;
          position: relative;
         
          .slot-effect {
            position: absolute;
          
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
           
            align-items: center;
            justify-content: center;
            text-align: center;
            color: rgba(255, 255, 255, 0.6);
            padding: 4px;
            display: flex;
            flex-direction: column;
            font-size: .7em;
            color: white;
            .score{
               font-size: 1.1em;
            }
            .effect {
              font-size: 1.8em;
            }
            .effect-score {
              .effect-score-label {
                font-size: .6em;
              }
              .effect-score-value {
                font-size: 1.1em;
                font-weight: bold;
              }
            }
            .effect-emoji-display {
              font-size: 1.7em;
            }
            .effect-display-icon {
              width: 100%;
              height: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
              img {
                max-width: 50px;
                max-height: 50px;
              }
            }
           
        }
        }
      }

      .building-tags {
        position: absolute;
        left: 100%;  // Position right at the edge of the building
        top: 0;
        margin-left: -8px;  // Add some spacing from building
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 4px;
        min-width: 120px;  // Give enough space for tags
        
        .tag {
         // background: rgba(255, 255, 255, 0.1);
          // padding: 4px 8px;
          // border-radius: 4px;
          font-size: 0.8em;
          white-space: nowrap;
          opacity: 0.5;
          transition: all 0.2s ease;
          cursor: default;
          &.clickable {
            opacity: 1;
            cursor: pointer;
    
            &:hover {
             // background: rgba(255, 255, 255, 0.2);
              transform: scale(1.05);
            }
    
            &.resolved {
              opacity: 0.3;
              text-decoration: line-through;
              cursor: default;
              
              &:hover {
                background: none;
                transform: none;
              }
            }
          }
        }
      }

    }
  
    .characters-container {
      position: absolute;
      bottom: 20;
      left: 0;
      right: 0;
      height: 100px;
      display: flex;
      gap: 10px;
      padding: 10px;
      
      padding-top: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      color: rgb(199, 199, 199);
      
    }
    .characters-title{
      margin-bottom: 4px;
      background: #00000044;
      border-radius: 20px;
      padding: 10px;
      padding-top: 4px;
      font-size: .75em;
    }

  .exhaustion-title
  {
    font-size: .7em;
    line-height: .5em;
    margin-bottom: 6px;
  }
  
      .character-token {
            height: 100%;;
            border-radius: 10px;
            align-items: center;
            justify-content: center;
            display: flex;
            flex-direction: column;
        &.selected {
          transform: translateY(-10px);
        }
    
        &.animating {
          transition: all 0.5s ease;
        }
        .character-icon {
          height: 90%;
          
        }
        .character-name {
          font-size: 0.5em;
          color: white;
          text-align: center;
        }
      } 
      .characters-display {
        background: #00000044;  border: 6px solid #ffffff44;
        border-radius: 20px;
        .exhaustion-title {
          font-size: 0.5em;
          margin-top: 6px;
          color: white;
          font-weight: bold;
          text-align: center;
        }
      }
       .characters-row {
      display: flex;
      flex-direction: row;
      gap: 10px;
      height: 90px;
      justify-content: center;
      border-radius: 20px;
      padding-top: 12px;
      padding-bottom: 4px;
    
    }
  }

 
  
  @keyframes highlight {
    0%, 100% {
      box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
      transform: scale(1);
    }
    50% {
      box-shadow: 0 0 20px rgba(255, 255, 255, 0.8);
      transform: scale(1.01);
    }
  }
  
  .character-token {
    &.animating {
      position: fixed;
      pointer-events: none;
      z-index: 1000;
      
      // Initial state
      transform: translate(0, 0);
      
      // Add animation after mount
      &.moving {
        transform: translate(
          var(--move-x, 0),
          var(--move-y, 0)
        );
      }
    }
  }
  
@keyframes buildingFlash {
    0%, 100% {
      box-shadow: none;
      transform: scale(1);
    }
    50% {
      box-shadow: 0 0 20px rgba(255, 255, 255, 0.8);
      transform: scale(1.05);
    }
  }