.c-container {
	position: relative;
	border: 8px solid var(--ui-color-primary-border, #b3c4c3);
	border-radius: 16px;
	background: var(--ui-color-primary, #65b6b2);
	overflow: hidden;

	// Title bar
	&__title-bar {
		background: var(--ui-color-primary-border, #b3c4c3);
		padding: 12px 20px;
		margin: -8px -8px 0 -8px; // Negative margins to extend to edges
		border-bottom: none;
	}

	&__title {
		margin: 0;
		font-size: 1.2em;
		font-weight: bold;
		color: var(--ui-color-primary-text, #efefef);
		text-align: center;
	}

	&__content {
		padding: 15px;
	}

	// Variant styles
	&--primary {
		border-color: var(--ui-color-primary-border, #b3c4c3);
		background: var(--ui-color-primary, #65b6b2);

		.c-container__title-bar {
			background: var(--ui-color-primary-border, #b3c4c3);
		}

		.c-container__title {
			color: var(--ui-color-primary-text, #efefef);
		}
	}

	&--primary-container {
		border-color: var(--ui-color-primary-border, #b3c4c3);
		background: var(--ui-color-primary-alt, #2e9c77);

		.c-container__title-bar {
			background: var(--ui-color-primary-border, #b3c4c3);
		}

		.c-container__title {
			color: var(--ui-color-primary-text, #efefef);
		}
	}

	&--secondary {
		border-color: var(--ui-color-secondary-border, #8b4513);
		background: var(--ui-color-secondary, #3a3a3a);

		.c-container__title-bar {
			background: var(--ui-color-secondary-border, #8b4513);
		}

		.c-container__title {
			color: var(--ui-color-secondary-text, #f0f0f0);
		}
	}

	&--highlight {
		border-color: var(--ui-color-highlight-border, #cd853f);
		background: var(--ui-color-highlight, #4a3a2a);

		.c-container__title-bar {
			background: var(--ui-color-highlight-border, #cd853f);
		}

		.c-container__title {
			color: var(--ui-color-highlight-text, #f0f0f0);
		}
	}

	// Hover effects - removed translateY to prevent jumping
	&:hover {
		transition: all 0.2s ease;
		box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
	}

	// No title version - just content with padding
	&:not(:has(.c-container__title-bar)) {
		.c-container__content {
			padding: 20px;
		}
	}
}

