.character-level-row {
  display: flex;
  align-items: center;
  gap: 15px;
  position: relative;

  .character-toggle {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px;
    border: 2px solid #8B7355;
    border-radius: 8px;
    background-color: #D2B48C;
    cursor: pointer;
    transition: all 0.2s ease;
    min-width: 200px;

    &:hover {
      transform: scale(1.02);
    }

    &.selected {
      border-color: #5D4E37;
      box-shadow: 0 0 8px rgba(93, 78, 55, 0.5);
    }

    &.very-unready {
      background-color: #ffcdd2;
      &.selected {
        background-color: #ef9a9a;
      }
    }

    &.unready {
      background-color: #ffe0b2;
      &.selected {
        background-color: #ffcc80;
      }
    }

    &.ready {
      background-color: #c8e6c9;
      &.selected {
        background-color: #a5d6a7;
      }
    }

    &.perfect {
      background-color: #4caf50;
      &.selected {
        background-color: #388e3c;
      }
    }

    &.overready {
      background-color: #ffab91;
      &.selected {
        background-color: #ff8a65;
      }
    }

    .character-icon {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      overflow: hidden;
      border: 1px solid #5D4E37;
    }

    .character-name {
      font-size: 1em;
      font-weight: bold;
      color: #2F2F2F;
    }
  }

  .experience-bar {
    flex: 1;
    position: relative;

    &.selected {
      filter: brightness(1.2);
    }
  }

  .tooltip {
    position: absolute;
    top: -60px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.9);
    color: white;
    padding: 8px 12px;
    border-radius: 4px;
    font-size: 0.85em;
    white-space: pre-line;
    z-index: 100;
    max-width: 300px;
    pointer-events: none;

    &::after {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: rgba(0, 0, 0, 0.9) transparent transparent transparent;
    }
  }
}
