// Welcome Modal Styles - Similar to EncounterTagModal
.modal-container.welcome {
  width: 90%;
  max-width: 1400px;
  height: 94vh;
  max-height: 1000px;
  display: flex;
  flex-direction: column;
 
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);

  // Header
  .welcome-header {
    position: relative;
    height: 150px;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
    
    
    .header-background {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      
      .cimage {
        width: 80px;
        height: 80px;
        opacity: 0.3;
      }
    }
    
    .header-overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(45deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.1));
      z-index: 2;
      border-radius: 15px;
    }
    
    .header-content {
      position: relative;
      z-index: 3;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: white;
      
      .welcome-title {
        font-size: 2.5rem;
        font-weight: bold;
        margin: 0;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
      }
      
      .welcome-subtitle {
        font-size: 1.2rem;
        margin: 0.5rem 0 0 0;
        opacity: 0.9;
        text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
      }
    }
  }

  // Body - Side by side layout
  .welcome-body {
    flex: 1;
    display: flex;
    height: calc(100% - 200px); // Account for header and footer
    
    .conversation-section {
      flex: 1;
      // padding-top: 20px;
      // padding-bottom: 20px;
      border: none;
     // border-right: 1px solid rgba(255, 255, 255, 0.1);
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      margin-top: 20px;
      margin-bottom: 20px;
     // background: #2a2a3a;
      .section-header {
        margin-bottom: 15px;
        
        h3 {
          color: #e0e6ed;
          font-size: 1.5rem;
          margin: 0;
          padding-bottom: 10px;
          border-bottom: 2px solid #3498db;
        }
      }
      
      .conversation-content {
        flex: 1;
        overflow-y: auto;
        padding-right: 10px;
        
        // Apply conversation scrollbar styling
        &::-webkit-scrollbar {
          width: 6px;
        }

        &::-webkit-scrollbar-track {
          background: rgba(255, 255, 255, 0.05);
          border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
          background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
          border-radius: 3px;
          border: 1px solid rgba(255, 255, 255, 0.1);
          transition: all 0.2s ease;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.2));
          transform: scaleX(1.2);
        }

        scrollbar-width: thin;
        scrollbar-color: rgba(255, 255, 255, 0.2) rgba(255, 255, 255, 0.05);
       
        // Reuse conversation styles from EncounterTagModal
        .conversation-display {
          
          margin-left: 0px;
          margin-right: 0px;
          justify-content: flex-start;
          background-color: #00000000;
        }
        
        .no-conversation {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
         // color: #bdc3c7;
          font-style: italic;
        }
      }
    }
    
    .info-section {
      
      flex: 1;
      padding: 20px;
      padding-top: 8px;
      padding-bottom: 8px;
      margin-top: 12px;
      border-radius: 15px;;
      display: flex;
      flex-direction: column;
      gap: 8px;
      
      .section-header {
        margin-bottom: 15px;
        
        h3 {
          color: #e0e6ed;
          font-size: 1.5rem;
          margin: 0;
          padding-bottom: 10px;
        //  border-bottom: 2px solid #e74c3c;
        }
      }
      
      .info-content {
        flex: 1;
        overflow-y: auto;
        padding-right: 10px;
        
        // Apply conversation scrollbar styling
        &::-webkit-scrollbar {
          width: 6px;
        }

        &::-webkit-scrollbar-track {
          background: rgba(255, 255, 255, 0.05);
          border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
          background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
          border-radius: 3px;
          border: 1px solid rgba(255, 255, 255, 0.1);
          transition: all 0.2s ease;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.2));
          transform: scaleX(1.2);
        }

        scrollbar-width: thin;
        scrollbar-color: rgba(255, 255, 255, 0.2) rgba(255, 255, 255, 0.05);
      }
    }
  }

 
 .welcome-button {
      background: var(--ui-color-primary);
      color: white;
      border: none;
      border-radius: 8px;
      padding: 15px 40px;
      font-size: 1.2rem;
      font-weight: bold;
      cursor: pointer;
      transition: all 0.3s ease;
      text-transform: uppercase;
      letter-spacing: 1px;
      border: 6px solid white;
      &.info {
        //width: 90%;
        border-width: 4px;
        background-color: var(--ui-color-secondary);
        padding: 8px;
        padding-left: 42px;
        padding-right: 42px;
      }
      &:hover:not(.disabled) {
        background: var(--ui-color-primary-hover);
        transform: translateY(-2px);
      
      }
      
      &:active:not(.disabled) {
        transform: translateY(0);
      }
      
      &.disabled {
        background: #7f8c8d;
        cursor: not-allowed;
        opacity: 0.6;
        transform: none;
        box-shadow: none;
      }
    }
  }

  // Footer
  .welcome-footer {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.2);
    
   
  
}

// Game Info Panel Styles
.game-info-panel {
  color: #ecf0f1;
  line-height: 1.6;
  
  .info-header {
    text-align: center;
    margin-bottom: 10px;
    
    h2 {
      color: #3498db;
      font-size: 2rem;
      margin: 0 0 10px 0;
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    }
    
    .info-subtitle {
      color: #bdc3c7;
      font-size: 1.1rem;
      font-style: italic;
    }
  }
  
  .info-content {
    .info-section {
      margin-bottom: 25px;
      padding: 20px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 8px;
     // border-left: 4px solid #3498db;
      
      // &.highlight {
      //  // border-left-color: #f39c12;
      //   background: rgba(243, 156, 18, 0.1);
      // }
      
      h3 {
        color: #3498db;
        font-size: 1.3rem;
        margin: 0;
        display: flex;
        align-items: center;
        gap: 10px;
      }
      
      p {
        margin: 0 0 10px 0;
        color: #ecf0f1;
        font-size: 23px;
        line-height: 30px;
      }
      
      ul {
        margin: 0px 0;
        padding-left: 20px;
        line-height: 17.5px;
        font-size: 17px;;
        
        li {
          margin-bottom: 8px;
          color: #bdc3c7;
          
          strong {
            color: #e74c3c;
            font-weight: 600;
          }
        }
      }
    }
  }
}

// Inline dice symbols styling
.inline-dice-symbols {
    display: inline-flex;
    align-items: center;
    gap: -2px; // Slight overlap
    margin: 0 4px;
    position: relative;
    
    .cimage {
      width: 18px;
      height: 18px;
      display: inline-block;
      position: relative;
      
      &:nth-child(1) { // d4
        z-index: 3;
      }
      
      &:nth-child(2) { // d6
        z-index: 2;
        margin-left: -3px; // Overlap with d4
      }
      
      &:nth-child(3) { // d20
        z-index: 1;
        margin-left: -3px; // Overlap with d6
      }
    }
  }

// Button centering container
.button-container-center {
    display: flex;
    justify-content: center;
    margin: 20px 0;
    width: 100%;
  }

// Responsive design
@media (max-width: 1200px) {
  .modal-container.welcome {
    .welcome-body {
      flex-direction: column;
      
      .conversation-section {
        border-right: none;
       // border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        flex: 0 0 40%;
        .conversation-content {
          
          .conversation-display {
          background-color: none;
          }
        }
      }
      
      .info-section {
        flex: 1;
      }
    }
  }
}

@media (max-width: 768px) {
  .modal-container.welcome {
    width: 95%;
    height: 95vh;
    
    .welcome-header {
      height: 100px;
      
      .header-content {
        .welcome-title {
          font-size: 1.8rem;
        }
        
        .welcome-subtitle {
          font-size: 1rem;
        }
      }
    }
    
    .welcome-body {
      .conversation-section,
      .info-section {
        padding: 15px;
        
      }
    }
    
    .game-info-panel {
      .info-header h2 {
        font-size: 1.5rem;
      }
      
      .info-content .info-section {
        padding: 15px;
        
        h3 {
          font-size: 1.1rem;
        }
      }
    }
  }
}
