.chat-rule-test-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--bg-color, #2a2a2a);
  border: 2px solid var(--accent-color, #8b4513);
  border-radius: 8px;
  padding: 20px;
  width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  z-index: 1000;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  
  .panel-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    border-bottom: 1px solid var(--accent-color, #8b4513);
    padding-bottom: 10px;
    
    h3 {
      margin: 0;
      color: var(--text-color, #f0f0f0);
      font-size: 1.2em;
    }
    
    .close-btn {
      background: none;
      border: none;
      color: var(--text-color, #f0f0f0);
      font-size: 24px;
      cursor: pointer;
      padding: 0;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      
      &:hover {
        background: rgba(255, 255, 255, 0.1);
        border-radius: 4px;
      }
    }
  }
  
  .test-controls {
    margin-bottom: 20px;
    
    .control-group {
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      gap: 10px;
      
      label {
        min-width: 140px;
        color: var(--text-color, #f0f0f0);
        font-size: 0.9em;
      }
      
      select, input[type="range"] {
        flex: 1;
        padding: 5px;
        border: 1px solid var(--accent-color, #8b4513);
        background: var(--bg-color-light, #3a3a3a);
        color: var(--text-color, #f0f0f0);
        border-radius: 4px;
        
        &:focus {
          outline: none;
          border-color: var(--accent-color-light, #cd853f);
        }
      }
      
      span {
        min-width: 30px;
        text-align: center;
        color: var(--text-color, #f0f0f0);
        font-weight: bold;
      }
    }
    
    .button-group {
      display: flex;
      gap: 10px;
      margin-top: 20px;
      
      button {
        flex: 1;
        padding: 10px 20px;
        border: 1px solid var(--accent-color, #8b4513);
        background: var(--accent-color, #8b4513);
        color: white;
        border-radius: 4px;
        cursor: pointer;
        font-size: 0.9em;
        
        &:hover {
          background: var(--accent-color-light, #cd853f);
          border-color: var(--accent-color-light, #cd853f);
        }
        
        &.reset-btn {
          background: var(--bg-color-light, #3a3a3a);
          color: var(--text-color, #f0f0f0);
          
          &:hover {
            background: var(--bg-color-lighter, #4a4a4a);
          }
        }
      }
    }
  }
  
  .test-results {
    margin-bottom: 20px;
    
    h4 {
      margin: 0 0 10px 0;
      color: var(--text-color, #f0f0f0);
      font-size: 1em;
    }
    
    ul {
      margin: 0;
      padding-left: 20px;
      
      li {
        color: var(--text-color-secondary, #cccccc);
        margin-bottom: 5px;
        font-size: 0.9em;
      }
    }
    
    p {
      color: var(--text-color-secondary, #cccccc);
      font-style: italic;
      margin: 0;
    }
  }
  
  .rule-examples {
    border-top: 1px solid var(--accent-color, #8b4513);
    padding-top: 15px;
    
    h4 {
      margin: 0 0 10px 0;
      color: var(--text-color, #f0f0f0);
      font-size: 1em;
    }
    
    ul {
      margin: 0;
      padding-left: 20px;
      
      li {
        color: var(--text-color-secondary, #cccccc);
        margin-bottom: 8px;
        font-size: 0.85em;
        
        strong {
          color: var(--accent-color-light, #cd853f);
        }
      }
    }
  }
}

/* Dark theme overrides */
.dark-theme .chat-rule-test-panel {
  --bg-color: #1a1a1a;
  --bg-color-light: #2a2a2a;
  --bg-color-lighter: #3a3a3a;
  --text-color: #e0e0e0;
  --text-color-secondary: #b0b0b0;
  --accent-color: #8b4513;
  --accent-color-light: #cd853f;
}

/* Light theme overrides */
.light-theme .chat-rule-test-panel {
  --bg-color: #f5f5f5;
  --bg-color-light: #ffffff;
  --bg-color-lighter: #e0e0e0;
  --text-color: #333333;
  --text-color-secondary: #666666;
  --accent-color: #8b4513;
  --accent-color-light: #cd853f;
}
