// Theme System using CSS Custom Properties
// This allows for easy theming of components by applying theme classes to parent elements

// Base theme variables structure
:root {
  // Default neutral theme
  --theme-primary: #4a4a5a;
  --theme-primary-light: #6a6a7a;
  --theme-primary-dark: #2a2a3a;
  --theme-secondary: #5a5a6a;
  --theme-accent: #7a7a8a;
  --theme-background: #1a1a2e;
  --theme-background-light: rgba(255, 255, 255, 0.5);
  --theme-background-medium: rgba(255, 255, 255, 1);
  --theme-text-primary: #ffffff;
  --theme-text-secondary: #cccccc;
  --theme-text-accent: #fff7cb;
  --theme-border: rgba(255, 255, 255, 1);
  --theme-border-light: rgba(255, 255, 255, 0.5);
  --theme-shadow: rgba(0, 0, 0, 0.3);
  --theme-glow: rgba(255, 215, 0, 0.3);
}

// Blue Theme (for DM Abilities and Encounter Tag modals)
.blue-theme {
  --theme-primary: #1a1a2e;
  --theme-primary-light: #16213e;
  --theme-primary-dark: #0f1419;
  --theme-secondary: #2a3f5f;
  --theme-accent: #4682b4;
  --theme-background: linear-gradient(135deg, #1a1a2e, #16213e);
  --theme-background-light: rgba(70, 130, 180, 0.4);
  --theme-background-medium: rgba(70, 130, 180, 1);
  --theme-text-primary: #ffffff;
  --theme-text-secondary: #b8d4f0;
  --theme-text-accent: #d6e2ff;
  --theme-border: rgba(70, 130, 180, .7);
  --theme-border-light: rgba(70, 130, 180, 0.5);
  --theme-shadow: rgba(0, 0, 0, 0.4);
  --theme-glow: rgba(70, 130, 180, 0.4);
}

// Green Theme (for Tag Collections modal)
.green-theme {
  --theme-primary: #1a2f1a;
  --theme-primary-light: #567d46;
  --theme-primary-dark: #0d1a0d;
  --theme-secondary: #2f4a2f;
  --theme-accent: #7a9665;
  --theme-background: #1a2f1a;
  --theme-background-light: rgba(122, 150, 101, 0.5);
  --theme-background-medium: rgba(122, 150, 101, 1);
  --theme-text-primary: #c5e6b8;
  --theme-text-secondary: #a8d193;
  --theme-text-accent: #ffd700;
  --theme-border: #7a9665;
  --theme-border-light: rgba(122, 150, 101, 0.8);
  --theme-shadow: rgba(0, 0, 0, 0.3);
  --theme-glow: rgba(122, 150, 101, 0.4);
}

// Red Theme (for combat/danger modals)
.red-theme {
  --theme-primary: #2e1a1a;
  --theme-primary-light: #4a2f2f;
  --theme-primary-dark: #1a0d0d;
  --theme-secondary: #5f2a2a;
  --theme-accent: #dc143c;
  --theme-background: linear-gradient(135deg, #2e1a1a, #4a2f2f);
  --theme-background-light: rgba(220, 20, 60, 0.1);
  --theme-background-medium: rgba(220, 20, 60, 0.2);
  --theme-text-primary: #ffffff;
  --theme-text-secondary: #f0b8b8;
  --theme-text-accent: #ffd700;
  --theme-border: rgba(220, 20, 60, 0.3);
  --theme-border-light: rgba(220, 20, 60, 0.5);
  --theme-shadow: rgba(0, 0, 0, 0.4);
  --theme-glow: rgba(220, 20, 60, 0.4);
}

// Purple Theme (for magic/mystical modals)
.purple-theme {
  --theme-primary: #2a1a2e;
  --theme-primary-light: #3f2a4a;
  --theme-primary-dark: #1a0d1a;
  --theme-secondary: #4a2f5f;
  --theme-accent: #9370db;
  --theme-background: linear-gradient(135deg, #2a1a2e, #3f2a4a);
  --theme-background-light: rgba(147, 112, 219, 0.1);
  --theme-background-medium: rgba(147, 112, 219, 0.2);
  --theme-text-primary: #ffffff;
  --theme-text-secondary: #d8b8f0;
  --theme-text-accent: #ffd700;
  --theme-border: rgba(147, 112, 219, 0.3);
  --theme-border-light: rgba(147, 112, 219, 0.5);
  --theme-shadow: rgba(0, 0, 0, 0.4);
  --theme-glow: rgba(147, 112, 219, 0.4);
}

// Utility classes for themed elements
.themed {
  // Basic theming for containers
  background: var(--theme-background);
  color: var(--theme-text-primary);
  border-color: var(--theme-border);
  
  // Themed borders
  &.themed-border {
    border: 2px solid var(--theme-border);
  }
  
  &.themed-border-light {
    border: 1px solid var(--theme-border-light);
  }
  
  &.themed-border-thick {
    border: 4px solid var(--theme-border);
  }
  
  // Themed backgrounds
  &.themed-bg-light {
    background: var(--theme-background-light);
  }
  
  &.themed-bg-medium {
    background: var(--theme-background-medium);
  }
  
  // Themed text
  &.themed-text-secondary {
    color: var(--theme-text-secondary);
  }
  
  &.themed-text-accent {
    color: var(--theme-text-accent);
  }
  
  // Themed shadows
  &.themed-shadow {
    box-shadow: 0 4px 8px var(--theme-shadow);
  }
  
  &.themed-glow {
    box-shadow: 0 0 10px var(--theme-glow);
  }
}

// Auto-apply theming to child elements with specific classes
.blue-theme,
.green-theme,
.red-theme,
.purple-theme {
  // Auto-theme common UI elements
  .modal-container {
    background: var(--theme-background);
    border-color: var(--theme-border);
    color: var(--theme-text-primary);
  }
  
  .modal-header {
    background: var(--theme-primary-light);
    border-bottom-color: var(--theme-border);
    color: var(--theme-text-primary);
  }
  
  .section-header {
    background: var(--theme-primary-light);
    color: var(--theme-text-primary);
  }
  
  .themed-button {
    background: var(--theme-accent);
    border-color: var(--theme-border-light);
    color: var(--theme-text-primary);
    
    &:hover:not(:disabled) {
      background: var(--theme-primary-light);
      box-shadow: 0 0 10px var(--theme-glow);
    }
    
    &.can-upgrade,
    &.can-purchase {
      border-color: var(--theme-text-accent);
      box-shadow: 0 0 10px var(--theme-glow);
    }
  }
  
  .themed-track {
    background: var(--theme-primary-dark);
    border-color: var(--theme-border);
  }
  
  .themed-progress {
    background: var(--theme-accent);
  }
  
  .themed-circle {
    border-color: var(--theme-border);
    
    &.filled {
      background: var(--theme-accent);
      border-color: var(--theme-text-primary);
    }
  }
  
  .themed-container {
    background: var(--theme-background-light);
    border-color: var(--theme-border);
  }
  
  .themed-section {
    background: var(--theme-background-medium);
    border-color: var(--theme-border);
  }
}
