

.game-toast {
  position: relative;
  background: rgba(0, 0, 0, 0.9);
  border: 2px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  min-width: 300px;
  max-width: 500px;
  animation: slideIn 0.3s ease-out;
  
  .toast-header {
    margin-bottom: 12px;
    
    .toast-title {
      font-weight: bold;
      font-size: 1.1em;
      color: white;
      margin-bottom: 4px;
    }
    
    .toast-subtitle {
      font-size: 0.9em;
      color: #ccc;
    }
  }
  
  .tag-list {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    
    .flavor-tag-pill {
      transition: all 0.3s ease;
      
      &.pulse {
        animation: pulse 1s ease-in-out infinite;
      }
      
      &.collected {
        opacity: 1;
      }
      
      &.uncollected {
        opacity: 0.6;
      }
      
      &.glow {
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
      }
    }
  }
  
  // Collection-specific styling
  &.collection-toast {
    border-color: #4CAF50;
    
    &.complete {
      border-color: #FFD700;
      background: rgba(121, 114, 74, 0.986);
      
      .toast-title {
        color: #0010a5;
      }
    }
  }
  
  // Quest-specific styling  
  &.quest-toast {
    border-color: #ff69b4;
    
    .toast-title {
      color: #ff69b4;
    }
    
    &.complete {
      border-color: #FFD700;
      background: rgba(255, 105, 180, 0.1);
    }
  }
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}
