.reward-modal-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #00000099;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    z-index: 999;
    .reward-modal-body 
        {
        width: 80%;
        height: 100%;
        margin: 30px;
        display: flex;
        flex-direction: column;
        background: #444444;
        border-radius: 15px;
        justify-content: flex-start;
        align-items: center;
        border: 4px solid #888888;
        justify-content: space-between;
        .reward-modal-title {
            margin: 10px;
            font-size: 2.5em;
            .reward-modal-instructions {
                font-size: .5em;
                margin-top: 10px;
                text-align: center;
                color: #d4d4d4;
            }

        }
        .reward-panels {
            margin-top: 10px;
            display: flex;
            justify-content: space-between;
            margin-bottom: 20px;
            position: relative;
            width: 50%;
            height: 140px;
            .panel {
                flex: 1;
                text-align: center;
                padding: 4px;
                position: relative;
                transition: all 0.3s ease;
                
                &.active {
                    background-color: rgba(255, 255, 255, 0.1);
                    transform: scale(1.05);
                    border-radius: 15px;
                    border: 4px solid #ffffff44;
                }
                
                .panel-title {
                    font-size: 1.4em;
                    opacity: 0.8;
                }
                
                .panel-value {
                font-size: 1.2em;
                font-weight: bold;
                &.expected{
                    
                    font-size: 2.4em;
                    bottom: 4px;
                    }
                }
                
                .accept-button {
                position: absolute;
                bottom: 10px;
                left: 50%;
                transform: translateX(-50%);
                padding: 5px 20px;
                background: #9aab3c;
                border: 4px solid #677229;
               
                border-radius: 10px;
                cursor: pointer;
                font-weight: bold;
                color: #eaeaea;
                transition: all 0.3s ease;
                &:hover {
                    transform: translateX(-50%) scale(1.05);
                }
                }
            }
        }
        
        .reward-items-list {
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
            gap: 10px;
            width: 100%;
            padding: 20px;
            box-sizing: border-box;
            .reward-item {
                padding: 10px;
                border: 4px solid rgba(255, 255, 255, 0.2);
                background-color: rgba(255, 255, 255, 0.1);
                border-radius: 8px;
                cursor: pointer;
                transition: all 0.2s ease;
                
                &.selected {
                background-color: rgba(76, 175, 80, 0.2);
                border-color: #4CAF50;
                }
                
                .item-header {
                display: flex;
                justify-content: space-between;
                margin-bottom: 5px;
                
                    .item-cost {
                        color: #4CAF50;
                        font-weight: bold;
                    }
                }
                
                .item-description {
                font-size: 0.9em;
                opacity: 0.8;
                }
            }
        }
    }
  }

  .reward-modal-heroes {
           
    display: flex;
    flex-direction: row;
    bottom: 0;
    
    align-items: flex-end;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;

    flex-wrap: wrap;
    left: 0;
    gap: 10px;
    margin-bottom: 10px;
 
 
    .reward-modal-hero-row {
       
        display: flex;
        flex-direction: column;
        
        justify-content: space-between;
        
        align-items: center;
      

       
        .reward-modal-hero-container {
            display: flex;
            flex-direction: row;
            border: 4px solid rgba(33, 54, 33, 0.667);
            background-color: #00000044;
            height: 92px;
            border-radius: 10px;
        }

        .reward-modal-hero {
            background-color: #ffffff;
            display: flex; 
            flex-direction: column;
            font-size: 1.5em;
            font-weight: bold;
            color: black;
            height: 100%;
            width: 140px;
            border-radius: 10px;
            box-sizing: border-box;
            align-items: center;
            border: 4px solid #00000099;
            position: relative;
           
            .combat-character-icon {
                width: 50px;
                height: 50px;
                border-radius: 5px;;
               
                margin-bottom: 4px;
                bottom: 4px;
                box-sizing: border-box;
                
                .cimage {
                    position: relative;
                    width: 100%;
                    height: 100%;
                }   
            }

            .reward-modal-hero-injury {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(71, 19, 18, 0.6);
                z-index: 2;
                &.dead {    
                    background-color: black;
                }
            }


        }
        .reward-modal-hero-attributes{
            margin-left: 4px;
            height: 100%;
            width: stretch;
            min-width:150px;
            display: flex;
            align-items: center;;
            .attribute-display-container{
                left: 0;
               
                align-items: flex-start;
                display: flex;
                flex-direction: column;



                .attribute-display {
                    font-size: 1.4em;
                    display: flex;
                    flex-direction: row;
                    .attribute-title {
                       display: none;
                    }
                    .attribute-container {
                        position: relative;
                        flex-direction: row;
                        display: flex;
                        font-size: .95em;
                        letter-spacing: -6px;
                        flex-wrap:wrap;
                      .attributes-lost {
                        opacity: 0.2;   
                      }
                    
                    }
                }
            }  
        }
    }
}