:root {
	--notebook-bg-color: #ececec;
	--notebook-text-color: #716f6d;
	--notebook-header-font-size: 2.8rem;
	--notebook-header-font-size-small: 1.9rem;
	--notebook-header-font-size-xsmall: 1.5rem;

	--notebook-bg-color-player: #6985af;
	--notebook-bg-color-player-highlight: #a2cce9;
	--notebook-bg-color-player-selected: #8ac3e9;
	--notebook-bg-color-player-light: #c5d2e7;

	--notebook-bg-color-character: #6ca065; //a3ca9e
	--notebook-bg-color-character-light: #d9f0db;
	--notebook-bg-color-character-highlight: #c4e9bf;
	--notebook-bg-color-character-selected: #abe6a3;
	--notebook-bg-color-player-character: #6ca065; //6ca065

	--notebook-bg-color-stats: #859bca;
	--notebook-bg-color-stats-light: #a2b8e6;
	--notebook-bg-color-player-name: #7f95c0;
	--notebook-bg-color-stress: #b088bb;
	--notebook-bg-color-preferences-light: #cfcfcf;
	--notebook-bg-color-preferences: #989898;
	--notebook-bg-color-likes: #8db597;
	--notebook-bg-color-hates: #a87b7b;
	--notebook-bg-color-traits: #a88a6e;
	--notebook-bg-color-traits-light: #e0d7ce;
	--notebook-bg-color-trait: #5f5f5f;

	--notebook-bg-color-encounter: #888f70;
	--notebook-bg-color-encounter-elements: #8aa68e;
	--notebook-bg-color-encounter-light: #b5c4b5;
	--notebook-bg-color-location: #8175b0;
	--notebook-text-color-player: #73a06b;
	--notebook-text-color-encounter: #a8a63b;
	--notebook-text-color-location: #8175b0;
	--notebook-text-color-character: #e24646;

	--notebook-bg-color-encounter-desc: #9187a0;

	--notebook-bg-color-location-info: #8b7ba1;
	--notebook-bg-color-location-details: #9c8bb2;
	--notebook-bg-color-location-features: #a4937a;
	--notebook-bg-color-location-tags: #a3824f;
	--notebook-bg-color-location-light: #e2dae8;

	--notebook-bg-color-group: #ecbd92;

	--notebook-color-conflicted-main: #7e7e7e;
	--notebook-color-like-main: #3d8b35;
	--notebook-color-hate-main: #c33e3e;
	--notebook-bg-color-tab: #555555;

	--notebook-tab-selected-border: #ffffff;

	--notebook-bg-color-character-info: #6885c5;
	--notebook-bg-color-character-health: #b7b7b7;
	--notebook-bg-color-character-abilities: #aa6e3d;
	--notebook-bg-color-character-backstory: #9f8dbd;
	--notebook-bg-color-character-ability-details: #858585;
	--notebook-bg-color-character-ability-details-light: #b9b9b9;

	--notebook-bg-color-character-ability-details-class: #8e93c4;
	--notebook-bg-color-character-ability-details-race: #cbaf97;
}

.notebook-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	color: var(--notebook-text-color);
	//font-style: italic;
	z-index: 9999;
}

.notebook-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	position: relative;
	z-index: 9999;
}

.notebook-content {
	background: white;
	width: 65%;
	height: 70%;
	padding: 0px;
	overflow-y: scroll;
	border: 1px solid #8b8b8b;
	// background-image: linear-gradient(to bottom, var(--notebook-bg-color) 30px, #a89e9e 31px);
	background-size: 100% 31px;
	//position: absolute;
	-ms-overflow-style: none; /* Internet Explorer 10+ */
	scrollbar-width: none; /* Firefox */
	border-radius: 15px;
	border: 12px solid blue;
	background-color: #e4e4e4;
	&::-webkit-scrollbar {
		display: none; /* Safari and Chrome */
	}
	&.o1 {
		margin-left: -30px;
		margin-top: -18px;
	}
	&.o2 {
		margin-top: -9px;
		margin-left: -15px;
		overflow: visible;
	}
	&.player {
		border-color: var(--notebook-bg-color-player);
		background-color: var(--notebook-bg-color-player-light);
	}

	&.encounter {
		border-color: var(--notebook-bg-color-encounter);
	}

	&.location {
		border-color: var(--notebook-bg-color-location);
	}
	&.character {
		border-color: var(--notebook-bg-color-character);
		background-color: var(--notebook-bg-color-character-light);
	}
	&.group {
		border-color: var(--notebook-bg-color-group);
	}

	.notebook-header {
		position: relative;
		&.player {
			background: var(--notebook-bg-color-player);
		}

		&.encounter {
			background: var(--notebook-bg-color-encounter);
		}

		&.location {
			background: var(--notebook-bg-color-location);
		}
		&.character {
			background: var(--notebook-bg-color-character);
		}
		&.group {
			background: var(--notebook-bg-color-group);
		}
		.notebook-header-title {
			color: var(--notebook-bg-color);
			position: absolute; // Use absolute positioning
			top: 40%; // Center vertically
			left: 45px;
			transform: translate(0%, -50%); // Adjust position to be centered
			font-size: 5rem;
			pointer-events: none;
			font-style: normal;
			font-weight: bold;
		}
	}
}

.notebook-tabs {
	//position: relative;
	//  top: -52px;
	// left: 7px;
	display: flex;
	//background-color: var(--notebook-bg-color-tab);
	cursor: pointer;
	// border: 4px solid var(--notebook-bg-color-tab);
	justify-content: flex-start;
	width: 65%;
	border-radius: 14px;
	gap: 8px;
	&.o2 {
		overflow: visible;
	}
	.notebook-tab {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 20px;
		background: var(--notebook-bg-color);
		border: 8px solid #3a3a3a;
		margin-right: 2px;
		z-index: 9999;
		box-sizing: border-box;
		// border-top-left-radius: 4px;
		//border-top-right-radius: 4px;
		border-radius: 14px;
		margin-top: 6px;
		// margin-bottom: -15px;
		//  border-bottom: none;
		transform: translate(0px, -4px);
		font-size: 25px;
		color: #424242;
		&.current {
			//margin-top: 10px;
			//border-bottom-left-radius: 4px;
			//border-bottom-right-radius: 4px;
			font-size: 30px;
			left: 0;
			// transform: translate(0px, 0px);
			//    border-bottom: none;
			font-weight: bold;
			color: var(--notebook-tab-selected-border);
			border-color: var(--notebook-tab-selected-border);
			border-width: 8px;
		}
		&.player {
			background: var(--notebook-bg-color-player);
		}
		&.encounter {
			background: var(--notebook-bg-color-encounter);
		}
		&.location {
			background: var(--notebook-bg-color-location);
		}
		&.character {
			background: var(--notebook-bg-color-character);
		}
		&.group {
			background: var(--notebook-bg-color-group);
		}
		.notebook-tab-title {
			//  font-size: 20px;
			font-weight: bold;
		}
	}
}

.notebook-header {
	height: 100px;
	width: 100%;
	//lue top: -10px;
	background-color: var(--notebook-bg-color);
	display: flex;
	align-items: flex-end;
	.title-bar {
		display: flex;
		width: 100%;
		.title-text,
		.features-text {
			flex: 1;
			text-align: left;
			font-size: xx-large;
			padding: 0 16px;
			position: relative;
			bottom: -10px;
		}
		.features-text {
			text-align: left; /* Ensure text is left-justified */
		}
	}
}

// -------------------------------------------------------------------------------------------------------------------
// ENCOUNTER
// -------------------------------------------------------------------------------------------------------------------
.notebook-encounter-container {
	display: flex;
	align-items: flex-end;
	width: 100%;
	position: relative;
	align-items: flex-start;
	gap: 10px;

	.text-pill {
		padding-bottom: 4px;
		margin-left: 0px;
	}

	.notebook-encounter-contents {
		flex: 1;
		text-align: left;
		.notebook-icon-container {
			width: 100%;
			height: auto;
			margin-bottom: 0px;
			.cimage {
				width: 100%;
				height: 100%;
				margin-bottom: 0px;
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
				max-height: 500px;
				object-fit: cover;
				object-position: center;
				transition: transform 0.3s ease;
			}
		}
	}
	.cbox {
		.cbox {
			margin: 0px;
		}
		background-color: white;
		border-color: var(--notebook-bg-color-encounter);
		.cbox-text {
			background-color: var(--notebook-bg-color-encounter);
			font-weight: bold;
			color: black;
		}
		.cbox-children {
			padding: 10px;
			font-size: 1.5em;
			line-height: 0.8; /* Adjust line-height to remove extra space */
		}
		.encounter-location {
			font-weight: bold;
			font-size: 1.2em;
		}
		&.encounter-info {
			padding: 0px;
			margin-top: 10px;
			.cbox-children {
				padding: 0px;
			}
		}
		&.details {
			margin-top: 10px;
			max-width: 65%;
			background-color: var(--notebook-bg-color-encounter-light);
			.cbox {
				font-size: 0.25em;
				.cbox-text {
					font-size: 1.5em;
					padding-bottom: 10px;
				}
				&.location {
					font-size: 0.25em;
					margin-bottom: 10px;
					border-color: var(--notebook-bg-color-encounter-desc);
					.cbox-text {
						background-color: var(--notebook-bg-color-encounter-desc);
						font-size: 1.5em;
						margin-bottom: 10px;
					}
				}
			}
		}
		&.elements {
			margin-top: 10px;
			border-color: var(--notebook-bg-color-encounter-elements);
			.cbox-text {
				background-color: var(--notebook-bg-color-encounter-elements);
			}
		}
	}
}

// -------------------------------------------------------------------------------------------------------------------
// LOCATION
// -------------------------------------------------------------------------------------------------------------------

.notebook-location-container {
	display: flex;
	width: 100%;
	gap: 10px;
	padding: 10px;

	.notebook-location-contents {
		&.left {
			width: 35%;
			max-width: 35%;
		}
		flex: 1;

		.notebook-icon-container {
			height: auto;
			margin-bottom: 0px;

			.cimage {
				padding-bottom: 0px;
				width: 100%;
				height: 100%;
				margin-bottom: 0px;
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
				max-height: 400px;
				object-fit: cover;
				object-position: center;
				transition: transform 0.3s ease;
			}
		}
	}

	.cbox {
		background-color: white;
		margin-bottom: 10px;
		font-size: 1em;
		.cbox {
			margin: 0px;
		}

		&.location-info {
			border-color: var(--notebook-bg-color-location-info);
			height: auto;
			padding-bottom: 0px;

			.cbox-text {
				background-color: var(--notebook-bg-color-location-info);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}

			.cbox-children {
				padding: 0;
			}
		}

		&.location-details {
			height: auto;
			width: auto;
			margin-right: 18px;
			border-color: var(--notebook-bg-color-location-details);
			background-color: var(--notebook-bg-color-location-light);
			font-size: 1em;
			.cbox-text {
				background-color: var(--notebook-bg-color-location-details);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}

			.cbox-children {
				padding: 14px;
				font-size: 1em;
				line-height: 1.4;
			}
		}

		&.description {
			border-color: var(--notebook-bg-color-location-details);
			background-color: var(--notebook-bg-color-location-light);

			.cbox-text {
				background-color: var(--notebook-bg-color-location-details);
			}
		}

		&.location-features {
			border-color: var(--notebook-bg-color-location-features);
			margin-top: 12px;
			.cbox-text {
				background-color: var(--notebook-bg-color-location-features);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}

			.cbox-children {
				padding: 10px;

				.elements-features-list-container {
					display: flex;
					flex-wrap: wrap;
					gap: 6px;
				
					.text-pill {
						background-color: white;
						border: 2px solid var(--notebook-bg-color-location-features);
						color: var(--notebook-text-color);
						padding: 4px 12px;
						font-size: 1.2em;

						&.type-location {
							background-color: var(--notebook-bg-color-location-tags);
							color: white;
						}
					}
				}
			}
		}
	}
}

.elements-features-list-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap; // Allow items to wrap to the next line if needed
		align-items: center;
					justify-content: center;
	gap: 0px; // Adjust the gap between items as needed
	.elements-features-list {
		white-space: nowrap;
	}
}

.pill-button {
	position: absolute;
	top: 10px;
	right: 10px;
	border-radius: 100%;
	color: var(--info-display-pencil);
	border: 3px solid var(--info-display-pencil);
	background: var(--notebook-bg-color);
	width: 47px;
	font-size: 30px;
	font-style: bold;
	font-weight: 900;
	transition: 0.2s;
	cursor: pointer;
	&:hover {
		color: var(--info-display-pen);
		border: 3px solid var(--info-display-pen);
		transform: scale(1.1);
	}
	&.remove {
		border-radius: 15px;
		white-space: nowrap;
	}
}

.notebook-text {
	left: 10px;
	top: 1px;
	position: relative;
	font-size: 20px;
	margin-right: 40px;
	line-height: 30px;
	color: var(--notebook-text-color);
	&.player {
		top: 12px;
	}
	&.large {
		font-size: 34px;
	}
	&.encounter-description,
	&.location-description {
		font-size: 24px;
		font-weight: bold;
		line-height: 1.4;
	}
}

// -------------------------------------------------------------------------------------------------------------------
// PLAYER
// -------------------------------------------------------------------------------------------------------------------

.notebook-player-container {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	gap: 10px;

	.player-notebook-upper {
		display: flex;
		flex-direction: row;
		width: 100%;
	}

	.player-notebook-left-content {
		display: flex;
		flex: 1 1 66.66%;
		width: 100%;
		flex-direction: column;
		.notebook-player-topbar {
			display: flex;
			flex-direction: row;
			border: 4px solid var(--notebook-bg-color-player-name);
			border-radius: 15px;
			background-color: white;
			.notebook-player-icon {
				width: 100px;
				min-width: 100px;
				height: 100px;
				position: relative;
				top: 10px;
				left: 10px;
				margin-bottom: 10px;
				.cimage {
					width: 100%;
					border-radius: 100%;
				}
			}
			.notebook-player-name {
				font-size: 5em;

				color: black;
				font-weight: bold;
				border-bottom-left-radius: 15px;
				padding-left: 20px;
				margin-left: 40px;
				background-color: var(--notebook-bg-color-player-name);
				width: 100%;
			}
		}

		.player-notebook-mid-content {
			display: flex;
			flex-direction: row;
			width: 100%;
			flex: 0 0 50%;
			margin-top: 20px;
			gap: 8px;
			.player-notebook-mid-left {
				//stats
				width: 100%;
				text-align: center;
				.character-stress {
					font-size: 2em;
				}
			}

			.player-notebook-mid-right {
				flex: 1 0 50%;

				width: 100%;
				flex-wrap: wrap;
				display: flex;
				flex-direction: row;

				.player-preferences {
					flex-wrap: wrap;
					display: flex;
					flex-direction: row;
					gap: 0px;
					// height: 0px;
					width: 100%;
					.player-preference-title-small {
						font-size: 2em;
						text-align: center;
						width: 100%;
					}
				}
			}
		}
	}

	.player-notebook-bottom {
		width: 100%;
		height: 100%;
		margin-top: 20px;
		margin-right: 10px;
		display: flex;
		.player-notebook-bottom-title {
			font-size: 2em;
			text-align: center;
		}
		.player-notebook-traits {
		}
	}

	.player-notebook-right-content {
		display: flex;
		flex: 1 1 33.33%; /* Ensure it takes at least 1/3rd of the parent's width */
		max-width: 100%;

		box-sizing: border-box; /* Include padding and border in the element's total width */

		.player-character-title-container {
			background-color: var(--notebook-bg-color-character);
			padding-bottom: 14px;
		}
		.player-character-container {
			border: 6px solid var(--notebook-bg-color-player-character);
			border-radius: 15px;
			margin: 4px;
			display: flex;
			flex-direction: row;
			transition: 0.3s;
			width: 100%;
			background-color: white;
			&:hover {
				transform: scale(1.01);
				opacity: 0.9;
				box-shadow: 0 0 10px rgba(255, 255, 0, 0.5); /* Subtle glowing effect */
				transition: opacity 0.3s ease, box-shadow 0.3s ease; /* Smooth transition */
			}
			cursor: pointer;
			.player-character-hero-image {
				.challenge-character-icon {
					width: 100px;
					height: 100px;
					.cimage {
						width: 100%;
						border-radius: 100%;
					}
				}
			}
			.player-character-text-container {
				display: flex;
				flex-direction: column;
				width: 100%;

				.player-character-name-container {
					width: 100%;
					background-color: var(--notebook-bg-color-player-character);
					border-bottom-left-radius: 15px;
					height: 40px;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					.player-character-name {
						font-size: 0.8em;
						text-align: center;
						width: 100%;
						font-weight: bold;
						margin-bottom: 7px;
						color: black;
					}
				}
				.player-character-info {
					text-transform: uppercase;
					font-weight: 600;
					font-size: 0.8em;
				}

				.player-character-info {
					font-size: 0.5em;
					width: 100%;
				}
			}
		}
	}

	.player-preference {
		font-size: 1.5em;
		font-weight: 600;
	}

	.cbox {
		background-color: white;
		// nested cbox needs this
		.cbox {
			margin: 0px;
			background-color: white;
		}

		border-color: red;
		.cbox-text {
			background-color: red;
		}

		&.characters {
			border-color: var(--notebook-bg-color-character);
			background-color: var(--notebook-bg-color-character-light);

			.cbox-text {
				background-color: var(--notebook-bg-color-character);
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				flex-direction: column;
				padding: 10px;
				margin-right: 16px;
			}
		}

		&.stats {
			border-color: var(--notebook-bg-color-stats);
			background-color: var(--notebook-bg-color-stats-light);
			font-size: 1em;
			.cbox-text {
				background-color: var(--notebook-bg-color-stats);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				padding: 14px;
				padding-top: 4px;
				padding-bottom: 4px;
			}
		}

		&.stress {
			border-color: var(--notebook-bg-color-stress);
			font-size: 1em;
			margin: 0px;
			margin-top: 8px;
			.cbox-text {
				background-color: var(--notebook-bg-color-stress);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				padding: 4px;

				font-size: 0.9em;
			}
		}

		&.preferences {
			border-color: var(--notebook-bg-color-preferences);
			font-size: 1em;
			background-color: var(--notebook-bg-color-preferences-light);
			.cbox-text {
				background-color: var(--notebook-bg-color-preferences);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				padding: 4px;
				font-size: 0.9em;
			}
		}

		&.hates {
			border-color: var(--notebook-bg-color-hates);
			font-size: 1em;
			margin-top: 8px;
			.cbox-text {
				background-color: var(--notebook-bg-color-hates);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				padding: 4px;
				font-size: 0.9em;
				padding-bottom: 8px;
			}
		}
		&.likes {
			border-color: var(--notebook-bg-color-likes);
			font-size: 1em;
			margin-top: 8px;
			.cbox-text {
				background-color: var(--notebook-bg-color-likes);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				padding: 4px;
				padding-bottom: 8px;
				font-size: 0.9em;
			}
		}
		&.traits {
			border-color: var(--notebook-bg-color-traits);
			font-size: 1.1em;
			font-weight: bold;

			.cbox-text {
				background-color: var(--notebook-bg-color-traits);
				font-size: 2em;
				font-weight: bold;
				color: black;
			}
			.cbox-children {
				justify-content: center;
				align-items: center;
				background-color: var(--notebook-bg-color-traits-light);
				.no-traits {
					padding: 4px;
					font-size: 2em;
				}
				.cbox {
					.cbox-children {
						background-color: white;
					}
				}
			}
		}
		&.trait {
			border-color: var(--notebook-bg-color-trait);

			max-width: 200px;
			margin: 4px;
			font-size: 1.1em;
			.cbox-text {
				background-color: var(--notebook-bg-color-trait);
				font-size: 1.2em;
				font-weight: bold;
				color: rgb(255, 255, 255);
			}
			.cbox-children {
				padding: 4px;
				font-size: 1em;
			}
		}
	}
}

