// Conversation Section Styles
@import '../../styles/themes.scss';

.conversation-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 6px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(4px);
  min-height: 0;

  .section-header {
    padding: 16px 20px;
    
    h3 {
      color: #ffffff;
      margin: 0;
      font-size: 30px;
      font-weight: 600;
    }
  }

  .conversation-content {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    min-height: 0;
    
    .conversation-display {
      &.chat-expanded-messages {
        .chat-message-container {
          margin-bottom: 20px;
          opacity: 0;
          transform: translateY(20px);
          animation: messageSlideIn 0.5s ease forwards;
          
          &.dm {
            .chat-message-display {
              .chat-message-speaker-container {
                .chat-message-speaker {
                  color: #ffd700;
                  font-weight: bold;
                }
              }
            }
          }
          
          &.player {
            .chat-message-display {
              .chat-message-speaker-container {
                .chat-message-speaker {
                  color: #4ecdc4;
                  font-weight: bold;
                }
              }
            }
          }
          
          &.character {
            .chat-message-display {
              .chat-message-speaker-container {
                .chat-message-speaker {
                  color: #ff6b6b;
                  font-weight: bold;
                }
              }
            }
          }
          
          .chat-message-display {
            display: flex;
            align-items: flex-start;
            gap: 12px;
            
            .chat-message-speaker-container {
              display: flex;
              flex-direction: column;
              align-items: center;
              gap: 8px;
              min-width: 80px;
              
              .chat-message-speaker {
                font-size: 14px;
                text-align: center;
                line-height: 1.2;
              }
              
              .chat-message-icon {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                overflow: hidden;
                border: 2px solid rgba(255, 255, 255, 0.3);
                
                .cimage {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }
              }
            }
            
            .chat-messager-content {
              flex: 1;
              
              .chat-message-text {
                background: rgba(255, 255, 255, 0.1);
                border: 1px solid rgba(255, 255, 255, 0.2);
                border-radius: 12px;
                padding: 12px 16px;
                
                .chat-message-words {
                  color: #838383;
                  font-size: 23px;
                  font-weight: 500;
                  line-height: 1.5;
                  
                  .chat-word {
                    opacity: 0;
                    animation: wordFadeIn 0.1s ease forwards;
                    
                    &.visible {
                      opacity: 1;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    
    .no-conversation {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      color: rgba(255, 255, 255, 0.6);
      font-style: italic;
      text-align: center;
      
      p {
        margin: 0;
        font-size: 16px;
      }
    }
  }
}

// Animations
@keyframes messageSlideIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes wordFadeIn {
  to {
    opacity: 1;
  }
}