@keyframes pulsate {
	0% {
		transform: scale(1);
		box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
		background-color: rgba(229, 0, 0, 0.5);
		border-width: 1px;
	}
	50% {
		transform: scale(3.3);
		box-shadow: 0 0 30px rgb(212, 0, 0);
		border-width: 100px;
		background-color: rgba(229, 0, 0, 0.5);
	}
	100% {
		transform: scale(1);
		box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
		border-width: 1px;
		background-color: rgba(15, 171, 18, 0.5);
	}
}
@keyframes gradientAnimation {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes fadeOut {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

