.fill-bar {
	.fill-bar-filled-button {
		width: 100%;
		font-weight: bold;
		cursor: pointer;
		background-color: black;
		color: white;
		transition: 1s;

		&:hover {
			background-color: white;
			color: black;
			transform: scale(1.1);
		}
	}

	.fill-bar-container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 20px;
		border-radius: 10px;
		overflow: hidden;
		margin: 2px 0;
		position: relative;
		.fill-bar-label-over {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 2;
			pointer-events: none;
			font-weight: bold;
		}
	}

	.fill-bar-segment {
		flex: 1;
		height: 100%;
		border-right: 1px solid #fff; // Segment lines
		transition: background-color 0.3s;
		&.dark {
			border-right: 1px solid #333; // Segment lines
		}

		&:last-child {
			border-right: none; // Remove border from the last segment
		}
		&.theme- {
			background-color: var(--town-display-pencil-light);
			&.filled,
			&.dark {
				background-color: var(--town-display-pencil);
			}
		}

		// Glow animation for when fill-bar segments increase
		&.fill-glow-animation {
			filter: brightness(1.4) contrast(1.2);
			transition: filter 0.8s ease-in-out;
			
			// Theme-specific glow effects
			&.bg-theme-combat {
				filter: brightness(1.5) contrast(1.3) saturate(1.4);
			}
			&.bg-theme-magic {
				filter: brightness(1.5) contrast(1.3) saturate(1.4);
			}
			&.bg-theme-gear {
				filter: brightness(1.5) contrast(1.3) saturate(1.4);
			}
		}
	}

	.fill-bar-label {
		font-size: 1.24em;
		font-weight: bold;
		//   text-transform: uppercase;
		top: -20px;
	}
}

.number-die {
	display: flex;
