.game-encounter-trap {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 16px;
}

.trap-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 16px;
}

.trap-grid {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 16px;
  flex: 1;
}

.grid-labels {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
  justify-content: center;
}

.result-label {
  padding: 4px 12px;
  border-radius: 4px;
  font-weight: bold;
  font-size: 12px;
  text-transform: uppercase;
}

.result-label.success {
  background: rgba(39, 174, 96, 0.3);
  color: #27ae60;
}

.result-label.survived {
  background: rgba(241, 196, 15, 0.3);
  color: #f1c40f;
}

.result-label.injured {
  background: rgba(230, 126, 34, 0.3);
  color: #e67e22;
}

.result-label.death {
  background: rgba(231, 76, 60, 0.3);
  color: #e74c3c;
}

.grid-content {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.grid-row {
  display: flex;
  gap: 2px;
  align-items: center;
}

.roll-number {
  width: 30px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  background: #4a4a5a;
  border-radius: 4px;
  margin-right: 8px;
}

.roll-number-header {
  width: 30px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  background: #3a3a4a;
  border-radius: 4px;
  margin-right: 8px;
  font-size: 18px;
}

.grid-cell {
  position: relative;
  width: 80px;
  height: 40px;
  border: 2px solid #333;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.grid-cell.success {
  background: linear-gradient(135deg, rgba(39, 174, 96, 0.2), rgba(39, 174, 96, 0.1));
  border-color: #27ae60;
}

.grid-cell.survived {
  background: linear-gradient(135deg, rgba(241, 196, 15, 0.2), rgba(241, 196, 15, 0.1));
  border-color: #f1c40f;
}

.grid-cell.injured {
  background: linear-gradient(135deg, rgba(230, 126, 34, 0.2), rgba(230, 126, 34, 0.1));
  border-color: #e67e22;
}

.grid-cell.death {
  background: linear-gradient(135deg, rgba(231, 76, 60, 0.2), rgba(231, 76, 60, 0.1));
  border-color: #e74c3c;
}

.grid-cell.highlighted {
  border-color: #ffa500 !important;
  box-shadow: 0 0 12px rgba(255, 165, 0, 0.8) !important;
  transform: scale(1.05) !important;
  animation: pulse 0.5s ease-in-out infinite alternate;
}

@keyframes pulse {
  0% { opacity: 1; }
  100% { opacity: 0.7; }
}

.character-marker {
  font-size: 24px;
  animation: bounce 0.5s ease-in-out;
}

@keyframes bounce {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.dm-reward-marker {
  position: absolute;
  top: 2px;
  right: 2px;
  background: #4a90e2;
  color: white;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: bold;
}

.character-headers {
  display: flex;
  gap: 2px;
  align-items: center;
  margin-top: 8px;
  border-top: 2px solid #666;
  padding-top: 8px;
}

.character-header {
  width: 80px;
  height: 60px;
  background: #2a2a3a;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4px;
}

.character-name {
  font-weight: bold;
  font-size: 12px;
  color: white;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

.character-player {
  font-size: 10px;
  color: #ccc;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

.trap-controls {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 16px;
  min-height: 200px;
}

.difficulty-adjustment h3,
.disarm-interface h3 {
  color: white;
  margin-bottom: 16px;
  text-align: center;
}

.risk-stats {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-bottom: 16px;
}

.risk-stat {
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  font-weight: bold;
  color: white;
}

.boredom-risks {
  margin-bottom: 16px;
}

.boredom-risks h4 {
  color: white;
  margin-bottom: 8px;
  text-align: center;
}

.boredom-risk {
  padding: 4px 8px;
  background: rgba(255, 165, 0, 0.2);
  border-radius: 4px;
  margin-bottom: 4px;
  color: #ffa500;
  font-size: 14px;
}

.introduce-trap-button,
.react-to-trap-button,
.end-trap-button {
  width: 100%;
  padding: 12px;
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
  margin-top: 16px;
}

.introduce-trap-button:hover,
.react-to-trap-button:hover,
.end-trap-button:hover {
  background: #357abd;
}

.end-trap-button {
  background: #e74c3c;
}

.end-trap-button:hover {
  background: #c0392b;
}

.character-disarm-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  margin-bottom: 16px;
}

.character-disarm-card {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid #666;
  border-radius: 8px;
  padding: 12px;
  transition: all 0.2s ease;
}

.character-disarm-card:hover:not(.disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.character-disarm-card.disabled {
  opacity: 0.5;
  background: rgba(100, 100, 100, 0.1);
}

.character-info {
  margin-bottom: 8px;
}

.character-info .character-name {
  font-size: 14px;
  font-weight: bold;
  color: white;
}

.character-info .character-player {
  font-size: 12px;
  color: #ccc;
}

.assigned-tag {
  margin-bottom: 8px;
  font-size: 12px !important;
  padding: 2px 6px !important;
}

.disarm-chance {
  font-weight: bold;
  color: white;
  margin-bottom: 8px;
  text-align: center;
}

.disarm-button {
  width: 100%;
  padding: 8px;
  background: #27ae60;
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.disarm-button:hover:not(:disabled) {
  background: #219a52;
}

.disarm-button:disabled {
  background: #666;
  cursor: not-allowed;
}

/* Modal styles - reusing from puzzle component */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-container {
  background: #2a2a3a;
  border-radius: 12px;
  padding: 24px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.close-button {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.modal-description {
  color: #ccc;
  margin-bottom: 16px;
  line-height: 1.4;
}

.trap-results {
  margin-bottom: 16px;
}

.effect-display {
  margin-bottom: 12px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 6px;
}

.effect-target {
  font-weight: bold;
  color: #4a90e2;
  margin-bottom: 4px;
}

.effect-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.effect {
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 14px;
}

.effect.player-exp {
  background: rgba(52, 152, 219, 0.2);
  color: #3498db;
}

.effect.character-tools {
  background: rgba(230, 126, 34, 0.2);
  color: #e67e22;
}

.effect.location-tag {
  background: rgba(155, 89, 182, 0.2);
  color: #9b59b6;
}

.effect.tension {
  background: rgba(231, 76, 60, 0.2);
  color: #e74c3c;
}

.effect.character-injury {
  background: rgba(230, 126, 34, 0.2);
  color: #e67e22;
}

.effect.character-death {
  background: rgba(231, 76, 60, 0.2);
  color: #e74c3c;
}

.effect.dm-points {
  background: rgba(74, 144, 226, 0.2);
  color: #4a90e2;
}

.effect.player-attention {
  background: rgba(255, 0, 0, 0.2);
  color: #ff6b6b;
}

.modal-actions {
  display: flex;
  justify-content: center;
}

.modal-actions button {
  padding: 10px 24px;
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.modal-actions button:hover {
  background: #357abd;
}
