.nextencounter {
  width: 90%;
 max-width: unset;
 width: fit-content;
  
  overflow-y: auto;
  

  .nextencounter-content {
    .encounter-options {
      display: flex;
     flex-direction: row;
     justify-content: center;
      gap: 20px;
      margin-bottom: 25px;
      
      .encounter-option {
        cursor: pointer;
        transition: transform 0.2s ease;
        height: 100%;
        width: 650px;
        &:hover {
          transform: scale(1.05);
          
          .encounter-overlay {
            opacity: 1;
          }
        }
        
        .encounter-card {
          position: relative;
          background: linear-gradient(135deg, #3a3a4a, #4a4a5a);
          border: 6px solid #c5c5c9;
          border-radius: 16px;
          padding: 20px;
          height: 100%;
          display: flex;
          flex-direction: column;
          overflow: hidden;
         
          .encounter-image {
            width: 100%;
            height: 150px;
            border-radius: 8px;
            overflow: hidden;
            margin-bottom: 15px;
            border: 2px solid #6a6a7a;
            
            .cimage {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          
          .encounter-info {
            flex: 1;
            display: flex;
            flex-direction: column;
            gap: 10px;
            
            .encounter-name {
              font-size: 2em;
              color: #ffffff;
              margin: 0;
              font-weight: bold;
            }
            
            .encounter-description {
              color: #cccccc;
              line-height: 1.4;
              margin: 0;
              flex: 1;
              font-size: 1.2em;
              min-height: 150px;
              max-height: 150px;;
              
            }
            
            .encounter-location {
              color: #ffffff;
              font-size: 1.5em;
            }
            
            .encounter-tags {
              .tags-label {
                color: #ffffff;
                font-size: 1.2em;
                margin-bottom: 5px;
              }
               .encounter-medallion-outer {
                
                .text-pill{
                    top: -2px;
                }
            }
              
              .tags-list {
                display: flex;
                flex-wrap: wrap;
                gap: 5px;
              }
            }
          }
          
          .player-preferences-container {
            margin-top: 15px;
            padding-top: 15px;
            border-top: 1px solid #5a5a6a;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            gap: 12px;
            
            .player-preferences {
              &.preferred {
                .preferences-label {
                  color: #4CAF50;
                  font-size: 1.4em;
                  font-weight: bold;
                }
                .preferences-effect {
                  color: #4CAF50;
                  margin-bottom: 4px;
                }
                
                .player-icon {
                  border-color: #4CAF50;
                }
              }
              
              &.not-wanted {
                .preferences-label {
                  color: #f44336;
                   font-size: 1.4em;
                  font-weight: bold;
                }
                
                .penalty {
                  color: #ff9800;
                  font-size: 0.8em;
                  margin-bottom: 5px;
                  font-style: italic;
                }
                
                .player-icon.not-wanted-icon {
                  border-color: #f44336;
                  filter: grayscale(0.3) brightness(0.8);
                }
              }
              
             
              
              .player-icons {
                display: flex;
                gap: 5px;
                
                .player-icon {
                  width: 30px;
                  height: 30px;
                  border-radius: 50%;
                  border: 2px solid #4CAF50;
                  overflow: hidden;
                  
                  .cimage {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                  }
                }
              }
            }
          }
          
          .encounter-overlay {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(33, 150, 243, 0.9);
            display: flex;
            align-items: center;
            justify-content: center;
            opacity: 0;
            transition: opacity 0.3s ease;
            border-radius: 16px;
            
            .select-text {
              color: white;
              font-size: 1.5em;
              font-weight: bold;
              text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
            }
          }
        }
      }
    }
    
    .selection-help {
      text-align: center;
      padding: 20px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 8px;
      border: 1px solid #4a4a5a;
      margin-top: 24px;
      height: 60px;
      width: 850px;
      p {
        margin: 0;
        color: #cccccc;
        font-size: 1.5em;
      }
    }
  }
}
