@keyframes enemyglow {
    from {
        box-shadow: 0 0 5px red;
    }
    to {
        box-shadow: 0 0 20px red;
    }
}

@keyframes pulse {
    0% {
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.5),
                   inset 0 0 15px rgba(255, 255, 255, 0.3);
    }
    50% {
        box-shadow: 0 0 20px rgba(255, 255, 255, 0.7),
                   inset 0 0 25px rgba(255, 255, 255, 0.5);
    }
    100% {
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.5),
                   inset 0 0 15px rgba(255, 255, 255, 0.3);
    }
}


.combat-peril-row {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.game-encounter-combat {
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    width: 100%;
    position: absolute;
    height: 100%;
  
   
    .combat-encounter-instructions{
        font-size: .4em;
        
        display: flex;
    }
    .combat-encounter-upper-container {
        display: flex;
       
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        max-height: 100px;
        .combat-encounter-header {
            width: 80%;
            display: flex;
           
            flex-direction: column;
            align-items: center;
          
            .combat-encounter-header-title {
                white-space: nowrap;
                font-size: .85em;
            }
        }
        .combat-encounter-description {
            font-size: .4em;
          
            display: flex;
        }
       
    }

    .combat-encounter-body-container {
       
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .combat-encounter-enemies-row {
            margin-top: 4px;
            display: grid;
            grid-template-columns: 1fr auto 1fr;
           
            width: 100%;
            max-height: 200px;
            .combat-encounter-enemies {
                display: flex; 
                flex-direction: column;
                justify-content: top;
                .combat-encounter-enemies-number {
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    justify-content: center;
                }
                .combat-enemies-ui{
                    display: flex;
                    flex-direction: column;
                    gap: 4px;
                    justify-content: center;
                    font-size: .5em;
                    background-color: #ffffff44;
                    border: 4px solid #280e09a8;
                    border-radius: 5px;
                    margin-left: 10px;
                    margin-right: 10px;
                    cursor: pointer;
                    &:hover {
                        transform: scale(1.1);
                        .number-die {
                            transform: scale(1.2);
                        }
                        transition: all 0.3s;
                        
                        background-color: #ffffff7d;
                    }
                    .number-die {
                        width: 50px;
                        height: 50px;
                        font-size: 1em;
                        background: #000000;
                        color: #ffffff;
                        margin-left: 20px;
                        margin-right: 20px;
                        border: none;
                        font-size: 1em;
                        margin-top: 4px;
                        .number-die-text {
                            margin-left: 0px;
                            margin-bottom: 0px;
                        }
                    }
                }
            } 
              
            .combat-message-container {
                
                margin-right: 10px;
                margin-left: 10px; 
                margin-top: 10px;
               
                padding: 10px;
                background: #967441bd;
                border-radius: 12px;
                display: flex;
                flex-direction: column;
                align-items: center;
                font-size: .5em;
                justify-content: center;
                border: 5px solid rgb(65, 44, 19);
                transition: all 0.3s;
                max-width: 300px;
                font-size: .5em;
                cursor: pointer;
              
                   .combat-message-legend{
                        display: flex;
                        flex-direction: column;
                      
                        align-items: left;
                        font-size: .75em;
                        color: #ffffff;
                        text-align: left;
                    }
            }
            .combat-encounter-advance-button {
                    background: #00000000;
                    border: none;
                    font-size: .5em;;
                   
                }

            .combat-encounter-enemies-header{
                margin-top: 2px;
                display: flex;
                height: 44px;
                flex-direction: row;
                line-height: 1;
                position: relative;
            
                letter-spacing: .081em;
                align-items: center;
                justify-content: center;
                color: #550000;
                font-size: .91em;
                border: 4px solid #550000;
                border-radius: 10px;;
                background-color: #ffffff44;
                width: auto;
            }

            .combat-encounter-enemies-attributes{
                .combat-encounter-enemies-attributes-display{
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    gap: 10px;
                    font-size: .5em;
                    .combat-enemy-attribute{
                        width: 200px;
                    }
                }

              
            }


            .combat-encounter-enemies-list{
                display: flex;
                flex-direction: row;
                gap: 10px;
                justify-content: center;
                .enemy-icon {
                    width: 100px;
                    height: 100px;
                    
                    .cimage{
                        width: 100%;
                        border-radius: 100%;
                    }
                    &.enemy-type-cultists {   
                        .cimage{
                            box-shadow: 0 0 10px red;
                            animation: enemyglow 1s infinite alternate;
                        }
                    }

                  
                }
            }
        }



        .combat-encounter-heroes {
           
                display: flex;
                flex-direction: row;
                bottom: 0;
                position: absolute;
                align-items: flex-end;
                justify-content: center;
                width: 100%;
                box-sizing: border-box;
            
                flex-wrap: wrap;
                left: 0;
                gap: 10px;
                margin: 12px;
                flex-wrap: nowrap;
                .combat-encounter-hero-row {
                   
                    display: flex;
                    flex-direction: column;
                    
                    justify-content: space-between;
                    
                    align-items: center;
                  

                    .combat-hero-attack-results-container {
                        display: flex;
                        width: 100%;
                        flex-direction: column;
                        font-size: .5em;
                       
                        margin-bottom: 4px;
                      
                        .combat-hero-button {
                            cursor: pointer;
                             border: 4px solid rgba(33, 54, 33, 0.667);
                             border-radius: 10px;
                            background-color: #ab9b8deb;
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            font-size: .75em;
                            align-items: center;
                            width: 100%;
                            max-width: 100%;
                            height: 34px;
                            margin-top: 4px;
                            transition: all 0.3s;
                            &:hover {
                                transform: scale(1.08);
                            }
                            &.acted {
                                cursor: not-allowed;
                                pointer-events: none;
                                border: none;
                                margin-bottom: 3px;
                                &:hover{
                                    transform: scale(1);
                                }
                            }
                            &.hide {
                                display: none;
                            }


                            .number-die {
                                margin: 4px;
                                height: 30px;
                                width: 30px;
                                font-size: .7em;
                                background-color: black;
                                border: none;
                                .number-die-text {
                                    margin-left: 0px;
                                    margin-bottom: 0px;
                                }
                            }
                        }
                    }

                    .combat-encounter-hero-container {
                        display: flex;
                        flex-direction: row;
                        border: 4px solid rgba(33, 54, 33, 0.667);
                        background-color: #00000044;
                        height: 92px;
                        border-radius: 10px;
                    }

                    .combat-encounter-hero {
                        background-color: #ffffff;
                        display: flex; 
                        flex-direction: column;
                        font-size: .5em;
                       
                        height: 100%;
                        width: 140px;
                        border-radius: 10px;
                        box-sizing: border-box;
                        align-items: center;
                        border: 4px solid #00000099;
                        position: relative;
                       
                        .combat-character-icon {
                            width: 50px;
                            height: 50px;
                            border-radius: 5px;;
                           
                            margin-bottom: 4px;
                            bottom: 4px;
                            box-sizing: border-box;
                            
                            .cimage {
                                position: relative;
                                width: 100%;
                                height: 100%;
                            }   
                        }

                        .combat-encounter-hero-injury {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            background-color: rgba(71, 19, 18, 0.6);
                            z-index: 2;
                            &.dead {    
                                background-color: black;
                            }
                        }


                    }
                    .combat-encounter-hero-attributes{
                        margin-left: 4px;
                        height: 100%;
                        width: stretch;
                        min-width:150px;
                        display: flex;
                        align-items: center;;
                        .attribute-display-container{
                            left: 0;
                           
                            align-items: flex-start;
                            display: flex;
                            flex-direction: column;



                            .attribute-display {
                                font-size: .4em;
                                display: flex;
                                flex-direction: row;
                                .attribute-title {
                                   display: none;
                                }
                                .attribute-container {
                                    position: relative;
                                    flex-direction: row;
                                    display: flex;
                                    font-size: .95em;
                                    letter-spacing: -6px;
                                    flex-wrap:wrap;
                                  .attributes-lost {
                                    opacity: 0.2;   
                                  }
                                
                                }
                            }
                        }  
                    }
                }
            
        }
    }



}

.combat-advance-box {
    display: flex;
    align-items: top;
    justify-content: center;
    .combat-encounter-advance-button-container{
        
        margin: 20px;
        width: 140px;
        height: 140px; 
        background: #967441bd;
        border-radius: 100%;
        display: flex;
        flex-direction: column;
        align-items: top;;
        justify-content: center;
        border: 5px solid rgb(65, 44, 19);
        transition: all 0.3s;
        cursor: pointer;
        &:hover{
            transform: scale(1.1);
        }
        .combat-encounter-advance-button {
            background: #00000000;
            border: none;
            font-size: .5em;;
            .combat-button-exhaustion-label{
                font-size: .6em;
                
               
            }
        }

    }
    .disabled {
        opacity: 0.5;
        pointer-events: none;
    }
}

.combat-encounter-rewards {
    margin-top: 20px;
    font-size: .5em;
    color: white;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
}


.combat-bar-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: large;
    
    margin-top: 20px;
    margin-bottom: 20px;
    position: relative;
    width: 60%;
    



    .combat-bar {
       
        width: 100%;
        position: relative;
        .combat-bar-range-indicator {
        
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            
            .range-card {
              position: absolute;
              height: 100%;
              background: rgba(117, 137, 200, 0.4);
              border-radius: 4px;
              border: 5px solid rgba(117, 137, 200, 0.8);
              transition: all 0.3s ease;
             
              padding-bottom: 2px;
              padding-top: 0px;
              line-height: 2.9em; 
            }
          }

        .segment-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 22px;
            border-radius: 10px;
            overflow: hidden;
            margin-top:30px;
            
        }
        
        .combat-bar-segment {
            flex: 1;
            height: 100%;
            border-right: 1px solid #000; // Segment lines
            transition: background-color 0.3s;
            color: #25282a;
            padding-bottom: 5px;
            width:100%;
            transition: all 0.1s ease;
            filter: brightness(.5);
            &.in-range {
                border: 2px solid #fff;
               
                transition: all 0.3s ease-in-out;
                filter: brightness(1.1);
                height: 100%;
                border-right: 3px solid #fff; // Segment lines
                &.highlighted {
               
                    box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
                    z-index: 1;
                  }
                  
                &.alternativeColor {
                    color: #ffffff;
                }
                &.positive-result {
                    filter: brightness(1.5);
                }
                &.negative-result {
                    filter: brightness(0.5);
                }
                &.animating {
                    filter: brightness(0.7);
                    
                    &.highlighted {
                      filter: brightness(1.5);
                    }
                }
            }
          
          

           
            &:last-child {
                border-right: none; // Remove border from the last segment
            }
            &.theme-{
                background-color: var(--town-display-pencil-light);
                &.filled, &.dark {
                    background-color: var(--town-display-pencil);
                }
            }
        }

        .combat-bar-label {
            font-size: 1.24em;
            font-weight: bold;
            text-transform: uppercase;
            top: -14px;
            position: relative;
            &.stress{
                color: rgb(61, 58, 58);
            }
            &.peril {
                color: rgb(107, 42, 42);
            }
            &.engagement{
                color: #43683f;
            }
        }
    }
}

.combat-bar-modifiers{
   
    width: 400px;
    height: 190px;
    padding: 4px;
   
        position: relative;
        
        
        &.show-arrow {
            &.left::before {
            content: '';
            position: absolute;
            top: 45%;
            left: 35%;
            transform: translate(-25%, -25%);
            width: 0;
            height: 0;
            border-top: 60px solid transparent;
            border-bottom: 60px solid transparent;
            border-right: 100px solid rgba(255, 255, 255, 0.3);
            pointer-events: none;
            }
            
            &.right::before {
                content: '';
                position: absolute;
                top: 45%;
                left: 50%;
                transform: translate(-25%, -25%);
                width: 0;
                height: 0;
                border-top: 60px solid transparent;
                border-bottom: 60px solid transparent;
                border-left: 100px solid rgba(255, 255, 255, 0.3);
                pointer-events: none;
            }
        }

    // COMBATANTS
    .combat-combatants {
        display: flex;
        flex-direction: column;
        height: 100%;
        border-radius: 10px;
        &.heroes {
            background-color: #23ab4355;
            border: 4px solid #102f17aa;
            margin: 20px;
            height: 150px;
        }

        &.enemies {
            background-color: #ab352355;
            border: 4px solid #2f1010aa;
            margin: 20px;
            height: 150px;
        }



        .combat-combatants-label{
            width: 100%;
            margin-top: 2px;
            font-size: .5em;
            line-height: 1em;
            filter: brightness(.5);
        }
        .combat-combatants-row {
            display: flex;
            flex-direction: row;
            height: 100%;
            justify-content: center;
            align-items: center;
            gap: 20px;
            .combat-combatants-tags{
               
                max-height: 100%;
                width: 100%;
                height: 100%;
                .combatant-tags{
                    font-size: .45em;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    gap: 5px;
                    align-items: center;
                    justify-content: center;
                    
                    .none-available {
                        margin-top: 20px;
                        opacity: 0.5;
                    }
                    .text-pill{
                        transition: all 0.3s;
                        padding-bottom: 3px;
                        border: 3px solid rgba(58, 42, 16, 0.847);
                        cursor: pointer;
                        &:hover {
                            transform: scale(1.1);
                        }
                    }
                }
            }
           
            
        }
        &.enemies {
            .combat-modifier-row {
                flex-direction: row-reverse;
            }
        }
    }

// MODIFIERS

    .combat-modifiers {
        display: flex;
        flex-direction: column;
        height: 100%;
        border-radius: 10px;
        height: 100%;
        margin-left: 10px;
        margin-right: 10px;
       
            &.heroes {
                background-color: #23ab4355;
                border: 4px solid #102f17aa;
            }
    
            &.enemies {
                background-color: #ab352355;
                border: 4px solid #2f1010aa;
            }
    
       
        .combat-modifiers-label{
            width: 100%;;
            font-size: .65em;
        }
        .combat-modifier-row {
            display: flex;
            flex-direction: row;
            height: 100%;
            width: 100%;
            align-items: center;
            justify-content: space-around;
            gap: 0px;
            .combat-modifiers-tags{
                transition: all 0.3s;
                max-height: 100%;
                cursor: pointer;
                margin-left: 5px;
                margin-right: 5px;;
                width: 100%;

                .location-tags{
                    margin: 0;
                    font-size: .38em;
                    display: flex;
                    flex-direction: row;
                    gap: 1px;
                    align-items: flex-start;
                    flex-wrap: wrap;
                    &.enemies {
                        align-items: flex-end;
                    }
                    .text-pill{
                        transition: all 0.3s;
                        &:hover {
                            transform: scale(1.1);
                        }
                        padding-bottom: 3px;
                        border: 2px solid black;
                        margin: 2px;
                      
                    }
                }
            }
         
            .combat-modifiers-die {
              
                margin: 10px;
                .number-die {
                    background-color: #ffffff44;
                    width: 60px;
                    height: 60px;
                    font-size: .75em;
                    &:hover{ 
                        transform: scale(1.1);
                    }
                    .number-die-text {
                        margin-left: -3px;
                       
                       }
                }
            }
        }
        &.enemies {
            .combat-modifier-row {
                flex-direction: row-reverse;
            }
        }
    }



}