// Quest Progress Modal Styles - Based on EncounterTagModal
@import "../../styles/themes.scss";

.modal-container.questprogress {
	width: 90%;
	max-width: 1400px;
	height: 90vh;
	max-height: 900px;
	display: flex;
	flex-direction: column;
	background: var(--theme-background);
	border-radius: 12px;
	overflow: hidden;
	box-shadow: 0 20px 40px var(--theme-shadow);

	// Header Strip
	.questprogress-header {
		position: relative;
		height: 150px;
		border-radius: 12px 12px 0 0;
		overflow: hidden;

		.header-background {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;

			.cimage {
				width: 100%;
				height: 100%;
				object-fit: cover;
				opacity: 0.7;
			}
		}

		.header-overlay {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(
				135deg,
				rgba(0, 0, 0, 0.8),
				rgba(0, 0, 0, 0.5)
			);
			z-index: 2;
		}

		.header-content {
			position: relative;
			z-index: 3;
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 100%;
			padding: 20px 30px;

			.header-left {
				.quest-info {
					.quest-title {
						color: var(--theme-text-primary);
						font-size: 28px;
						font-weight: bold;
						margin: 0 0 8px 0;
						text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
					}

					.quest-description {
						color: var(--theme-text-secondary);
						font-size: 16px;
						text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
					}
				}
			}

			.header-right {
				.close-button {
					background: rgba(255, 255, 255, 0.1);
					color: #ffffff;
					border: none;
					width: 36px;
					height: 36px;
					border-radius: 50%;
					font-size: 20px;
					cursor: pointer;
					transition: all 0.2s ease;
					backdrop-filter: blur(4px);

					&:hover {
						background: rgba(255, 255, 255, 0.2);
						transform: scale(1.1);
					}
				}
			}
		}
	}

	// Quest Progress Section
	.quest-progress-section {
		padding: 4px 20px;
		background: rgba(255, 255, 255, 0.05);
		border-bottom: 1px solid rgba(255, 255, 255, 0.1);

		.progress-row {
			display: flex;
			align-items: center;
			gap: 20px;
			margin-bottom: 2px;

			&:last-child {
				margin-bottom: 0;
			}

			.progress-label {
				flex-shrink: 0;
				min-width: 10px;

				h4 {
					color: #ffffff;
					margin: 0;
					font-size: 16px;
					font-weight: 600;
					text-transform: uppercase;
				}
			}

			.progress-tags {
				display: flex;
				gap: 1px;
				padding: 0px 0;
				flex: 1;
				.progress-tag-item {
					cursor: pointer;
					transition: all 0.2s ease;
					border-radius: 8px;
					padding: 4px;
					position: relative;
					flex-shrink: 0;

					&:hover {
						background: rgba(255, 255, 255, 0.1);
					}

					// Current/Selected tag - highlighted with blue styling
					&.selected {
						border: 2px solid rgba(33, 150, 243, 0.5);
						padding-bottom: 10px;
						padding-top: 2px;
						background: rgba(33, 150, 243, 0.1);
					}

					// Starting and completed tags have no extra border styling - they use base appearance

					// Special case: starting tag that's also completed and selected
					&.starting-tag.completed-tag.selected {
						border: 2px solid rgba(33, 150, 243, 0.5);
						background: rgba(33, 150, 243, 0.1);
					}
				}
			}
		}
	}

	// Side-by-Side Body
	.questprogress-body {
		flex: 1;
		display: flex;
		gap: 20px;
		padding: 20px;
		min-height: 0;

		// Left Side - Conversation
		.conversation-section {
			flex: 2;
			display: flex;
			flex-direction: column;
			background: rgba(255, 255, 255, 0.05);
			border-radius: 8px;
			border: 1px solid rgba(255, 255, 255, 0.1);
			backdrop-filter: blur(4px);

			.conversation-content {
				flex: 1;
				padding: 20px;
				overflow-y: auto;

				.no-conversation {
					color: rgba(255, 255, 255, 0.6);
					font-style: italic;
					text-align: center;
					padding: 40px 20px;
				}

				// Conversation display styles (reusing from EncounterTagModal)
				.conversation-display {
					.chat-message-container {
						margin-bottom: 16px;
						opacity: 0;
						transform: translateY(10px);

						&.complete {
							animation: messageSlideIn 0.3s ease forwards;
						}

						&.current {
							animation: messageSlideIn 0.3s ease forwards;
						}

						.chat-message-display {
							display: flex;
							gap: 12px;

							.chat-message-speaker-container {
								min-width: 60px;

								.chat-speaker-name {
									font-size: 12px;
									font-weight: 600;
									text-transform: uppercase;
									padding: 4px 8px;
									border-radius: 12px;
									background: rgba(255, 255, 255, 0.1);
									color: rgba(255, 255, 255, 0.8);
								}
							}

							.chat-messager-content {
								flex: 1;

								.chat-message-text {
									color: #ffffff;
									line-height: 1.5;

									.chat-word {
										opacity: 0;
										transition: opacity 0.1s ease;

										&.visible {
											opacity: 1;
										}
									}
								}
							}
						}

						&.dm .chat-message-speaker-container .chat-speaker-name {
							background: rgba(76, 175, 80, 0.3);
							color: #a5d6a7;
						}

						&.player .chat-message-speaker-container .chat-speaker-name {
							background: rgba(33, 150, 243, 0.3);
							color: #90caf9;
						}

						&.character .chat-message-speaker-container .chat-speaker-name {
							background: rgba(255, 152, 0, 0.3);
							color: #ffcc02;
						}
					}
				}
			}
		}

		// Right Side - Quest Details
		.quest-details-section {
			flex: 1;
			display: flex;
			flex-direction: column;
			background: rgba(255, 255, 255, 0.05);
			border-radius: 8px;
			border: 1px solid rgba(255, 255, 255, 0.1);
			backdrop-filter: blur(4px);

			.section-header {
				display: flex;
        align-items: center;
        justify-content: center;
        padding: 16px 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        background: #00000000;
        font-size: 29px;
			}

			.quest-details-content {
				flex: 1;
				padding: 20px;
				overflow-y: auto;

				.quest-info-block {
					margin-bottom: 24px;

					&.highlighted {
						background: rgba(255, 215, 0, 0.1);
						border: 1px solid rgba(255, 215, 0, 0.3);
						border-radius: 8px;
						padding: 16px;
						animation: questCompleteGlow 2s ease-in-out infinite;
					}

					h4 {
						color: #ffffff;
						font-size: 16px;
						font-weight: 600;
						margin: 0 0 12px 0;
						text-transform: uppercase;
					}

					.cost-item,
					.reward-item {
						display: flex;
						margin-bottom: 12px;
						padding: 8px 12px;
						background: rgba(255, 255, 255, 0.05);
						border-radius: 6px;
						border: 1px solid rgba(255, 255, 255, 0.1);
						min-height: 28px;
						&.completed {
							background: rgba(76, 175, 80, 0.1);
							border-color: rgba(76, 175, 80, 0.3);
							box-shadow: 0 0 8px rgba(76, 175, 80, 0.2);
						}
						.encounter-flavor-tag {
							position: relative;
							padding: 5px;
							padding-left: 10px;
							padding-right: 10px;
						}

						.cost-label,
						.reward-label {
							color: #ffffff;
							font-weight: 600;
							font-size: 14px;
							margin-bottom: 4px;
						}

						.cost-description,
						.reward-description {
							color: rgba(255, 255, 255, 0.7);
							font-size: 12px;
						}
					}

					.next-topic {
						display: flex;
						justify-content: center;
						margin-top: 8px;
					}
				}

				.quest-complete-rewards {
					text-align: center;

					.quest-info-block.highlighted h4 {
						color: #ffd700;
						font-size: 20px;
					}
				}
			}
		}
	}

	.section-header {
		padding: 16px 20px;
		border-bottom: 1px solid rgba(255, 255, 255, 0.1);

		h3 {
			color: #ffffff;
			margin: 0;
			font-size: 22px;
			font-weight: 600;
		}
	}

	// Footer
	.questprogress-footer {
		padding: 20px 30px;
		display: flex;
		justify-content: center;
		border-top: 1px solid rgba(255, 255, 255, 0.1);

		.questprogress-accept-button {
			background: var(--ui-color-primary, #ff6b6b);
			border: 2px solid rgba(255, 255, 255, 0.3);
			border-radius: 8px;
			color: #ffffff;
			padding: 12px 40px;
			font-size: 16px;
			font-weight: bold;
			cursor: pointer;
			text-transform: uppercase;
			transition: all 0.2s ease;

			&:hover:not(.disabled) {
				background: var(--ui-color-primary-hover, #ff4c4c);
				transform: scale(1.05);
				box-shadow: 0 4px 12px rgba(255, 107, 107, 0.3);
			}

			&.disabled {
				background: rgba(255, 255, 255, 0.1);
				color: rgba(255, 255, 255, 0.5);
				cursor: not-allowed;
				border-color: rgba(255, 255, 255, 0.1);
			}
		}
	}
}

// Animations
@keyframes messageSlideIn {
	to {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes questCompleteGlow {
	0%,
	100% {
		box-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
	}
	50% {
		box-shadow: 0 0 20px rgba(255, 215, 0, 0.6);
	}
}
