.llm-config-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.llm-config-modal {
  background: #2a2a2a;
  border: 2px solid #444;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  color: #fff;
  font-family: 'Courier New', monospace;
  .modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #444;
  padding-top: 10px;;
  background: #333;
}

}


.modal-header h2 {
  margin: 0;
  color: #4CAF50;
  font-size: 1.5rem;
}

.close-button {
  background: none;
  border: none;
  color: #ccc;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  left: 50%;
  top: -50px;;
  //align-items: center;
  //justify-content: center;
}

.close-button:hover {
  color: #fff;
  background: #555;
  border-radius: 3px;
}

.modal-content {
  padding: 1.5rem;
}

.config-section {
  margin-bottom: 2rem;
}

.config-section h3 {
  color: #4CAF50;
  margin-bottom: 0.5rem;
}

.description {
  color: #ccc;
  margin-bottom: 1.5rem;
  line-height: 1.4;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #fff;
  font-weight: bold;
}

.form-control {
  width: 100%;
  padding: 0.5rem;
  background: #1a1a1a;
  border: 1px solid #555;
  border-radius: 4px;
  color: #fff;
  font-family: 'Courier New', monospace;
  box-sizing: border-box;
}

.form-control:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 0 2px rgba(76, 175, 80, 0.2);
}

.form-text {
  display: block;
  margin-top: 0.25rem;
  color: #999;
  font-size: 0.875rem;
}

.form-text a {
  color: #4CAF50;
  text-decoration: none;
}

.form-text a:hover {
  text-decoration: underline;
}

.test-section {
  margin-top: 1.5rem;
  padding: 1rem;
  background: #1a1a1a;
  border-radius: 4px;
  border: 1px solid #333;
}

.test-button {
  background: #4CAF50;
  border: none;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Courier New', monospace;
  transition: background-color 0.2s;
}

.test-button:hover:not(:disabled) {
  background: #45a049;
}

.test-button:disabled {
  background: #666;
  cursor: not-allowed;
}

.test-button.testing {
  background: #FF9800;
}

.test-button.success {
  background: #4CAF50;
}

.test-button.error {
  background: #f44336;
}

.test-result {
  margin-top: 1rem;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 0.875rem;
  line-height: 1.4;
}

.test-result.success {
  background: rgba(76, 175, 80, 0.1);
  border: 1px solid #4CAF50;
  color: #4CAF50;
}

.test-result.error {
  background: rgba(244, 67, 54, 0.1);
  border: 1px solid #f44336;
  color: #f44336;
}

.test-result.testing {
  background: rgba(255, 152, 0, 0.1);
  border: 1px solid #FF9800;
  color: #FF9800;
}

.features-section h3 {
  color: #4CAF50;
  margin-bottom: 1rem;
}

.features-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.features-section li {
  padding: 0.5rem 0;
  color: #ccc;
  line-height: 1.4;
}

.features-section li strong {
  color: #fff;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem;
  border-top: 1px solid #444;
  background: #333;
}

.btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Courier New', monospace;
  transition: background-color 0.2s;
}

.btn-secondary {
  background: #666;
  color: #fff;
}

.btn-secondary:hover {
  background: #777;
}

.btn-primary {
  background: #4CAF50;
  color: #fff;
}

.btn-primary:hover:not(:disabled) {
  background: #45a049;
}

.btn-primary:disabled {
  background: #666;
  cursor: not-allowed;
}
