// DM Abilities Screen Styles
@import '../../styles/themes.scss';

.dm-abilities-screen {
  padding: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 0px;
  overflow-y: auto;
}

.abilities-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  //background: var(--theme-background-light);
  border-radius: 8px;
  //border: 1px solid var(--theme-border);

  .available-points {
    display: flex;
    align-items: center;
    gap: 10px;

    .points-label {
      font-size: 1.2rem;
      font-weight: bold;
      color: var(--theme-text-secondary);
    }

    .points-value {
      font-size: 1.8rem;
      font-weight: bold;
      color: var(--theme-text-accent);
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
      background: rgba(255, 215, 0, 0.1);
      padding: 5px 12px;
      border-radius: 20px;
      border: 2px solid var(--theme-text-accent);
    }
  }

  .abilities-info {
    flex: 1;
    text-align: center;
    color: var(--theme-text-secondary);
    font-size: 1rem;
    opacity: 0.9;
    font-style: italic;
  }
}

.abilities-meters {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 15px;
  
}

  .ability-button {
    min-width: 180px;
    padding: 12px 20px;
    border: 4px solid #666666;
    border-radius: 18px;
    color: var(--theme-text-primary);
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
  //  box-shadow: 0 2px 4px var(--theme-shadow);
    
    &:hover:not(:disabled) {
      transform: scale(1.08);
   //   box-shadow: 0 4px 8px var(--theme-shadow);
    }
    
    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
    
    &.can-upgrade {
      border: 4px solid #ffffff;
   //   box-shadow: 0 0 10px var(--theme-glow);
    }
    
    .ability-name {
      font-size: 1rem;
      font-weight: bold;
      pointer-events: none;
    }
    
    .ability-cost {
      font-size: 0.8rem;
      opacity: 0.9;
    }

    &.consumable {
      background-color: red;
      &.locked {
        background-color: darkred;
      }
    }


  }

.ability-meter {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px 0;
  


  
  
  .ability-track {
    flex: 1;
    height: 14px;
    background: var(--theme-primary-dark);
    border-radius: 20px;
    border: 2px solid var(--theme-border);
    position: relative;
    //overflow: hidden;
    
    .ability-progress {
      height: 100%;
      border-radius: 18px;
      transition: width 0.5s ease;
    // opacity: 0.7;
      position: relative;
      z-index: 2;
      &.filled {
        top: -14px;
      }
    }
    
    .ability-circles {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 21px;
     
      &.names {
        top: -30px;
        left: -8px;
      }
      padding-left: 20px;
        &.foreground {
          z-index: 9;
          .ability-circle {
            border: none;
          }
        }
      .ability-circle {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 2px solid var(--theme-border);
        transition: all 0.3s ease;
      
        &.filled {
          border-color: var(--theme-text-secondary);
      //    box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
          z-index: 10;
          border: none;
          transform: translate(-2px, 0px);
        }

      }

      .ability-circle-name {
       bottom: 0px;;
       margin-top: 20px;
        justify-content: center;
        align-items: center;
        width: 22px;
        text-wrap: nowrap;
        text-align: center;
        height: 50px;
      }

      .ability-circle-name-text {
        align-items: center;
        display: flex;
        justify-content: center;  
      }
    }
  }
  
  .ability-info {
    min-width: 150px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 4px;
    
    .ability-level {
      font-size: 1.2rem;
      font-weight: bold;
      color: var(--theme-text-accent);
    }
    
    .ability-unlock {
      font-size: 0.9rem;
      color: var(--theme-text-secondary);
      font-style: italic;
      text-align: right;
    }
  }
}

.consumables-section {
  background: var(--theme-background-medium);
  border-radius: 12px;
  border: 1px solid var(--theme-border);
  padding: 15px;
  margin-top: 12px;
  border: 6px solid white;
}

.consumable-row {
  display: flex;
  flex-direction: column;
  gap: 20px;
 
  .consumable-header {
    text-align: center;
    
    .section-title {
      font-size: 2rem;
      font-weight: bold;
      color: var(--theme-text-primary);
      display: block;
      margin-bottom: -10px;
      
    }
    
    .section-subtitle {
      font-size: 1rem;
      color: var(--theme-text-secondary);
      opacity: 0.8;
    }
  }

  .consumable-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    justify-content: center;
  }

  .consumable-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    padding: 2px;
    background: var(--theme-background-light);
    border-radius: 12px;
    border: 2px solid var(--theme-border);
    transition: all 0.3s ease;


    .consumable-category-label {
      font-size: 1.1rem;
      font-weight: bold;
      color: var(--theme-text-primary);
      text-align: center;
      text-transform: uppercase;                                                    
      letter-spacing: 1px;
      border-bottom: 2px solid var(--theme-text-accent);
      padding-bottom: 5px;
      width: 100%;
    }

    .consumable-current-status {
      min-height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      .current-info {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 4px;
      }
      .current-level {
        font-size: 1.1rem;
        font-weight: bold;
        color: var(--theme-text-accent);
        background: rgba(0, 0, 0, 0.2);
        padding: 8px 12px;
        border-radius: 8px;
      }
      
      .locked-status {
        font-size: 1rem;
        color: var(--theme-text-secondary);
        opacity: 0.7;
        font-style: italic;
      }
    }

    .consumable-upgrade-button {
      width: 100%;
      padding: 12px 16px;
      border: 6px solid var(--ui-color-primary-border);
      border-radius: 8px;
      color: var(--theme-text-primary);
      font-weight: bold;
      font-size: 1.2em;
      cursor: pointer;
      transition: all 0.3s ease;
      text-align: center;
      margin-top: 8px;
      background: var(--ui-color-primary);
      &:hover:not(:disabled) {
        transform: scale(1.05);
        background-color: var(--ui-color-primary-hover);
      }
      
      &.can-upgrade {
        border: 6px solid white;
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
      }
      
      &.disabled {
        opacity: 0.6;
        cursor: not-allowed;
        background-color: #666666;
      }
    }

    .locked-indicator {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2px;
      margin-top: -14px;
      
      .lock-icon {
        font-size: 2rem;
        opacity: 0.5;
      }
      
      .unlock-requirement {
        font-size: 0.85rem;
        color: var(--theme-text-secondary);
        text-align: center;
        opacity: 0.8;
      }
    }
  }
}



.game-mastery-section {
    display: flex;
    align-items: center;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 14px;
    .game-mastery-row {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      justify-content: space-between;
      margin-right: 120px;
     
      
     
      cursor: pointer;
      transition: all 0.3s ease;

      .game-mastery-name {
        font-size: 1rem;
        font-weight: bold;
        color: var(--theme-text-secondary);
      }

      .game-mastery-level {
        font-size: 0.9rem;
        color: var(--theme-text-accent);
      }
    }
  }


.game-mastery-label{
  margin-left: 30px;

  color: white;
  font-weight: bold;
  font-size: 18px;
  
}

.mastery-container{
  margin-bottom: 24px;
  .mastery-label-top {
    margin-bottom: 12px;
    display: flex;
    flex-direction: column;
    .mastery-name{
      color: white;
      font-weight: bold;
      font-size: 24px;
    }
  }
}



// Responsive design
@media (max-width: 768px) {
  .dm-abilities-screen {
    padding: 15px;
    gap: 15px;
  }
  
  .abilities-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
    
    .abilities-info {
      font-size: 0.9rem;
    }
  }
  
  .ability-meter {
    flex-direction: column;
    gap: 10px;
    
    .ability-button {
      min-width: 120px;
      padding: 10px 15px;
    }
    
    .ability-track {
      width: 100%;
    }
    
    .ability-info {
      min-width: auto;
      align-items: center;
      text-align: center;
    }
  }
  
  .consumable-row {
    margin-top: 20px;
    display: none;
    .consumable-grid {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    
    .consumable-item {
      padding: 12px;
      
      .consumable-category-label {
        font-size: 1rem;
      }
      
      .consumable-upgrade-button {
        font-size: 0.85rem;
        padding: 10px 12px;
        margin-top: 80px;
       
      }
    }
  }
}
